<template>
  <div id="">
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
    <van-list
      v-model="loading"
      :finished="finished"
      :finished-text="$t('Tips.no_more')"
      :loading-text="$t('Tips.loading')"
      @load="onLoad"
    >
      <div class="" v-for="(val,key) in list" :key="val.id">
        <div class="msg-text">{{val.addtime}}</div>
        <div class="msg-box">
          <div class="msg-box-a">{{val.title}}</div>
          <div class="msg-box-b">{{val.centent}}</div>
          <div class="msg-box-c" v-if="val.is_see" @click="href_log(val.id,val.type,val.url)">
            <div class="msg-box-c-l float_l">{{ $t('btn.View_now') }}</div>
            <div class="msg-box-c-r float_r"><img src="../assets/img/9.png" alt=""></div>
          </div>
          <div class="msg-box-c" v-else></div>
        </div>
      </div>
      <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
    </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        list:[],
        info:[],
        privilege:[],
        loading:false,
        finished:false,
        refreshing: false,
        page:1,
      }
    },
    created: function() {
      //this.convert();
      //this.get_list();
      document.title = this.$t('title.system_message')
    },
    methods: {
      get_list(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/vue/system_message_api/get_system_message",{ params:{ uid:uid,token:token }}).then(res =>{
           console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             var list = res.data.data
             _this.list = list
             _this.page = 2;
             /* for(var i =0;i<list.length;i++){

             } */
             /* var _this = this;
             _this.list = res.data.data.noble_list
             _this.info = res.data.data.noble_info
             _this.privilege = res.data.data.privilege
             _this.id =  _this.info.id
             _this.currency_name =  res.data.data.currency_name
             _this.coin = res.data.data.coin */
             //console.log(_this.list)
             //this.bank_name = list[0]['name']
           }

        })
      },
      onLoad(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var page = this.page;

        axios.get(this.baseUrlApi+"/vue/system_message_api/get_system_message",{ params:{ uid:uid,token:token,page:page,type:2 }}).then(res =>{
           //console.log(res.data)
           //this.loading = false;
           if(res.data.code==1){
             if(res.data.data==''){
               this.loading = false
               this.finished = true
               this.refreshing = false
               return
             }
             var _this = this;
             var list = res.data.data
             //_this.list = list
             _this.page++;
             for(var i =0;i<list.length;i++){
              _this.list.push(list[i])
             }
           }
           this.finished = true
           this.refreshing = false
           this.loading = false;
        })
      },
      delAtt(keys,id){
        var list = this.list;
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var page = this.page;

        axios.get(this.baseUrlApi+"/vue/user_vue_api/click_attention_vue",{ params:{ uid:uid,token:token,id:id }}).then(res =>{
           //console.log(res.data)
           //this.loading = false;
           if(res.data.code==1){
             Toast.success(res.data.msg);
             //list.splice(keys,1);
             list[keys]['focus'] = 1;
           }else{
             Toast.fail(res.data.msg);
           }
           //console.log(page)

        })
      },
      onRefresh(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var page = 1;
        this.finished = false;
        // 重新加载数据
        // 将 loading 设置为 true，表示处于加载状态
        //this.loading = true;

        axios.get(this.baseUrlApi+"/vue/system_message_api/get_system_message",{ params:{ uid:uid,token:token,page:page,type:2 }}).then(res =>{
           //console.log(res.data)
           this.refreshing = false;
           if(res.data.code==1){
             if(res.data.data==''){
               this.refreshing = true
             }
             var _this = this;
             var list = res.data.data
             _this.list = list
           }else{
             this.refreshing = true
           }
           //console.log(page)

        })
      },
      href_log(id,type,url){
        if(type==1 && url != null && url != ""){
          window.location.href = url
        }else if (type==2 && url != null && url != ""){
          window.location.href = url
        }else{
          var uid = this.$route.query.uid;
          var token = this.$route.query.token;
          //window.location.href = data;
          this.$router.push({path:'/system_content',query:{uid:uid, token: token,id:id }});
        }

      },

    }
  }

</script>

<style>
  #app,body{
    background-color: #F5F7FA;
  }

</style>



