<template>
  <div id="add_bank" >
    <el-row>
      <van-col span="24" class="add_bank_div">
        <van-cell-group>
          <!-- 银行用户名称 -->
          <van-field v-model="bank_name" :label="$t('column.account_name')" :placeholder="$t('Tips.please_account_name')" input-align="right"/>
          <!-- 银行id -->
          <van-field v-model="bank_title" :label="$t('column.bank_id')" :placeholder="$t('Tips.please_bank_id')" readonly input-align="right" is-link @click="user_account = true" />
          <!-- 银行卡号 -->
          <van-field v-model="bank_number" :label="$t('column.bank_number')" :placeholder="$t('Tips.please_bank_name')" input-align="right"/>
        </van-cell-group>
        <van-action-sheet v-model="user_account" :actions="bank_list" @select="onSelect" :cancel-text="$t('btn.cancel')"/>
      </van-col>
      <van-col span="22" offset="1" class="add_bank_btn">
        <van-button color="linear-gradient(to right, #F86C2F, #CA27ED)" round size="large" @click="binding_bank_btn">
          {{ $t('btn.bind') }}
        </van-button>
      </van-col>
    </el-row>
  </div>
</template>
<script>
  //请求接口组件
  import '@/assets/css/style.css'
  import axios from 'axios'
  import { Toast } from 'vant';
  export default {
    data() {
      return {
        user_account: false,
        bank_list:[],
        bank_name:'',
        bank_number: '',
        bank_id: '',
        bank_title: '',
      }
    },
    created: function() {
      this.get_bank();
      document.title = this.$t('title.add_bank')
    },
    methods: {
      onSelect(item) {
        this.bank_id = item.id
        this.bank_title = item.name
        this.user_account = false
      },
      // 绑定账号
      binding_bank_btn() {
        const uid = this.$route.query.uid
        const token = this.$route.query.token
        if (!this.bank_number) {
          Toast(this.$t('Tips.please_bank_name'))
          return false
        }
        if (!this.bank_title) {
          Toast(this.$t('Tips.please_bank_id'))
          return false
        }
        axios.get(this.baseUrlApi+"/vue/wallet_api/binding_bank",{ params:{ uid:uid,token:token, bank_number: this.bank_number, bank_name:this.bank_name, bank_id:this.bank_id }}).then(res =>{
          console.log(res.data.data)
          Toast(res.data.msg)
          if (res.data.code === 1) {
            this.user_account = false
          }
        })
      },
      get_bank(){
        const uid = this.$route.query.uid
        const token = this.$route.query.token
        axios.get(this.baseUrlApi+"/vue/wallet_api/get_bank_list",{ params:{ uid:uid,token:token }}).then(res =>{
          if(res.data.code === 1){
            this.bank_list = res.data.data.cash_card_name
            this.bank_name = res.data.data.account.name
            this.bank_number = res.data.data.account.bank_card
            this.bank_id = res.data.data.account.bank_card_id
            this.bank_title = res.data.data.account.bank_card_name
          }
        })
      }
    }

  }
</script>



