<template>
  <div class="invite-box" :style="{backgroundImage: 'url(' + invite_bg + ')', backgroundSize:'100%', backgroundRepeat: 'no-repeat'}">
    <van-notice-bar class="invite-notice" :text="invite_info.banner_msg" :scrollable="true" />
    <div class="invite-title">
      <div class="invite-title-l" @click="href('invite_info')">{{ $t('btn.Invitation_description') }}</div>
      <div class="invite-title-r" @click="href('my_invite')">{{ $t('btn.My_invitation') }}</div>
    </div>
    <div class="invite-top">
      <div class="invite-top-z" v-if="isMongolia">
        <img src="../assets/img/zi3@2x.png" alt="">
      </div>
      <div class="invite-top-z" v-else-if="invite_title_bg">
        <img :src="invite_title_bg" alt="">
      </div>
      <div class="invite-top-z" v-else>
        <div class="invite_friend_size">{{ $t('invite.invite_friends') }}</div>
        <!--        <img src="../assets/img/zi1@2x.png" alt="">
                <img src="../assets/img/zi2@2x.png" alt="">-->
      </div>
      <div class="invite-top-time">
        <!-- <div>活动时间：</div>
        <div class="invite-top-time-t">{{invite_info.start_time}}-{{invite_info.end_time}}</div> -->
      </div>
      <div class="invite-top-button" @click="showPopup()">{{ $t('btn.To_invite') }}</div>
      <div class="invite-top-center"></div>
    </div>
    <van-popup v-model="show" @opened="creatQrCode()" :round="round">
      <div class="invite-show">
        <div class="invite-show-t">—— {{ $t('column.Face_to_face_invitation') }} ——</div>
        <div id="qrcode" ref="qrcode"></div>
        <div class="invite-show-t-b">{{ $t('column.Scan_code_quick_registration') }}</div>
        <div class="invite_share_img">
          <img src="../assets/img/invite_share.png" @click="invite_share_btn"/>
        </div>
        <!--        <div class="invite-show-t">—— {{ $t('column.Send_invitation_code_to') }} ——</div>-->
        <!--        <div class="invite-show-bottom">-->
        <!--          <div class="invite-show-bottom-l" v-if="invite_info.open_login_wx">-->
        <!--            <img src="../assets/img/weixin3@2x.png" alt="" @click="share(1)">-->
        <!--            <p>{{ $t('btn.wx') }}</p>-->
        <!--          </div>-->
        <!--          <div class="invite-show-bottom-c" v-if="invite_info.open_login_qq">-->
        <!--            <img src="../assets/img/qq@2x.png" alt="" @click="share(3)">-->
        <!--            <p>{{ $t('btn.qq') }}</p>-->
        <!--          </div>-->
        <!--          <div class="invite-show-bottom-r" v-if="invite_info.open_login_wx">-->
        <!--            <img src="../assets/img/pengyouquan3@2x.png" alt="" @click="share(2)">-->
        <!--            <p>{{ $t('btn.Wechat_Moments') }}</p>-->
        <!--          </div>-->
        <!--        </div>-->
      </div>
    </van-popup>
    <div class="invite-red">
      <div class="invite-red-top">
        <img src="../assets/img/diandian@2x.png" alt="">
        {{ $t('column.Invitation_reward') }}
        <img src="../assets/img/diandian@2x.png" alt="">
      </div>
      <div class="invite-red-bottom">
        <div class="invite-redbag-box">
          <div :class="redbag.reda==1? 'invite-redbag':'invite-redbag-b'"  @click="open_redbag(1)">
            <div  :class="redbag.reda ==2?'invite-redbag-coin':'invite-redbag-coin-b'">{{invite_info.coin_one}} <span></span></div>
            <div :class="redbag.reda==1 ? 'invite-redbag-text':'invite-redbag-text-b'">{{ $t('column.Cumulative_invitation_Award') }}</div>
          </div>
        </div>
        <div class="invite-redbag-box">
          <div :class="redbag.redb==1? 'invite-redbag':'invite-redbag-b'"  @click="open_redbag(2)">
            <div  :class="redbag.redb ==2?'invite-redbag-coin':'invite-redbag-coin-b'">
              <img src="../assets/img/diamond.png" class="invite-redbag-coin-img"/> {{invite_info.coin_tow}}
            </div>
            <div :class="redbag.redb==1 ? 'invite-redbag-text':'invite-redbag-text-b'">{{ $t('column.Invite_local_tyrant_Award') }}</div>
          </div>
        </div>
        <div class="invite-redbag-box">
          <div :class="redbag.redc==1? 'invite-redbag':'invite-redbag-b'"  @click="open_redbag(3)">
            <div :class="redbag.redc ==2?'invite-redbag-coin':'invite-redbag-coin-b'">
              <img src="../assets/img/diamond.png" class="invite-redbag-coin-img"/> {{invite_info.coin_three}}
            </div>
            <div :class="redbag.redc==1 ? 'invite-redbag-text':'invite-redbag-text-b'">{{ $t('column.Invitation_goddess_Award') }}</div>
          </div>
        </div>

      </div>
    </div>
    <div v-if="redbag.reda==2">
      <div class="invite-center">{{ $t('column.Invite_friends_to_get_rewards') }}</div>
      <div class="center">&nbsp;</div>
      <div class="invite-center invite-center2"> {{ $t('column.Friend_recharge_reward') }}<span>{{invite_info.invite_income_ratio}}%</span></div>
      <div class="invite-center invite-center2"> {{ $t('column.Friend_benefit_reward') }}<span>{{invite_info.invite_income_ratio_female}}%</span></div>
      <div class="invite-jl-img">
        <img src="../assets/img/shouji@2x.png" alt="">
      </div>
      <div class="invite-jl">
        <div class="invite-jl-l">
          &nbsp;
        </div>
        <div class="invite-jl-l">
          <p>{{ $t('column.receive_rewards_today') }}</p>
          <div class="invite-jl-l-coin"><img src="../assets/img/diamond.png" class="invite-redbag-coin-img"/> {{invite_info.income}}</div>
        </div>
        <div class="invite-jl-r">
          <div class="invite-jl-r-b" @click="getInviteRecord()">{{ $t('btn.immediately_receive') }}</div>
        </div>
      </div>
    </div>
    <div v-if="redbag.redb==2">
      <div class="invite-center">{{ $t('column.Invite_friends_red_envelopes',{ day: invite_info.registration_period_tycoon_rewards, sum: invite_info.accumulated_income_tycoon_awards }) }}</div>
      <div class="center">&nbsp;</div>
      <div class="red-bag-box">
        <div class="red-bag-box-a" v-for="(v,k) in red_list" :key="v.id">
          <div :class="v.num<=0?'red-bag-box-b-a':'red-bag-box-b'">
            <div v-if="v.num <= 0" class="red-bag-coin">{{v.coin}}</div>
            <div v-if="v.num > 0" class="red-bag-coin-a"><img src="../assets/img/diamond.png" class="invite-redbag-coin-img"/> {{v.coin}}</div>
            <div v-if="v.num > 0" class="red-bag-text">{{ $t('column.Invite_friends_to_unlock') }}</div>
            <div v-if="v.num <= 0 && v.is_receive==0" class="red-bag-button" @click="inviteRedbag(v.id,k)">{{ $t('btn.immediately_receive') }}</div>
            <div v-if="v.num <= 0 && v.is_receive==1" class="red-bag-button">{{ $t('btn.Received') }}</div>
          </div>
          <p>{{ $t('other.Not_good') }} {{v.num}} {{ $t('other.people') }}</p>
        </div>
      </div>
<!--      <div class="red-bag-bittom">（{{ $t('column.Reward_1_yuan') }} $1={{exchange}}{{ $t('column.Issued_diamond_form') }}）</div>-->
    </div>
    <div v-if="redbag.redc==2">
      <div class="invite-center">{{ $t('column.Invite_friends_Numbe_people_red_envelope', { day: invite_info.registration_period_goddess_rewards, sum: invite_info.accumulated_earnings_goddess_award }) }}</div>
      <div class="center">&nbsp;</div>
      <div class="red-bag-box">
        <div class="red-bag-box-a" v-for="(v,k) in red_list" :key="v.id">
          <div :class="v.num<=0?'red-bag-box-b-a':'red-bag-box-b'">
            <div v-if="v.num <= 0" class="red-bag-coin">{{v.coin}}</div>
            <div v-if="v.num > 0" class="red-bag-coin-a"><img src="../assets/img/diamond.png" class="invite-redbag-coin-img"/> {{v.coin}}</div>
            <div v-if="v.num > 0" class="red-bag-text">{{ $t('column.Invite_friends_to_unlock') }}</div>
            <div v-if="v.num <= 0 && v.is_receive==0" class="red-bag-button" @click="inviteRedbag(v.id,k)">{{ $t('btn.immediately_receive') }}</div>
            <div v-if="v.num <= 0 && v.is_receive==1" class="red-bag-button">{{ $t('btn.Received') }}</div>
          </div>
          <p>{{ $t('other.Not_good') }}{{v.num}}{{ $t('other.people') }}</p>
        </div>
      </div>
<!--      <div class="red-bag-bittom">（{{ $t('column.Reward_1_yuan') }} $1={{exchange}}{{ $t('column.Issued_diamond_form') }}）</div>-->
    </div>

    <div class="invite-red-top">
      <img src="../assets/img/diandian@2x.png" alt="">
      {{ $t('column.Revenue_list') }}
      <img src="../assets/img/diandian@2x.png" alt="">
    </div>
    <div class="invite-list-top">{{ $t('other.Current_total') }}<span>{{invite_info.user_num}}</span>{{ $t('other.people') }}，{{ $t('other.Total_acquisition') }}<span><img src="../assets/img/diamond.png" class="invite-redbag-coin-img"/> {{invite_info.user_coin}}</span></div>
    <div class="invite-list">
      <div class="invite-list-box" v-for="(val,k) in rank_list" :key="val.id">
        <div class="invite-list-box-a">{{k+1}}</div>
        <div class="invite-list-box-b">
          <img :src="val.avatar" alt="">
          <div class="invite-list-box-b-t">{{val.user_nickname}}</div>
        </div>
        <div class="invite-list-box-c"><img src="../assets/img/diamond.png" class="invite-redbag-coin-img"/> {{val.total_income/exchange}}</div>
      </div>
    </div>
  </div>
</template>
<script>
import '@/assets/css/style_a.css'
import axios from 'axios'
import { Toast } from 'vant'
import QRCode from 'qrcodejs2'
import i18n from '../assets/i18n'
//Vue.use(Loading);

export default {
  name: 'Index',
  data:function() {
    return {
      loading:false,
      user_account: false,
      withdrawal: '',
      list:[],
      type_list:[],
      //privilege:[],
      redbag:{
        'reda':2,
        'redb':1,
        'redc':1,
        'reda_coin':265,
        'redb_coin':300,
        'redc_coin':588,
      },
      invite_info:[],
      currency_name:'',
      friend_name:'',
      red_list:[],
      rank_list:[],
      show:false,
      round:true,
      server:'',
      avatar:'',
      exchange:10,
      isMongolia: false,
      invite_bg: require('../assets/img/yaoqinghaoyou_bac@2x.png'),
      invite_title_bg: null,
    }
  },
  created: function() {
    this.isMongolia = i18n.locale === 'Mongolia' ? true : false
    console.log(i18n.locale)
    console.log(this.isMongolia)
    this.invite_info.income = 0;
    this.get_list();
    //this.creatQrCode()
    //this.loading = true
    document.title = this.$t('title.invite')
  },
  methods: {
    invite_share_btn(){
      const text = this.server;
      window.location.href="live://live?action=inviteShare&url="+encodeURIComponent(text);
    },
    get_list(){
      const uid = this.$route.query.uid;
      const token = this.$route.query.token;
      //var order_id = this.$route.query.order_id;
      const loadings = Toast.loading({
        message: this.$t('Tips.loading'),
        forbidClick: true,
        duration: 0, // 持续展示 toast
      });
      axios.get(this.baseUrlApi+"/vue/user_vue_api/get_invite_info",{ params:{ uid:uid,token:token }}).then(res =>{
        console.log(res.data)
        if(res.data.code==1){
          const _this = this;

          _this.invite_info = res.data.data.invite_info
          _this.rank_list = res.data.data.rank_list
          _this.server = res.data.data.invite_info.server
          _this.avatar = res.data.data.user_info.avatar
          _this.invite_bg = res.data.data.invitation_background ? res.data.data.invitation_background : _this.invite_bg
          _this.invite_title_bg = res.data.data.invitation_title_img ? res.data.data.invitation_title_img : _this.invite_title_bg
          _this.exchange = res.data.data.exchange
          loadings.clear()
        }else{
          loadings.clear()
          Toast.fail(res.data.msg);
        }
        loadings.clear()
      })

    },
    href(href_d){
      const uid = this.$route.query.uid;
      const token = this.$route.query.token;
      //window.location.href = data;
      this.$router.push({path:'/'+href_d+'',query:{uid:uid, token: token }});
    },
    open_redbag(id){
      const _this = this;
      let is_open = 0;
      if(id==1){
        is_open = 1;
        _this.redbag.reda=2
        _this.redbag.redb=1
        _this.redbag.redc=1

      }else{
        const uid = this.$route.query.uid;
        const token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        let type = 2;
        if(id==2){
          type = 1;
        }
        const loadings = Toast.loading({
          message: this.$t('Tips.loading'),
          forbidClick: true,
          duration: 0, // 持续展示 toast
        });
        axios.get(this.baseUrlApi+"/vue/user_vue_api/get_invite_bag",{ params:{ uid:uid,token:token,type:type }}).then(res =>{
          //console.log(res.data)
          if(res.data.code==1){
            const _this = this;
            _this.red_list = res.data.data
            if(id==2){
              is_open = 1;
              _this.redbag.reda=1
              _this.redbag.redb=2
              _this.redbag.redc=1
            }else if(id==3){
              is_open = 1;
              _this.redbag.reda=1
              _this.redbag.redb=1
              _this.redbag.redc=2
            }
            loadings.clear()
          }else{
            loadings.clear()
            Toast.fail(res.data.msg);
          }

        })
      }
    },
    creatQrCode() {
      const text = this.server;
      document.getElementById("qrcode").innerHTML = "";
      var qrcode = new QRCode("qrcode", {
        text: text, // 需要转换为二维码的内容
        width: 130,
        height: 130,
        //colorDark: '#000000',
        //colorLight: '#ffffff',
        //correctLevel: QRCode.CorrectLevel.H
      })

    },
    showPopup(){
      this.show=true
      //this.creatQrCode()
    },
    getInviteRecord(){
      const uid = this.$route.query.uid;
      const token = this.$route.query.token;
      const income = this.invite_info.income;
      if(income<=0){
        Toast.fail(this.$t('Tips.There_no_rewards_claim'));
        return;
      }
      const loadings = Toast.loading({
        message: this.$t('Tips.loading'),
        forbidClick: true,
        duration: 0, // 持续展示 toast
      });
      //request_invite_record
      axios.get(this.baseUrlApi+"/vue/user_vue_api/request_invite_record",{ params:{ uid:uid,token:token }}).then(res =>{
        //console.log(res.data)
        if(res.data.code==1){
          const _this = this;
          _this.invite_info.income = 0;
          Toast.success(res.data.msg);
          loadings.clear()
        }else{
          loadings.clear()
          Toast.fail(res.data.msg);
        }

      })

    },
    inviteRedbag(id,k){
      const uid = this.$route.query.uid;
      const token = this.$route.query.token;
      const loadings = Toast.loading({
        message: this.$t('Tips.loading'),
        forbidClick: true,
        duration: 0, // 持续展示 toast
      });
      //request_invite_record
      axios.get(this.baseUrlApi+"/vue/user_vue_api/request_invite_redbag",{ params:{ uid:uid,token:token,id:id }}).then(res =>{
        console.log(res.data)
        if(res.data.code==1){
          var _this = this;
          _this.red_list[k].is_receive = 1;
          Toast.success(res.data.msg);
          loadings.clear()
        }else{
          loadings.clear()
          Toast.fail(res.data.msg);
        }

      })
    },
    share(type){
      const text = this.server;
      const avatar = this.avatar;
      if(type==1){
        window.location.href="wechat://invite?url="+text+"&avatar="+avatar;
      }else if(type==2){
        window.location.href="friend://invite?url="+text+"&avatar="+avatar;
      }else{
        // qq
        window.location.href="qq://invite?url="+text+"&avatar="+avatar;
      }
    }
  }
}

</script>

<style>
.invite-redbag-coin-img{width: 1rem;}
</style>



