<template>
  <div>
    <div class="dress-top-fixed">
      <div class="frame-top">
        <div class="frame-top-left float_l">{{ $t('column.available_number') }}：{{count}}</div>
        <div class="frame-top-right float_l">
          <div class="frame-top-right-box">
            <div :class="type==1?'frame-top-right-box-l-a':'frame-top-right-box-l'" @click="get_list_type(1)">{{ $t('column.all') }}</div>
            <div :class="type==2?'frame-top-right-box-r-a':'frame-top-right-box-r'" @click="get_list_type(2)">{{ $t('column.available') }}</div>
          </div>
        </div>
      </div>
      <div class="frame-box">
        <img class="frame-box-bg-avatar" :src="avatar" alt="">
        <div class="frame-box-content">
          <div class="frame-box-content-k">
            <img :src="avatar_frame" alt="">
          </div>
          <div class="frame-box-content-avatar">
            <img :src="avatar" alt="">
          </div>
        </div>
      </div>
    </div>
    <div class="dressup-center"></div>

      <van-list
        v-model="loading"
        :finished="finished"
        :finished-text="$t('Tips.no_more')"
        :loading-text="$t('Tips.loading')"
        @load="onLoad"
      >
      <div class="dress-box-bottom">
        <div class="float_l dress-box-bottom-a" v-for="val in list" :key="val.id" @click="switchFrame(val)">
          <div class="dress-avatar-k">
            <img :src="val.icon" alt="">
          </div>
          <div class="dress-box-bottom-b" :class="id==val.id?'dress-box-bottom-a-b':''">
            <div class="dress-box-bottom-b-t">
              <img class="dress-avatar" src="../assets/img/tutu@2x.png" alt="">
            </div>
            <div class="dress-box-bottom-b-name">{{val.name}}</div>
            <div class="dress-box-bottom-b-text">{{ $t('column.Head_frame') }}</div>
          </div>
        </div>
        </div>
      </van-list>

    <div class="dressup-bottom"></div>
    <div class="fram-button" @click="get_list_id()">{{ $t('btn.Get_it_now') }}</div>
   <!-- <div v-if="is_receive==1" class="fram-button" @click="get_list_id()">立即获取</div>
    <div v-if="is_receive==0" class="fram-button fram-button-o">立即获取</div> -->
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        loading: false,
        finished: false,
        page:1,
        user_account: false,
        withdrawal: '',
        list:[],
        avatar:'',
        avatar_frame:'',
        is_receive:0,
        count:0,
        //privilege:[],
        id:0,
        coin:0,
        type:1,
      }
    },
    created: function() {
      //this.convert();
      //this.get_list();
      document.title = this.$t('title.avatar_frame')
    },
    methods: {
      onLoad(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var page = this.page;
        var type = this.type;
        axios.get(this.baseUrlApi+"/vue/dress_api/get_avatar_frame",{ params:{ uid:uid,token:token,type:type,page:page}}).then(res =>{
           console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             var ret_data = res.data.data
             if(page==1){
               _this.avatar = ret_data.user_info.avatar
               _this.avatar_frame = ret_data.avatar_frame
               _this.id = ret_data.id
               _this.is_receive = ret_data.is_receive
               _this.count = ret_data.count
             }
             //_this.list = res.data.data.list
             for(var i =0;i<ret_data.list.length;i++){
              _this.list.push(ret_data.list[i])
             }
             _this.loading = false
             var limit = ret_data.limit
             if(ret_data.list.length<limit){
               _this.finished = true
             }

             _this.page++

           }else{
             Toast(res.data.msg);
           }

        })
      },
      href_log(id){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //window.location.href = data;
        this.$router.push({path:'/help_info',query:{uid:uid, token: token,id:id }});
      },
      get_list_id(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var id = this.id
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/vue/dress_api/request_dress_up",{ params:{ uid:uid,token:token,id:id }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             /* var _this = this;
             _this.list = res.data.data.list
             _this.avatar = res.data.data.user_info.avatar
             _this.avatar_frame = res.data.data.avatar_frame
             _this.id = res.data.data.id
             _this.is_receive = res.data.data.is_receive
             _this.count = res.data.data.count */

             //_this.privilege = res.data.data.privilege

             Toast(res.data.msg);
           }else{
             Toast(res.data.msg);
           }

        })
      },
      get_list_type(type){
        this.type = type
        this.page = 1
        this.list = []
        this.onLoad()
      },

      switchFrame(info){
        console.log(info);
        this.id = info.id
        this.avatar_frame = info.icon
        this.is_receive = info.is_receive
      }
    }
  }

</script>

<style>
  #app,body{
    background-color: #F5F7FA;
    /* overflow: hidden; */
  }

</style>



