<template>
  <div id="">
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
    <van-list
      v-model="loading"
      :finished="finished"
      :finished-text="$t('Tips.no_more')"
:loading-text="$t('Tips.loading')"
      @load="onLoad"
    >
      <div class="" v-for="(val,key) in list" :key="val.id">
        <div class="msg-text">{{val.addtime}}</div>
        <div class="msg-box" @click="hrefInfo(val.id)">
          <div class="msg-box-a">
            <div class="msg-box-a-l float_l">{{val.title}}</div>
            <div class="msg-box-a-r float_r">{{val.endtime}} <img src="../assets/img/9.png" alt=""></div>
          </div>
          <div class="msg-box-b">{{val.content}}</div>
          <img class="msg-box-img" :src="val.img" alt="">
        </div>
      </div>
      <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
    </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        list:[],
        info:[],
        privilege:[],
        loading:false,
        finished:false,
        refreshing: false,
        page:1,
      }
    },
    created: function() {
      //this.convert();
      //this.get_list();
      document.title = this.$t('title.active_message')
    },
    methods: {
      onLoad(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var page = this.page;

        axios.get(this.baseUrlApi+"/vue/system_message_api/get_system_activity",{ params:{ uid:uid,token:token,page:page,type:2 }}).then(res =>{
           // console.log(res.data)
           this.loading = false;
           if(res.data.code==1){
             if(res.data.data==''){
               this.finished = true
             }
             var _this = this;
             var list = res.data.data
             //_this.list = list
             _this.page++;
             for(var i =0;i<list.length;i++){
              _this.list.push(list[i])
             }
           }else{
             this.finished = true
           }
           this.loading = false;

        })
      },
      onRefresh(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var page = 1;
        this.finished = false;
        // 重新加载数据
        // 将 loading 设置为 true，表示处于加载状态
        //this.loading = true;

        axios.get(this.baseUrlApi+"/vue/system_message_api/get_system_activity",{ params:{ uid:uid,token:token,page:page,type:2 }}).then(res =>{
           //console.log(res.data)
           this.refreshing = false;
           if(res.data.code==1){
             if(res.data.data==''){
               this.refreshing = true
             }
             var _this = this;
             var list = res.data.data
             _this.list = list
           }else{
             this.refreshing = true
           }
           //console.log(page)

        })
      },
      href(data){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;

        window.location.href=data+'?uid='+uid+'&token='+token
        //window.location.href = data;
        //this.$router.push({path:'/system_content',query:{uid:uid, token: token,id:id }});
      },
      hrefInfo(id){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;

        axios.get(this.baseUrlApi+"/vue/system_message_api/get_system_activity_info",{ params:{ uid:uid,token:token,id:id }}).then(res =>{
           // console.log(res.data)
           if(res.data.code==0){
             Toast.fail(res.data.msg);
           }else{
             this.$router.push({path:'/system_activity_info',query:{uid:uid, token: token,id:id }});
           }
        })
        // this.$router.push({path:'/system_activity_info',query:{uid:uid, token: token,id:id }});
      }
    }
  }

</script>

<style>
  #app,body{
    background-color: #F5F7FA;
  }
  .msg-text{
    text-align: center;
    color: #999999;
    line-height: 3rem;
  }
  .msg-box{
    width: 95%;
    margin: 0 auto;
    background-color: #FFFFFF;
    border-radius: 0.5rem;
  }
  .msg-box-a,.msg-box-b,.msg-box-c{
    width: 90%;
    margin: 0 auto;
  }
  .msg-box-a{
    line-height: 4rem;
  }
  .msg-box-c{
    margin-top: 1rem;
    line-height: 3rem;
    border-top: 1px solid #EEEEEE;
    overflow: hidden;
  }
  .msg-box-b{
    color: #999999;
    font-size: 0.9rem;
    overflow: hidden;
  }
  .msg-box-c-l{
    color: #5C96F6;
  }
  .msg-box-a-r{
    color: #999999;
    font-size: 0.9rem;
  }
  .msg-box-img{
    width: 100%;
    margin-top: 1rem;
  }
  .msg-box-a img{
    float: right;
    height: 0.8rem;
    margin-top: 1.6rem;
    margin-left: 0.5rem;
  }
  .msg-box-a-l {
    width: 40%;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
</style>



