<template>
  <div>
    <div class="dress-top-fixed">
      <div class="frame-top">
        <div class="frame-top-left float_l">{{ $t('column.available_number') }}：{{count}}</div>
        <div class="frame-top-right float_l">
          <div class="frame-top-right-box">
            <div :class="type==1?'frame-top-right-box-l-a':'frame-top-right-box-l'" @click="get_list_type(1)">{{ $t('column.all') }}</div>
            <div :class="type==2?'frame-top-right-box-r-a':'frame-top-right-box-r'" @click="get_list_type(2)">{{ $t('column.available') }}</div>
          </div>
        </div>
      </div>
      <div class="frame-box" >
        <img class="frame-box-bg-avatar" :src="avatar" alt="">
        <div class="frame-box-content">
          <div class="chat-bg-img-demo-div">
            <img class="chat-bg-img-demo" :src="img_bg" alt="">
          </div>

        </div>
      </div>
    </div>

    <van-pull-refresh v-model="refreshing" @refresh="onRefresh" style="">
      <van-list
        v-model="loading"
        :finished="finished"
        :finished-text="$t('Tips.no_more')"
        :loading-text="$t('Tips.loading')"
        @load="onLoad"
      >
        <div class="chat-bg-box-box van-clearfix">
          <div class="float_l dress-box-bottom-a" v-for="val in list" :key="val.id" @click="switchFrame(val)">

            <div class="dress-box-bottom-b" :class="id==val.id?'dress-box-bottom-a-b':''">
              <div class="dress-box-bottom-b-t">
                <img class="chat-bg-bubble-icon-medal" :src="val.img_bg" alt="">
              </div>
              <div class="dress-box-bottom-b-name">{{val.name}}</div>

            </div>
          </div>
        </div>

      </van-list>
    </van-pull-refresh>

    <div class="dressup-bottom"></div>
    <div class="fram-button" @click="get_list_id()">{{ $t('btn.Get_it_now') }}</div>
    <!-- <div v-if="is_receive==1" class="fram-button" @click="get_list_id()">立即获取</div>
    <div v-if="is_receive==0" class="fram-button fram-button-o">立即获取</div> -->
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import SVGA from 'svgaplayerweb'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        list:[],
        avatar:'',
        avatar_frame:'',
        is_receive:0,
        count:0,
        //privilege:[],
        id:0,
        coin:0,
        type:1,
        img_bg:'',
        v_bg:'',
        loading: false,
        finished: false,
        refreshing: false,
        page: 1,
      }
    },
    created: function() {
      //this.convert();
      //this.get_list();
      document.title = this.$t('title.chat_bg')
    },
    methods: {
      onLoad(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var p = this.page;
        var type = this.type;
        var _this = this;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/vue/dress_api/get_chat_bg",{ params:{ uid:uid,token:token,type:type,page:p }}).then(res =>{
           console.log(res.data)
           if(res.data.code==1){
             this.loading = false;
             var list = res.data.data.list;
             if(res.data.data.is_page == 1){
               _this.finished = true
               return;
             }
             _this.page ++
             if(p == 1){
                _this.list = res.data.data.list
                _this.avatar = res.data.data.user_info.avatar
                _this.avatar_frame = res.data.data.avatar_frame
                _this.id = res.data.data.id
                _this.is_receive = res.data.data.is_receive
               _this.v_bg = res.data.data.v_bg
               _this.img_bg = res.data.data.img_bg
               _this.count = res.data.data.count
             }else{
               for(var i =0;i<list.length;i++){
                  _this.list.push(list[i])
               }
             }
           }else{
             Toast.fail(res.data.msg);
           }

        })
      },
      href_log(id){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //window.location.href = data;
        this.$router.push({path:'/help_info',query:{uid:uid, token: token,id:id }});
      },
      get_list_id(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var id = this.id
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/vue/dress_api/request_dress_up",{ params:{ uid:uid,token:token,id:id }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             /* var _this = this;
             _this.list = res.data.data.list
             _this.avatar = res.data.data.user_info.avatar
             _this.avatar_frame = res.data.data.avatar_frame
             _this.id = res.data.data.id
             _this.is_receive = res.data.data.is_receive
             _this.count = res.data.data.count */

             //_this.privilege = res.data.data.privilege

             Toast(res.data.msg);
           }else{
             Toast(res.data.msg);
           }

        })
      },
      onRefresh(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var page = 1;
        this.finished = false;
        // 重新加载数据
        // 将 loading 设置为 true，表示处于加载状态
        //this.loading = true;
        axios.get(this.baseUrlApi+"/vue/dress_api/get_chat_bg",{ params:{ uid:uid,token:token,type:1 }}).then(res =>{
           //console.log(res.data)
           this.refreshing = false;
           this.page = 2;
           if(res.data.code==1){
             var _this = this;
             _this.list = res.data.data.list
             _this.avatar = res.data.data.user_info.avatar
             _this.avatar_frame = res.data.data.avatar_frame
             _this.id = res.data.data.id
             _this.is_receive = res.data.data.is_receive
            _this.v_bg = res.data.data.v_bg
            _this.img_bg = res.data.data.img_bg

           }else{
             Toast.fail(res.data.msg);
           }

        })
      },
      switchFrame(info){
        //console.log(info);
        var _this = this
        this.id = info.id
        this.avatar_frame = info.icon
        this.is_receive = info.is_receive
        //_this.svga = info.img_bg
        _this.v_bg = info.v_bg
        _this.img_bg = info.img_bg
        //_this.playerSvga(_this.svga)
      },
      get_list_type(type){
        this.type = type
        this.page = 1
        this.list = []
        this.onLoad()
      }
    }
  }

</script>

<style>
  #app,body{
    background-color: #F5F7FA;
    /* overflow: hidden; */
  }
  /* .frame-box-content-avatar img{
    border-radius: 0;
    width: 50%;
    object-fit: cover;
  } */


  </style>



