<template>
  <div id="app">
    <div class="first_charge_height">
      <div class="first_charge_div">
        <div class="first_charge_x" @click="first_charge_close"><van-icon name="close" size="30"/></div>
        <van-row>
          <van-col span="22" offset="1" class="scrollerContainer">
            <div class="first_charge_top">
              <div  v-for="(v,i) in list" :key="i" :class="type_i === i ? 'first_charge_name first_charge_checked' : 'first_charge_name'" @click="first_charge_type_btn(i)"> {{ v.name }}</div>
            </div>
          </van-col>
          <van-col span="20" offset="2" class="first_charge_center">
            <van-col span="20" offset="2" class="first_charge_center_div">
              <van-grid :column-num="2" :border="false">
                <van-grid-item v-for="(v,j) in details.gift_list" :key="j">
                  <img :src="v.img ? v.img : coin" style="max-height: 6vh;max-width:6vh;"/>
                  <van-col span="24" class="first_charge_center_div_title">{{ v.title }}</van-col>
                </van-grid-item>
              </van-grid>
            </van-col>
            <van-col span="20" offset="2" class="first_charge_center_instructions">{{ details.first_centent }}</van-col>
          </van-col>
          <van-col span="20" offset="2" class="first_charge_button">
            <van-button color="#fa9a4a" block round @click="first_charge_close">立 即 充 值</van-button>
          </van-col>
        </van-row>
      </div>
    </div>

  </div>
</template>
<script>
  import axios from 'axios'
  import { Toast, Popover, Dialog } from 'vant'
  export default {
    name: 'app',
    data() {
      return {
        list: [],
        details: {},
        type_i: 0,
        coin: require('../assets/img/coin.png')
      }
    },
    created: function() {
      document.title = '首次充值'
      this.get_list()
    },
    methods: {
      first_charge_type_btn(i) {
        this.type_i = i
        this.details = {
          first_centent: this.list[this.type_i].first_centent,
          gift_list: this.list[this.type_i].gift_list
        }
      },
      first_charge_close() {
        // 拦截关闭
        window.location.href="live://live?action=close"
      },
      get_list(){
        const uid = this.$route.query.uid;
        const token = this.$route.query.token;
        axios.get(this.baseUrlApi+"/vue/wallet_api/first_charge",{
          params:{
            uid: uid,
            token: token
          }
        }).then(res =>{
          if(res.data.code==1){
            this.type_i = 0
            this.list = res.data.data
            if (this.list.length > 0) {
              this.details = {
                first_centent: this.list[this.type_i].first_centent,
                gift_list: this.list[this.type_i].gift_list
              }
            }
            console.log(this.count)
          }else{
            Toast(res.data.msg);
          }
        })
      },
    }
  }
</script>



