<template>
  <div id="lucky_gift_log">
      <van-row>
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        :finished-text="$t('Tips.no_more')"
        :loading-text="$t('Tips.loading')"
        @load="onLoad"
      >
        <van-col span="24" class="lucky_gift_log_border" v-for="(val,key) in list" :key="key">
            <van-col span="24" class="lucky_gift_log_bg">
                <van-col span="4" class="lucky_gift_log_bg_coin2">
                  <img src="../assets/img/coin2.png"/>
                </van-col>
                <van-col span="12" class="lucky_gift_log_bg_title">
                    {{ val.status === 1 ? $t('column.Lucky_gift') : $t('column.Lucky_jackpot') }} ×{{ val.num }}
                </van-col>
                <van-col span="8" class="lucky_gift_log_bg_coin3">
                    {{ val.coin }} <img src="../assets/img/coin3.png"/>
                </van-col>
            </van-col>
            <van-col span="24" class="lucky_gift_log_bottom">
                <van-col span="12">
                    <van-col span="24" class="lucky_gift_log_bottom_time">{{ val.addtime }} </van-col>
                    <van-col span="24" class="lucky_gift_log_bottom_coin">{{ $t('column.Coins_reward') }}: {{ val.winning }} </van-col>
                </van-col>
                <van-col span="12" v-if="val.host_id">
                    <van-col span="24" class="lucky_gift_log_bottom_name">{{ val.user_nickname }} </van-col>
                    <van-col span="24" class="lucky_gift_log_bottom_name">ID: {{ val.host_id }} </van-col>
                </van-col>
            </van-col>
        </van-col>

      </van-list>
    </van-pull-refresh>
      </van-row>
  </div>
</template>

<script>
import '@/assets/css/style_a.css'
import axios from 'axios'
export default {
  data:function() {
    return {
      list:[],
      loading:false,
      finished:false,
      refreshing: false,
      page:1,
    }
  },
  created: function() {
    document.title = this.$t('title.lucky_gift_log')
  },
  methods: {
    onLoad(){
      this.get_list()
    },
    get_list(){
      const uid = this.$route.query.uid
      const token = this.$route.query.token
      const page = this.page
      axios.get(this.baseUrlApi+"/vue/user_vue_api/lucky_gift_log",{ params:{ uid:uid,token:token,page:page }}).then(res =>{
        if(res.data.code ===1){
          if(res.data.data ===''){
            this.loading = false
            this.finished = true
            this.refreshing = false
            return
          }
          var list = res.data.data
          this.page++
          for(var i =0;i<list.length;i++){
            this.list.push(list[i])
          }
          if(list.length<0){
              this.finished = true
          }
        }else{
            this.finished = true
        }
        this.refreshing = false
        this.loading = false
      })
    },
    onRefresh(){
      this.page = 1
      this.finished = false
      // 重新加载数据
      this.get_list()
    }
  }
}

</script>

<style>
#app,body{
  background-color: #F5F7FA;
}

</style>



