<template>
  <div id="">
    <div class="noble-top">
      <div class="noble-top-t">
        <div class="noble-top-a" v-for="(noble,key) in list" :key="noble.id" @click="get_list_info(noble.id)">
          <div class="noble-top-a-l">
            <div class="noble-top-a-l-img">
              <img :src="noble.noble_img" alt="">
            </div>
            <div :class="id == noble.id ? 'noble-top-a-l-name' : 'noble-top-a-l-name-a'">{{noble.name}}</div>
          </div>
          <!-- <div :class="'noble-top-a-r'+key " class="noble-top-a-r">
          </div> -->
        </div>
      </div>
      <div class="noble-top-b">
        <div class="noble-top-b-img">
          <img :src="info.noble_img" alt="">
        </div>
        <div class="noble-top-b-name">{{info.name}} {{ $t('column.noble') }}</div>
      </div>
    </div>
    <div class="noble-buttom">
      <div class="noble-buttom-top"><img src="../assets/img/yongyoutequan.png" alt=""></div>
      <div class="noble-top-b-bag">
        <div class="noble-top-b-bag-l">
          <h2>{{ $t('column.Gift_bag') }}: </h2>
          <h5>{{info.return_coin}} {{currency_name}}</h5>
        </div>
        <img class="noble-top-b-bag-r" src="../assets/img/guizhuzhognxin_bac_tu@2x.png" alt="">
      </div>
      <div class="noble-buttom-b">
        <div class="noble-buttom-b-i" v-for="(val,key) in privilege" :key="val.id">
          <img :src="val.privilege_img" alt="">
          <div class="noble-buttom-c">{{val.name}}</div>
        </div>
      </div>
    </div>
    <div class="noble-center">
    </div>
    <div class="noble-button-a"></div>
    <div class="noble-button-b">
      <div class="noble-button-b-left">
        <div class="noble-button-b-b">{{ $t('column.Upgrade_recharge') }}<span>{{info.coin}}</span>{{currency_name}}/{{ $t('other.month') }}</div>
        <div class="noble-button-b-b-xf">({{ $t('column.Premium_recharge') }}<span>{{info.renew_coin}}</span>{{currency_name}}/{{ $t('other.month') }})</div>
      </div>
      <div class="noble-button-b-right">
        <div v-if="noble_id==info.id" class="noble-button-buy noble-button-buy-bj" @click="buyNoble(2)">{{ $t('btn.relegation') }}</div>
        <div v-else class="noble-button-buy " @click="buyNoble(1)">{{ $t('btn.Recharge_now') }}</div>
<!--        <div class="noble-button-buy " @click="buyNoble(1)">立即充值</div>-->
      </div>
    </div>
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast,Dialog } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        list:[],
        info:[],
        privilege:[],
        id:0,
        coin:0,
        currency_name:'',
        noble_id:0,
      }
    },
    created: function() {
      //this.convert();
      this.get_list();
      document.title = this.$t('title.noble')
    },
    methods: {
      get_list(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/api/noble_vue_api/get_info",{ params:{ uid:uid,token:token }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             _this.list = res.data.data.noble_list
             _this.info = res.data.data.noble_info
             _this.privilege = res.data.data.privilege
             _this.id =  _this.info.id
             _this.currency_name =  res.data.data.currency_name
             _this.noble_id = res.data.data.noble_id
             //console.log(_this.list)
             //this.bank_name = list[0]['name']
           }

        })
      },
      get_list_info(pid){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/api/noble_vue_api/get_info",{ params:{ uid:uid,token:token,id:pid }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             _this.list = res.data.data.noble_list
             _this.info = res.data.data.noble_info
             _this.privilege = res.data.data.privilege
             _this.id =  _this.info.id

             //console.log(_this.list)
             //this.bank_name = list[0]['name']
           }

        })
      },
      buyNoble(status){
        let msg;
        const info = this.info;
        const currency_name = this.currency_name;
        if(status==1){
          msg = info.name + ','+ this.$t('Tips.Recharge_or_not') +info.coin+currency_name;
        }else{
          msg = info.name + ',' + this.$t('Tips.relegation_Recharge_or_not') + info.renew_coin + currency_name;
        }
        Dialog.confirm({
          title: this.$t('Tips.Confirm_recharge'),
          message: msg,
          cancelButtonText: this.$t('btn.cancel'),
          confirmButtonText: this.$t('btn.confirm'),
          closeOnClickOverlay: true
        }).then(() => {
            // on confirm
            //console.log(1)
            var uid = this.$route.query.uid;
            var token = this.$route.query.token;
            var id = this.id;
            axios.get(this.baseUrlApi+"/api/noble_vue_api/buy_noble",{ params:{ uid:uid,token:token,id:id }}).then(res =>{
               //console.log(res.data)
               if(res.data.code==1){
                 var _this = this;
                 /* _this.list = res.data.data.noble_list
                 _this.info = res.data.data.noble_info
                 _this.privilege = res.data.data.privilege */
                 _this.noble_id =  id

                 //console.log(_this.list)
                 //this.bank_name = list[0]['name']
               }
               Toast(res.data.msg)

            })
        }).catch(() => {
            // on cancel
            // console.log(2)
        });
        return;
      }
    }
  }

</script>

<style>
</style>



