<template>
  <div id="login">
    <van-row>
      <van-col span="22" offset="1" class="login_title">
        手机登录
      </van-col>
      <van-col span="22" offset="1"  class="login_center">
        <van-cell-group inset>
          <!-- 输入手机号，调起手机号键盘 -->
          <van-field v-model="tel" type="tel" label="手机号" />
          <van-field
            v-model="sms"
            center
            clearable
            label="验证码"
            placeholder="短信验证码"
          >
            <template #button>
              <van-button size="small" color="#00a1ff" v-if="isSend" @click="SendVerificationCode">发送验证码</van-button>
              <van-button size="small" color="#00a1ff" v-else>{{ time }}秒后重新获取</van-button>
            </template>
          </van-field>
        </van-cell-group>
      </van-col>
      <van-col span="20" offset="2" class="login_btn">
        <van-button size="large" round color="#00a1ff" @click="login_btn"> 登录 </van-button>
      </van-col>
    </van-row>
  </div>
</template>
<script>
  //请求接口组件
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant';

  export default {
    name: 'Login',
    data:function() {
      return {
        isBtn: false,
        tel: undefined,
        sms: '',
        isSend: true,
        time: 60
      }
    },
    created: function() {
      document.title = '手机登录'
    },
    methods: {
      //倒计时
      sendMsg() {
        this.isSend = false
        let timer = setInterval(()=> {
          this.time --;
          if(this.time <= 0 ) {
            this.isSend = true
            clearInterval(timer)
            this.time = 60
          }
        },1000)
      },
      // 发送验证码
      SendVerificationCode() {
        if (this.isBtn) {
          return false
        }
        this.isBtn = true
        axios.get(this.baseUrlApi+"/api/login_api/code",{
          params:{
            mobile: this.tel
          }
        }).then(res =>{
          console.log(res.data.data)
          this.isBtn = false
          if (res.data.code === 0) {
            Toast.fail({ message: res.data.msg, duration: 1000 })
          } else {
            Toast.success({ message: res.data.msg, duration: 1000 })
            this.sendMsg()
          }
        })
      },
      // 登录
      login_btn(){
        if(this.tel.length <=0) {
          Toast.fail('请输入验证码')
          return;
        }
        if(this.sms.length <=0) {
          Toast.fail('请输入验证码')
          return;
        }
        if (this.isBtn) {
          return false
        }
        this.isBtn = true
        axios.get(this.baseUrlApi+"/vue/login_api/login_vue",{
          params:{
            mobile: this.tel,
            code: this.sms
          }
        }).then(res =>{
          console.log(res.data.data)
          this.isBtn = false
          if (res.data.code === 0) {
            Toast.fail({ message: res.data.msg, duration: 1000 })
          } else {
            Toast.success({ message: res.data.msg, duration: 1000 })
            this.Jump(res.data.data)
          }
        })
      },
      Jump(data){
        this.$router.push({path:'/withdrawal',query:{uid: data.uid, token: data.token }});
      },
    }

  }
</script>



