<template>
  <div id="guild_description">
    <van-nav-bar
      class="nav-pad-top"
      left-arrow
      fixed
      safe-area-inset-top
      @click-left="onClickLeft"
    >
      <template #left>
        <van-icon name="arrow-left" size="18" color="#ffffff"/>
      </template>
      <template #title>
        <span class="wealth_level_title">{{ $t('guild.Agency_application') }}</span>
      </template>
    </van-nav-bar>
    <van-row>
      <van-col span="22" offset="1" class="guild_description_type">
        <van-col span="24" class="guild_description_type_val">
          <img src="../assets/img/guild_description1.png" alt="">
          <div class="guild_description_type_name">{{ $t('guild.Certification') }}</div>
        </van-col>
        <van-col span="24" class="guild_description_type_val">
          <img src="../assets/img/guild_description2.png" alt="">
          <div class="guild_description_type_name">{{ $t('guild.High_salary') }}</div>
        </van-col>
        <van-col span="24" class="guild_description_type_val">
          <img src="../assets/img/guild_description3.png" alt="">
          <div class="guild_description_type_name">{{ $t('guild.High_welfare') }}</div>
        </van-col>
      </van-col>
      <van-col span="22" offset="1" class="guild_description_title">{{ guild_description_title }}</van-col>
      <van-col span="22" offset="1">
        <div v-html="guild_description_content" class="guild_description_content">{{ guild_description_content }}</div>
      </van-col>
      <van-col span="22" offset="1" class="guild_description_checkbox">
        <van-checkbox v-model="checked" class="guild_description_checkbox_name" icon-size="18px" checked-color="#D63AB7">
         {{ $t('guild.Relevant_provisions') }}
        </van-checkbox>
      </van-col>
      <van-col span="22" offset="1" class="guild_description_checkbox">
        <van-button color="linear-gradient(to left, #CA26ED, #F86C2F)" round size="large" @click="Jump">{{ $t('guild.Apply_join') }}</van-button>
      </van-col>
    </van-row>
  </div>

</template>
<script>
import '@/assets/css/style_a.css'
import axios from 'axios'
import { Toast } from 'vant'
export default {
  data:function() {
    return {
      guild_description_title: '',
      guild_description_content: '',
      checked: false,
    }
  },
  created: function() {
     this.get_list();
    document.title = this.$t('guild.Agency_application')
  },
  methods: {
    onClickLeft() {
      // 拦截返回app
      window.location.href="live://live?action=close"
    },
    Jump(){
      if (this.checked != true) {
        Toast.fail(this.$t('guild.please_check_relevant_provisions'))
        return false
      }
      const token = this.$route.query.token
      const uid = this.$route.query.uid
      this.$router.push({path:'/GuildDescriptionChoose',query:{ uid: uid, token: token }})
    },
    get_list(){
      const token = this.$route.query.token
      const uid = this.$route.query.uid
      axios.get(this.baseUrlApi+"/vue/guild_api/rule_specification",{ params:{ uid: uid, token:token }}).then(res =>{
        if(res.data.code==1){
          this.guild_description_title = res.data.data.title
          this.guild_description_content = res.data.data.content
        }else{
          Toast.fail(res.data.msg)
        }
      })
    }
  }
}

</script>

<style>

</style>



