<template>
  <div>
    <div class="banner">
      <van-tabs v-model="active" swipeable title-active-color="#9563F7" color="#9563F7">
        <van-tab v-for="val in title" :key="val.id">
          <template #title> {{val.name}} </template>
          <div class="wurengrong" v-if="(val.id == 1 && is_medal == 0) || (val.id == 2 && is_home_page == 0) || (val.id == 3 && is_avatar_frame == 0) || (val.id == 4 && is_chat_bubble == 0) || (val.id == 5 && is_chat_bg == 0) || (val.id == 7 && is_car == 0) ">
            <img src="../assets/img/zanwuneirong.png" alt="">
            <div>{{ $t('Tips.no_more')}}</div>
          </div>
          <div class="my-dress">
            <div class="my-dress-box" v-for="v in list" :key="v.id" v-if="v.type==val.id">
              <div class="my-dress-boxa">
                <img class="my-dress-boxa-img" :src="v.icon" alt="">
                <div class="my-dress-boxa-name">{{v.name}}</div>
                <div>
                  <van-switch v-model="check[v.item]" size="24px" @change="requesStatus(v.id)" active-color="#9563F7" inactive-color="#dcdee0"/>
                </div>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        list:[],
        type_list:[],
        //privilege:[],
        id:0,
        check:[],
        currency_name:'',
        status1:true,
        active:0,
        is_medal:1,
        is_home_page:1,
        is_avatar_frame:1,
        is_chat_bubble:1,
        is_chat_bg:1,
        is_car:1,
        title:[
          {id:1,name:'勋章',img:'/xunzhang@2x.png'},
          {id:2,name:'主页特效',img:'/zhuyetexiao@2x.png'},
          {id:3,name:'头像框',img:'/touxiangkuang@2x.png'},
          {id:4,name:'聊天气泡',img:'/liaotianbeijing@2x.png'},
          {id:5,name:'聊天背景',img:'/liaotianbeijing@2x.png'},
          {id:7,name:'进场动画',img:'/liaotianbeijing@2x.png'},
        ]
      }
    },
    created: function() {
      //this.convert();
      this.get_list();
      document.title = this.$t('title.my_dress')
      for(let i= 0; i < this.title.length; i++) {
        if (this.title[i].id === 1 ) {
          this.title[i].name = this.$t('btn.medal')
        }else if(this.title[i].id === 2) {
          this.title[i].name = this.$t('btn.Home_effects')
        }else if(this.title[i].id === 3) {
          this.title[i].name = this.$t('btn.Head_frame')
        }else if(this.title[i].id === 4) {
          this.title[i].name = this.$t('btn.Chat_bubble')
        }else if(this.title[i].id === 5) {
          this.title[i].name = this.$t('btn.Chat_background')
        }else {
          this.title[i].name = this.$t('btn.Mobilization_effect')
        }
      }
    },
    methods: {
      get_list(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/vue/dress_api/get_my_dress",{ params:{ uid:uid,token:token }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             _this.list = res.data.data
             var list = res.data.data
             for(var i=0;i<list.length;i++){
               if(list[i].status==1){
                 //var aa =
                 //console.log(aa)
                 _this.check['status'+list[i].id]= true
               }else{
                 _this.check['status'+list[i].id]= false
               }
             }
             _this.is_medal = res.data.is_medal
             _this.is_home_page = res.data.is_home_page
             _this.is_avatar_frame = res.data.is_avatar_frame
             _this.is_chat_bubble = res.data.is_chat_bubble
             _this.is_chat_bg= res.data.is_chat_bg
             _this.is_car= res.data.is_car
             //console.log(_this['status1'])
           }else{
             Toast.fail(res.data.msg);
           }

        })
      },
      href(){
        console.log(this.check)
        return
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var path = '';
        if(id==3){
          path = 'avatar_frame';
        }else if(id==4){
          path = 'chat_bubble'
        }else if(id==5){
          path = 'chat_bg'
        }else if(id==2){
          path = 'home_page'
        }else if(id==1){
          path = 'medal'
        }
        //window.location.href = data;
        this.$router.push({path:'/'+path+'',query:{uid:uid, token: token,id:id }});
      },
      requesStatus(id){
        //request_dress_status
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/vue/dress_api/request_dress_status",{ params:{ uid:uid,token:token,id:id }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             Toast.success(res.data.msg)
             this.get_list();
             //console.log(_this['status1'])
           }else{
             Toast.fail(res.data.msg);
           }

        })
      }
    }
  }

</script>

<style>


</style>



