<template>
  <div id="guild_bg">
    <van-nav-bar title="家族名片" safe-area-inset-top @click-left="onClickLeft" left-arrow>
    </van-nav-bar>
    <van-row>
      <van-col span="22" offset="1" class="guild_top">
        <van-col span="5" class="guild_avatar">
          <van-image
            width="8.5vh"
            height="8.5vh"
            fit="cover"
            :src="guild.logo"
          />
        </van-col>
        <van-col span="19" class="guild_avatar_left">
          <van-col span="24" class="guild_name">{{ guild.name }}</van-col>
          <van-col span="24" class="guild_id"><img src="../assets/img/guild_id.png"/> {{ guild.id }}</van-col>
          <van-col span="24" class="guild_introduce">{{ guild.introduce }}</van-col>
        </van-col>
      </van-col>
      <van-col span="22" offset="1" class="guild_bg2">
        <van-col span="20" offset="2" class="guild_Notice"><em>家族公告</em></van-col>
        <van-col span="20" offset="2" class="guild_Notice_centent">{{ guild.notice }}</van-col>
      </van-col>
      <van-col span="24" class="guild_bg3">
        <van-col span="20" offset="1" class="guild_bg3_number">家族成员({{ join_number }})</van-col>
        <van-col span="24" class="guild_bg3_width_hide">
         <div class="guild_bg3_width">
           <div class="guild_bg3_width_type" v-for="(item,e) in join_list" :key="e">
             <van-col span="24" v-for="(t,m) in item" :key="t.id" :class="e === 0 && m === 0 ? 'guild_bg3_width_type_centent1' : 'guild_bg3_width_type_centent'" >
               <van-col span="16" offset="1" class="guild_bg3_width_type_name">{{ t.user_nickname }}</van-col>
               <van-col span="22" offset="1" class="guild_bg3_width_type_id"><img src="../assets/img/guild_id2.png"/> {{ t.user_id }}</van-col>
               <van-col span="22" offset="1" class="guild_bg3_width_type_sex">
                <span class="guild_bg3_width_type_sex_color">年龄:</span> <span>{{ t.age }}</span>
                 <span class="guild_bg3_width_type_sex_color">性别:</span> <span>{{ t.sex === 2 ? '女' : '男'}}</span>
                 <span class="guild_bg3_width_type_sex_color" v-if="t.city">城市:</span> <span v-if="t.city">{{ t.city }}</span>
               </van-col>
               <van-col span="22" offset="1" class="guild_bg3_width_type_sx">
                 <div class="guild_bg3_width_type_sx_btn" @click="private_letter(t.user_id)" v-if="uid != t.user_id">私信</div>
                 <div class="guild_bg3_width_type_sx_btn_r">{{ t.user_id === guild.user_id ? '族长' : '成员' }}</div>
               </van-col>
               <div class="guild_bg3_width_type_ab" @click="JumpUserinfo(t.user_id)">
                 <van-image
                   width="7vh"
                   height="7vh"
                   fit="cover"
                   round
                   :src="t.avatar"
                 />
               </div>
             </van-col>
           </div>
         </div>
        </van-col>
        <van-col span="20" offset="1" class="guild_bg3_number">家族派对({{ voice_number }})</van-col>
        <van-col span="24" class="guild_bg3_list">
                  <van-list
                    v-model="loading"
                    :finished="finished"
                    finished-text="没有更多了"

                    @load="onLoad"
                  >
                    <van-cell v-for="(item,i) in list" :key="i">
                      <van-col span="4" @click="make_friends(item.voice_id)">
                        <van-image
                          width="6vh"
                          height="6vh"
                          fit="cover"
                          round
                          :src="item.avatar"
                        />
                      </van-col>
                      <van-col span="14" class="guild_bg3_list_centent">
                        <van-col span="24" class="guild_bg3_list_centent_name">
                          {{ item.title }}
<!--                          <span :class="item.sex === 1 ? 'guild_bg3_list_centent_sex1' : 'guild_bg3_list_centent_sex'">-->
<!--                            <img src="../assets/img/sex.png"/> {{ item.age }}-->
<!--                          </span>-->
                        </van-col>
                        <van-col span="24" class="guild_bg3_list_centent_id">ID: {{ item.voice_id }}</van-col>
                      </van-col>
                      <van-col span="5">
                        <div class="guild_bg3_list_btn" @click="make_friends(item.voice_id)">交友</div>
                      </van-col>
                    </van-cell>
                  </van-list>
        </van-col>
      </van-col>
    </van-row>
    <van-button color="linear-gradient(to top, #c363de,#d848b3, #ed95c7)" round class="guild_add" @click="add_join_guild(guild.id)" v-if="is_join_show">
      <van-icon name="plus" v-if="is_join != 1"/>
      <span class="guild_ranking_cell_add_name">{{ is_join === 1 ? '申请退出家族' : '申请加入家族'}} </span>
    </van-button>
  </div>
</template>
<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import {Dialog, Toast} from 'vant'

  export default {
    data:function() {
      return {
        guild: {},
        join_list: [],
        join_number: undefined,
        voice_number: undefined,
        list: [],
        loading: false,
        finished: false,
        page: 1,
        is_join: 1,
        join_status: 0,
        is_join_show: 0,
        uid: undefined
      }
    },
    created: function() {
      this.uid = this.$route.query.uid
      this.get_guild()
      this.get_list()
    },
    methods: {
      onLoad() {
        // 异步更新数据
       if(this.page > 1) {
         this.get_list()
       }
      },
      onClickLeft() {
        // 拦截关闭h5
        window.location.href="live://live?action=back"
      },
      private_letter(id) {
        // 拦截跳转私信页面
        window.location.href="live://live?action=private_letter&id=" + id
      },
      make_friends(id) {
        // 拦截跳转直播间页面
        window.location.href="live://live?action=room&voice_id=" + id
      },
      JumpUserinfo(id) {
        // 拦截跳转用户主页面
        window.location.href="live://live?action=Userinfo&id=" + id
      },
      get_guild() {
        const uid = this.$route.query.uid
        const token = this.$route.query.token
        const guild_id = this.$route.query.guild_id
        axios.get(this.baseUrlApi+"/vue/guild_api/index",{ params:{ uid: uid, token: token, guild_id: guild_id }}).then(res =>{
          if(res.data.code==1){
            this.guild = res.data.data.guild
            this.join_list = res.data.data.join_list
            this.join_number = res.data.data.join_number
            this.voice_number = res.data.data.voice_number
            this.join_status = res.data.data.join_status
            this.is_join = res.data.data.is_join
            this.is_join_show = res.data.data.is_join_show
          }else{
            Toast.fail(res.data.msg);
          }
        })
      },
      get_list(){
        this.loading = true
        const uid = this.$route.query.uid
        const token = this.$route.query.token
        const guild_id = this.$route.query.guild_id
        axios.get(this.baseUrlApi+"/vue/guild_api/make_friends_room",{ params:{ uid: uid, token: token, guild_id: guild_id, page: this.page }}).then(res =>{
          if(res.data.code==1){
            if (this.page === 1) {
              this.list = []
            }
            const data = res.data.data
            if(data.length < 10){
              this.finished = true
            }
            for(let i=0; i< data.length; i++) {
              this.list.push(data[i])
            }

            this.page ++
            this.loading = false
            console.log(this.list)
          }else{
            // 加载状态结束
            this.loading = false
            // 数据全部加载完成
            this.finished = true
            Toast.fail(res.data.msg);
          }

        })
      },
      guild_join_quit() {
        const uid = this.$route.query.uid;
        const token = this.$route.query.token;
        const text = this.$t('Tips.Compulsory_withdrawal_center')
        const Whether_quit_guild = this.$t('Tips.Whether_quit_guild')
        const Exit_type = this.$t('Tips.Exit_type')
        Dialog.confirm({
          title: Whether_quit_guild,
          cancelButtonText: this.$t('btn.cancel'),
          confirmButtonText: this.$t('btn.confirm'),
          closeOnClickOverlay: true
        }).then(() => {
          Dialog.confirm({
            title: Exit_type,
            message: text,
            cancelButtonText: this.$t('btn.tuì_huì'),
            confirmButtonText: this.$t('btn.Compulsory_withdrawal')
          }).then(() => {
            axios.get(this.baseUrlApi+"/vue/user_vue_api/quit_guild_join",{ params:{ uid:uid,token:token, is_force: 1 }}).then(res =>{
              Toast.fail(res.data.msg);
              if(res.data.code==1){
                const _this = this;
                _this.info = res.data.data
              }
            })
          }).catch(() => {
            axios.get(this.baseUrlApi+"/vue/user_vue_api/quit_guild_join",{ params:{ uid:uid,token:token, is_force: 0 }}).then(res =>{
              Toast.fail(res.data.msg);
              if(res.data.code==1){
                const _this = this;
                _this.info = res.data.data
              }
            })
          });
        }).catch(() => {
        });
      },
      add_join_guild(id) {
        const uid = this.$route.query.uid
        const token = this.$route.query.token

        if (this.is_join === 1) {
          const text = this.$t('Tips.Compulsory_withdrawal_center')
          const Whether_quit_guild = this.$t('Tips.Whether_quit_guild')
          Dialog.confirm({
            title: Whether_quit_guild,
            cancelButtonText: this.$t('btn.cancel'),
            confirmButtonText: this.$t('btn.confirm'),
            closeOnClickOverlay: true
          }).then(() => {
            if (this.join_status === 1) {
              const Exit_type = this.$t('Tips.Exit_type')
              Dialog.confirm({
                title: Exit_type,
                message: text,
                cancelButtonText: this.$t('btn.tuì_huì'),
                confirmButtonText: this.$t('btn.Compulsory_withdrawal')
              }).then(() => {
                axios.get(this.baseUrlApi+"/vue/user_vue_api/quit_guild_join",{ params:{ uid:uid,token:token, is_force: 1 }}).then(res =>{
                  Toast.fail(res.data.msg);
                  if(res.data.code==1){
                    this.page = 1
                    this.finished = false
                    this.get_guild()
                    this.get_list()
                  }
                })
              }).catch(() => {
                axios.get(this.baseUrlApi+"/vue/user_vue_api/quit_guild_join",{ params:{ uid:uid,token:token, is_force: 0 }}).then(res =>{
                  Toast.fail(res.data.msg);
                  if(res.data.code==1){
                    this.page = 1
                    this.finished = false
                    this.get_guild()
                    this.get_list()
                  }
                })
              });
            } else {
              axios.get(this.baseUrlApi+"/vue/user_vue_api/quit_guild_join",{ params:{ uid:uid,token:token, is_force: 0 }}).then(res =>{
                Toast.fail(res.data.msg);
                if(res.data.code==1){
                  this.page = 1
                  this.finished = false
                  this.get_guild()
                  this.get_list()
                }
              })
            }
          }).catch(() => {
            // on cancel
          });
        }else{
          Dialog.confirm({
            title: "申请加入家族",
            message: "是否加入本家族？",
            cancelButtonText: this.$t('btn.cancel'),
            confirmButtonText: this.$t('btn.confirm')
          }).then(() => {

            axios.get(this.baseUrlApi+"/vue/guild_api/add_join_guild",{ params:{ uid:uid,token:token, guild_id: id }}).then(res =>{
              Toast.fail(res.data.msg);
              if(res.data.code==1){
                this.page = 1
                this.finished = false
                this.get_guild()
                this.get_list()
              }
            })

          }).catch(() => {
            // on cancel
          });
        }
      }
    }
  }

</script>

<style>
</style>



