<template>
  <div>
    <div class="wallet-top">
      <div class="wallet-top-l">
        <div class="wallet-top-n">{{currency_name}}{{ $t('column.balance') }}：</div>
        <div class="wallet-top-t">{{info.coin}}</div>
      </div>
      <div class="wallet-top-r">
        <div class="wallet-top-n">{{friend_name}}{{ $t('column.balance') }}：</div>
        <div class="wallet-top-t">{{info.friend_coin}}</div>
      </div>
    </div>
    <div class="wallet-list">
      <div class="wallet-list-a">
        <div class="wallet-list-a-b" @click="href_log('top_up')">
          <div class="wallet-list-a-b-l">{{ $t('column.Recharge') }}</div>
          <div class="wallet-list-a-b-r">
            <img src="../assets/img/9.png" alt="">
          </div>
        </div>
      </div>
      <div class="wallet-list-a">
        <div class="wallet-list-a-b">
          <div class="wallet-list-a-b-l">{{ $t('column.Withdrawal') }}</div>
          <div class="wallet-list-a-b-r">
            <img src="../assets/img/9.png" alt="">
          </div>
        </div>
      </div>
      <div class="wallet-list-a">
        <div class="wallet-list-a-b">
          <div class="wallet-list-a-b-l">{{ $t('column.My_earnings') }}</div>
          <div class="wallet-list-a-b-r">
            ¥{{info.income}}
            <img src="../assets/img/9.png" alt="">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        list:[],
        info:[],
        privilege:[],
        id:0,
        coin:0,
        currency_name:'',
        friend_name:'',
      }
    },
    created: function() {
      //this.convert();
      this.get_list();
      document.title = this.$t('title.wallet')
    },
    methods: {
      get_list(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/api/wallet_vue_api/get_info",{ params:{ uid:uid,token:token }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             //_this.list = res.data.data.noble_list
             _this.info = res.data.data.user_info
             //_this.privilege = res.data.data.privilege
             //_this.id =  _this.info.id
             _this.currency_name =  res.data.data.currency_name
             _this.friend_name = res.data.data.friend_name
             //console.log(_this.list)
             //this.bank_name = list[0]['name']
           }else{
             Toast.fail(res.data.msg);
           }

        })
      },
      href_log(data){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //window.location.href = data;
        this.$router.push({path:'/'+data+'',query:{uid:uid, token: token }});
      }
    }
  }

</script>

<style>
</style>



