<template>
  <div id="guild_description_choose">
    <van-nav-bar
      class="nav-pad-top"
      left-arrow
      fixed
      safe-area-inset-top
      @click-left="onClickLeft"
    >
      <template #left>
        <van-icon name="arrow-left" size="18" color="#1A1A1A"/>
      </template>
      <template #title>
        <span class="guild_description_choose_color">{{ $t('guild.Agency_application') }}</span>
      </template>
    </van-nav-bar>
    <van-row>
      <van-col span="24" class="nav-pad-top"></van-col>
      <van-col span="22" offset="1" class="guild_description_choose_search">
        <van-search
          v-model="SearchValue"
          show-action
          shape="round"
          :placeholder="$t('guild.search_placeholder')"
          @search="onSearch"
        >
          <template #action>
            <van-button color="linear-gradient(to right, #F86C2F, #CA26ED)" round class="guild_description_choose_search_btn" @click="onSearch">{{ $t('guild.search') }}</van-button>
          </template>
        </van-search>
      </van-col>
      <van-col span="22" offset="1" class="guild_description_choose_list">
        <van-list
          v-model="loading"
          :finished="finished"
          :finished-text="$t('Tips.no_more')"
          :loading-text="$t('Tips.loading')"
          @load="onLoad"
        >
          <van-cell v-for="(item, index) in list" :key="index" >
            <van-col span="5">
              <van-image
                width="7vh"
                height="7vh"
                fit="cover"
                class="guild_description_choose_avatar"
                :src="item.logo"
              />
            </van-col>
            <van-col span="14">
              <van-col span="24" class="guild_description_choose_name">{{ item.name }}</van-col>
              <van-col span="24" class="guild_description_choose_center">
                <span>ID: {{ item.id }}</span>
                <span><van-icon name="friends-o" size="1rem"/> {{ item.guild_join_sum }}</span>
              </van-col>
            </van-col>
            <van-col span="5" class="guild_description_choose_add">
              <van-button color="linear-gradient(to right, #F86C2F, #CA26ED)" round  @click="Jump(item.id)">{{ $t('guild.join') }}</van-button>
            </van-col>
          </van-cell>
        </van-list>
      </van-col>

    </van-row>
  </div>

</template>
<script>
import '@/assets/css/style_a.css'
import axios from 'axios'
import { Toast } from 'vant'
export default {
  data:function() {
    return {
      SearchValue: '',
      list: [],
      loading: false,
      finished: false,
      page: 0,
    }
  },
  created: function() {
    this.get_list();
    document.title = this.$t('guild.Agency_application')
  },
  methods: {
    onLoad() {
      if (this.page != 0) {
        this.get_list()
      }
    },
    onClickLeft() {
      // 返回上一页
      const token = this.$route.query.token
      const uid = this.$route.query.uid
      this.$router.push({path:'/GuildDescription',query:{ uid: uid, token: token }})
    },
    Jump(id){
      const token = this.$route.query.token
      const uid = this.$route.query.uid
      this.$router.push({path:'/GuildDescriptionApply',query:{ uid: uid, token: token, guild_id: id }})
    },
    onSearch() {
      this.page = 0
      this.get_list()
    },
    get_list(){
      const token = this.$route.query.token
      const uid = this.$route.query.uid
      const loadings = Toast.loading({
        message: this.$t('Tips.loading'),
        forbidClick: true,
        duration: 0, // 持续展示 toast
      });
      axios.get(this.baseUrlApi+"/vue/guild_api/get_guild_list",{ params:{ uid: uid, token: token, search: this.SearchValue, page: this.page }}).then(res =>{
        loadings.clear()
        if(res.data.code==1){
          const list = res.data.data
          if (this.page == 0) {
            this.list = []
          }
          for (let i = 0; i < list.length; i++) {
            this.list.push(list[i])
          }
          // 加载状态结束
          this.loading = false
          // 数据全部加载完成
          if (list.length < 20) {
            this.finished = true
          }
          this.page ++
        }else{
          Toast.fail(res.data.msg);
        }
      })
    }
  }
}

</script>

<style>

</style>



