<template>
  <div class="invite-info-box">
    <div class="invite-info-box-b">
      <div class="rich">
        <div style="text-align: center;margin-top:1.5rem" v-if="rank_list==''"> {{ $t('column.No_invitation') }} </div>
        <div class="invite-list-box" v-for="(val,k) in rank_list" :key="val.id">
          <div class="invite-list-box-a">{{k+1}}</div>
          <div class="invite-list-box-b">
            <img :src="val.avatar" alt="">
            <div class="invite-list-box-b-t">{{val.user_nickname}}</div>
          </div>
          <div class="invite-list-box-c"><img src="../assets/img/diamond.png" class="invite-redbag-coin-img"/>  {{val.total_income}}</div>
        </div>
      </div>
      <!-- {{post_content}} -->
    </div>
  </div>


</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant';
  export default {
    data() {
      return {
        post_content:'',
        //min_height:document.clientHeight,
        rank_list:[]
      }
    },
    created: function() {
      this.convert()
      document.title = this.$t('title.My_invitation')
    },
    methods: {
      convert: function() {
        //  var id = this.$route.query.id;
        /* var id = 27;
        axios.get(this.baseUrlApi+"mapi/public/index.php/api/user_vue_api/get_help_info",{ params:{ id:id }}).then(res =>{
          //console.log(res);
          this.post_content = res.data.data.info.post_content;
          document.title = res.data.data.info.post_title

        }) */
        this.get_list()
      },
      get_list(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        const loadings = Toast.loading({
          message: this.$t('Tips.loading'),
          forbidClick: true,
          duration: 0, // 持续展示 toast
        });
        axios.get(this.baseUrlApi+"/vue/user_vue_api/request_my_invite",{ params:{ uid:uid,token:token }}).then(res =>{
           console.log(res.data)
           if(res.data.code==1){
             var _this = this;

             //_this.invite_info = res.data.data.invite_info
             _this.rank_list = res.data.data
             //_this.type_list = res.data.data.type_list
             loadings.clear()
           }else{
             loadings.clear()
             Toast.fail(res.data.msg);
           }
           loadings.clear()
        })

      },

    }
  }
</script>

<style>
.invite-redbag-coin-img{width: 1rem;}
</style>



