<template>
  <div id="app">
    <div class="explore_planet_close" @click="close"></div>
    <div class="explore_planet"  :style="{backgroundImage: 'url(' + explore_planet_bg + ')', backgroundSize:'100% 100%', backgroundRepeat: 'no-repeat'}">

      <van-row>
        <!--  顶部      -->
        <van-col span="24" class="explore_planet_title_top">
          <van-col span="4">
            <van-image
              width="90%"
              fit="contain"
              :src="rank"
              @click="rankClick"
              v-if="is_ranking"
            />
          </van-col>
          <van-col span="14" offset="1">
            <!--     滚动    -->
            <van-notice-bar class="notice_bar" color="#d2e8f5" :scrollable="false">
              <van-swipe
                vertical
                class="notice-swipe"
                :autoplay="3000"
                :show-indicators="false"
              >
                <van-swipe-item v-for="t in game_explore_log" :key ="t.id">
                  {{ t.text }}
                </van-swipe-item>
              </van-swipe>
            </van-notice-bar>
          </van-col>
          <van-col span="4" offset="1" class="explore_planet_title_top_left">
            <van-image
              width="90%"
              fit="contain"
              :src="record"
              @click="recordsLogBtn"
            />
          </van-col>
        </van-col>
        <!--    玩法规则    -->
        <van-col span="5">
          <van-image
            width="70%"
            height="3rem"
            fit="contain"
            :src="howplay"
            @click="portalIsShow"
          />
        </van-col>
        <van-col span="12" offset="3" class="explore_planet_number_height">
          <div v-if="is_expedition" class="explore_planet_number_height_time">
            <p class="explore_planet_expedition">{{ $t('column.Exploring') }}</p>
            <p :class="explore_planet_number_5">{{ expedition_time |get_time(5,that) }}</p>
            <p :class="explore_planet_number_6">{{ expedition_time |get_time(6,that) }}</p>
            <p class="explore_planet_expedition">s</p>
          </div>
          <div v-else>
            <p class="magic-number-l explore_planet_number_l"></p>
            <p :class="explore_planet_number_1">{{ Countdown_time |get_time(1,that) }}</p>
            <p :class="explore_planet_number_2">{{ Countdown_time |get_time(2,that) }}</p>
            <p class="magic-number-colon explore_planet_number_colon"></p>
            <p :class="explore_planet_number_3">{{ Countdown_time |get_time(3,that) }}</p>
            <p :class="explore_planet_number_4">{{ Countdown_time |get_time(4,that) }}</p>
            <p class="magic-number-l explore_planet_number_r"></p>
          </div>

        </van-col>

        <van-col span="24">
          <van-col span="2" offset="5" class="explore_planet_warn">
            <van-image
              width="0.9rem"
              :src="explore_planet_warn"
            />
          </van-col>
          <van-col span="10" class="explore_planet_warn_notice">
            <!--     滚动    -->
            <van-notice-bar class="notice_bar_text" color="#d2e8f5" :scrollable="false">
              <van-swipe
                vertical
                class="notice-swipe"
                :autoplay="2900"
                :show-indicators="false"
              >
                <van-swipe-item>{{ $t('column.Choose_dispatch_spacecraft') }}</van-swipe-item>
                <van-swipe-item>{{ $t('column.Explore_multiple_planets') }}</van-swipe-item>
                <van-swipe-item>{{ $t('column.Choose_dispatch_spacecraft') }}</van-swipe-item>
              </van-swipe>
            </van-notice-bar>
          </van-col>
          <van-col span="2" class="explore_planet_warn">
            <van-image
              width="0.9rem"
              :src="explore_planet_warn"
            />
          </van-col>
        </van-col>
        <van-col span="24" class="explore_planet_list">
          <!--    土星      -->
          <div class="explore_planet_saturn">
            <div class="explore_planet_venus_img" @click="explore_planet_bay_type(1,require('../assets/img/media/Saturn_icon.png'))" v-if="game_explore_list[1]">
              <img src="../assets/img/media/txsy.png"/>
              <div class="explore_planet_saturn_earnings">{{ game_explore_list[1]['multiple'] }}</div>
              <div class="explore_planet_saturn_add explore_planet_saturn_add_img">  <span class="explore_planet_saturn_add_1"> {{ game_explore_list[1]['waiting_shelves'] }}{{ $t('column.rack') }} </span><span class="explore_planet_saturn_add_2"> + </span></div>
              <div class="m-planet-saturn"> {{ game_explore_list[1]['departures_sum'] }} {{ $t('column.rack') }}</div>
            </div>
          </div>
          <!--    金星      -->
          <div class="explore_planet_venus">
            <div class="explore_planet_venus_img" @click="explore_planet_bay_type(2,require('../assets/img/media/Venus_icon.png'))" v-if="game_explore_list[2]">
              <img src="../assets/img/media/jxsy.png"/>
              <div class="explore_planet_saturn_earnings">{{ game_explore_list[2]['multiple'] }}</div>
              <div class="explore_planet_saturn_add explore_planet_saturn_add_img">  <span class="explore_planet_saturn_add_1">{{ game_explore_list[2]['waiting_shelves'] }}{{ $t('column.rack') }} </span><span class="explore_planet_saturn_add_2"> + </span></div>
              <div class="m-planet-venus"> {{ game_explore_list[2]['departures_sum'] }} {{ $t('column.rack') }}</div>
            </div>
          </div>
          <!--    火星      -->
          <div class="explore_planet_mars">
            <div class="explore_planet_venus_img" @click="explore_planet_bay_type(3,require('../assets/img/media/Mars_icon.png'))" v-if="game_explore_list[3]">
              <img src="../assets/img/media/hxsy.png"/>
              <div class="explore_planet_saturn_earnings">{{ game_explore_list[3]['multiple'] }}</div>
              <div class="explore_planet_saturn_add explore_planet_saturn_add_img"> <span class="explore_planet_saturn_add_1">{{ game_explore_list[3]['waiting_shelves'] }}{{ $t('column.rack') }} </span><span class="explore_planet_saturn_add_2"> + </span></div>
              <div class="m-planet-mars"> {{ game_explore_list[3]['departures_sum'] }} {{ $t('column.rack') }}</div>
            </div>
          </div>
          <!--    天王星      -->
          <div class="explore_planet_uranus">
            <div class="explore_planet_venus_img" @click="explore_planet_bay_type(4,require('../assets/img/media/Uranus_icon.png'))" v-if="game_explore_list[4]">
              <img src="../assets/img/media/twxsy.png"/>
              <div class="explore_planet_saturn_earnings">{{ game_explore_list[4]['multiple'] }}</div>
              <div class="explore_planet_saturn_add explore_planet_saturn_add_img"> <span class="explore_planet_saturn_add_1">{{ game_explore_list[4]['waiting_shelves'] }}{{ $t('column.rack') }} </span><span class="explore_planet_saturn_add_2"> + </span></div>
              <div class="m-planet-uranus"> {{ game_explore_list[4]['departures_sum'] }} {{ $t('column.rack') }}</div>
            </div>
          </div>
          <!--    木星      -->
          <div class="explore_planet_jupiter">
            <div class="explore_planet_venus_img" @click="explore_planet_bay_type(5,require('../assets/img/media/Jupiter_icon.png'))" v-if="game_explore_list[5]">
              <img src="../assets/img/media/Jupiter_icon.png"/>
              <div class="explore_planet_saturn_earnings">{{ game_explore_list[5]['multiple'] }}</div>
              <div class="explore_planet_saturn_add explore_planet_saturn_add_img"> <span class="explore_planet_saturn_add_1">{{ game_explore_list[5]['waiting_shelves'] }}{{ $t('column.rack') }} </span><span class="explore_planet_saturn_add_2"> + </span></div>
              <div class="m-planet-jupiter"> {{ game_explore_list[5]['departures_sum'] }} {{ $t('column.rack') }}</div>
            </div>
          </div>
          <!--    海王星      -->
          <div class="explore_planet_neptune">
            <div class="explore_planet_venus_img" @click="explore_planet_bay_type(6,require('../assets/img/media/Neptune_icon.png'))" v-if="game_explore_list[6]">
              <img src="../assets/img/media/hwxsy.png"/>
              <div class="explore_planet_saturn_earnings">{{ game_explore_list[6]['multiple'] }}</div>
              <div class="explore_planet_saturn_add explore_planet_saturn_add_img"> <span class="explore_planet_saturn_add_1">{{ game_explore_list[6]['waiting_shelves'] }}{{ $t('column.rack') }} </span><span class="explore_planet_saturn_add_2"> + </span></div>
              <div class="m-planet-neptune"> {{ game_explore_list[6]['departures_sum'] }} {{ $t('column.rack') }}</div>
            </div>
          </div>
          <!--    水星      -->
          <div class="explore_planet_sxsy">
            <div class="explore_planet_venus_img" @click="explore_planet_bay_type(7,require('../assets/img/media/Mercury_icon.png'))" v-if="game_explore_list[7]">
              <img src="../assets/img/media/sxsy.png"/>
              <div class="explore_planet_saturn_earnings">{{ game_explore_list[7]['multiple'] }}</div>
              <div class="explore_planet_saturn_add explore_planet_saturn_add_img"> <span class="explore_planet_saturn_add_1">{{ game_explore_list[7]['waiting_shelves'] }}{{ $t('column.rack') }} </span><span class="explore_planet_saturn_add_2"> + </span></div>
              <div class="m-planet-sxsy"> {{ game_explore_list[7]['departures_sum'] }} {{ $t('column.rack') }}</div>
            </div>
          </div>
          <!--    开普勒星      -->
          <div class="explore_planet_kepler">
            <div class="explore_planet_venus_img" @click="explore_planet_bay_type(8,require('../assets/img/media/Kepler_icon.png'))" v-if="game_explore_list[8]">
              <img src="../assets/img/media/kpl.png"/>
              <div class="explore_planet_saturn_earnings">{{ game_explore_list[8]['multiple'] }}</div>
              <div class="explore_planet_saturn_add explore_planet_saturn_add_img"> <span class="explore_planet_saturn_add_1">{{ game_explore_list[8]['waiting_shelves'] }}{{ $t('column.rack') }} </span><span class="explore_planet_saturn_add_2"> + </span></div>
              <div class="m-planet-kepler"> {{ game_explore_list[8]['departures_sum'] }} {{ $t('column.rack') }}</div>
            </div>
          </div>
        </van-col>

        <div class="explore_planet_bottom" v-if="is_expedition === false">
          <van-col span="16" class="explore_planet_bottom_left">
            <img src="../assets/img/media/left.png" class="explore_planet_bottom_left_1"/>
            <div class="explore_planet_bottom_left_tank"> {{ explore_supply_tank }}</div>
            <img src="../assets/img/media/get.png" class="explore_planet_bottom_left_2" @click="explore_supply_tank_btn"/>
          </van-col>
          <van-col span="8" class="explore_planet_bottom_right" @click="send_airship">
            <img src="../assets/img/media/pq.png" class="explore_planet_bottom_left_3" v-if="count_number"/>
            <img src="../assets/img/media/pq_disable.png" class="explore_planet_bottom_left_3" v-else/>
            <div class="explore_planet_count_number"> {{ count_number }}</div>
          </van-col>
        </div>
        <img src="../assets/img/media/fc.png" class="explore_planet_fc" v-if="is_expedition"/>

        <!--  中奖提示    -->
        <van-popup v-model="planetTipShow" :close-on-click-overlay="false" position="bottom" round :class="planet_tip_one.is_status == 1 ? 'explore_planet_tip' : planet_tip_one.is_status == 2 ? 'explore_planet_tip_fail' :'explore_planet_tip_success'">
          <van-col span="24" :class="planet_tip_one.is_status == 1 ? 'explore_planet_tip_title11' : planet_tip_one.is_status == 2 ? 'explore_planet_tip_title12' :'explore_planet_tip_title1'">  {{ planet_tip_one.winning_tip }}  </van-col>
          <van-col span="24" class="explore_planet_tip_title2">   {{ $t('column.This_round') }} <span>{{ planet_tip_one.user_count}}</span> {{ $t('column.Successful_human_exploration') }}  </van-col>
          <van-col span="24" class="explore_planet_tip_title3">
            <img :src="planet_tip_type"/>
            <div class="planet-result" v-if="planet_tip_one.is_status == 3"><span>{{ planet_tip_one.quantity }}</span></div>
          </van-col>
          <van-col span="24" :class="planet_tip_one.is_status == 3 ?'explore_planet_tip_bottom' : 'immediate_participation_top explore_planet_tip_bottom'">
            <van-col span="24" class="explore_planet_tip_title4" v-if="planet_tip_one.is_status == 3">{{ $t('column.You_will_receive') }}</van-col>
            <van-col span="18" offset="3" class="explore_planet_tip_title5" v-if="planet_tip_one.is_status == 3">{{ planet_tip_one.text }}</van-col>

            <van-col span="14" offset="5" class="immediate_participation2" v-if="participationTime"> {{ $t('column.next_round_is_about_begin') }} {{ participationTime }} s  </van-col>
            <van-col span="14" offset="5" class="immediate_participation" @click="immediate_participation_btn" v-else> </van-col>
            <van-col span="20" class="explore_planet_tip_bag" offset="2" v-if="planet_tip_one.is_status == 3">{{ $t('column.Distribute_prizes_to_backpacks') }}</van-col>
          </van-col>

        </van-popup>

        <!--  购买补给箱    -->
        <van-popup v-model="ExploreSupplyTankShow" position="bottom" class="explore_planet_supply_tankShow">
          <van-col span="4" class="explore_planet_supply_tankShow_img" offset="1">
            <img src="../assets/img/media/xjbgx.png"/>
          </van-col>
          <van-col span="18" class="explore_planet_supply_tankShow_title" offset="1">
            <van-col span="24" class="explore_planet_supply_tankShow_title1">{{ $t('column.Gift_interstellar_supply_box') }}</van-col>
            <van-col span="24" class="explore_planet_supply_tankShow_title2">{{ $t('column.Gift_supply_box_obtain_spaceship') }}</van-col>
          </van-col>
          <van-col span="4" v-for="(item,i) in game_explore_coin" :key="i" :class=" i === game_explore_coin_checked ?'explore_planet_supply_tankShow_type game_explore_coin_checked' : 'explore_planet_supply_tankShow_type'" :offset="i === 0 ? 2 : 0" @click="game_explore_coin_checked_btn(i)">
            {{ item.sum }} {{ $t('column.individual') }}
          </van-col>
          <van-col span="20" class="explore_planet_supply_tankShow_text" offset="2">
            {{ game_explore_coin_text }} {{ currency_name }}
          </van-col>
          <van-col span="24" class="explore_planet_supply_tankShow_bg">
            <van-col span="10" offset="2" class="explore_planet_supply_tankShow_bg1"> {{ $t('column.balance') }}： {{ coin }}</van-col>
            <van-col span="10" class="explore_planet_supply_tankShow_bg2"> <van-button color="#4697c9" round @click="pay_supply_coin">{{ $t('btn.purchase') }}</van-button></van-col>
          </van-col>
        </van-popup>
        <!--  选择起飞的星球飞船数量    -->
        <van-popup v-model="NumberDeparturesShow" position="bottom" class="explore_planet_number_departures">
          <van-col span="20" offset="2" class="explore_planet_supply_tankShow_btn">
            <img src="../assets/img/media/planet_confirmation.png" @click="NumberDeparturesShowBtn"/>
          </van-col>
          <van-col span="7" class="explore_planet_supply_multipleShow_img">
            <img :src="NumberDeparturesUrl"/>
            <div class="number_departures_multiple">{{ NumberDeparturesMultiple }}</div>
          </van-col>
          <van-col span="14" class="explore_planet_supply_tankShow_number">
            <van-col span="24" class="explore_planet_supply_tankShow_number_title">{{ $t('column.Select_number_dispatched_aircraft') }}</van-col>
            <van-col span="24">
              <van-col span="6" class="explore_planet_supply_tankShow_number1">
                <img src="data:image/png;base64," @click="ships_number_save(1)"/>
              </van-col>
              <van-col span="12" class="explore_planet_supply_tankShow_number3">{{ ships_number}}</van-col>
              <van-col span="6" class="explore_planet_supply_tankShow_number2">
                <img src="data:image/png;base64,"  @click="ships_number_save(2)"/>
              </van-col>
            </van-col>
          </van-col>
          <van-col span="20" class="explore_planet_supply_tankShow_multiple" offset="2">{{ $t('column.Input_multiple') }}：</van-col>
          <van-col span="22" class="explore_planet_supply_tankShow_multiple_type" offset="1">
            <van-col span="8" v-for="(m,j) in tankShow_multiple_list" :key="m.id" class="explore_planet_supply_tankShow_multiple_type_number">
              <van-button :color="tankShow_multiple_list_checked == j ? '#E0644A' : '#3F7A8C'" round @click="tankShow_multiple_list_btn(j)">{{ m.title }}</van-button>
            </van-col>

          </van-col>
        </van-popup>
        <!--  玩法规则    -->
        <van-popup v-model="portalShow" position="bottom" round class="portal_show_bg">
          <van-col span="24">
            <van-col span="4" class="portal_show_bg_icon" @click="portal_show_bg_btn"><van-icon name="arrow-left" /></van-col>
            <van-col span="16" class="portal_show_bg_title">{{portal.title}}</van-col>
          </van-col>
          <van-col span="24">
            <div v-html="portal.content" class="portal_show_bg_content">{{portal.content}}</div>
          </van-col>

        </van-popup>
        <!--  记录     -->
        <van-popup v-model="RecordsShow" position="bottom" round class="explore_planet_records_bg">
          <van-col span="4" class="explore_planet_records_bg_icon" @click="portal_show_bg_btn"><van-icon name="arrow-left" /></van-col>
          <van-col span="24">
            <van-col span="8" offset="8">
              <van-col span="12" :class="RecordsShowChecked != 1 ? 'explore_planet_records_type explore_planet_records_type_color' : 'explore_planet_records_type'" @click="RecordsShowType(0)">{{ $t('column.Dispatch_record') }}</van-col>
              <van-col span="12" :class="RecordsShowChecked == 1 ? 'explore_planet_records_type explore_planet_records_type_color' : 'explore_planet_records_type'" @click="RecordsShowType(1)">{{ $t('column.Dispatch_log') }}</van-col>
            </van-col>
          </van-col>
          <van-col span="20" offset="2" class="explore_planet_records_log"> {{ RecordsShowChecked == 1 ? $t('column.Only_show_last_rounds') : $t('column.Your_recent_dispatch_records') }}</van-col>
          <van-col span="22" offset="1" class="explore_planet_records_list" v-show="RecordsShowChecked != 1">
              <van-col span="20" offset="2" class="explore_planet_records_list_top" v-for="r in RecordsList" :key="r.id">
                <van-col span="4" class="explore_planet_records_list_left">
                  <img src="../assets/img/media/Saturn_icon2.png" v-if="r.type == 1"/>
                  <img src="../assets/img/media/Venus_result2.png" v-else-if="r.type == 2"/>
                  <img src="../assets/img/media/Mars_icon2.png" v-else-if="r.type == 3"/>
                  <img src="../assets/img/media/Uranus_result2.png" v-else-if="r.type == 4"/>
                  <img src="../assets/img/media/Jupiter_result2.png" v-else-if="r.type == 5"/>
                  <img src="../assets/img/media/Neptune_icon2.png" v-else-if="r.type == 6"/>
                  <img src="../assets/img/media/Mercury_icon2.png" v-else-if="r.type == 7"/>
                  <img src="../assets/img/media/Kepler_icon2.png" v-else/>
                </van-col>
                <van-col span="19" offset="1" class="explore_planet_records_list_right">
                  <van-col span="18" offset="1" class="explore_planet_records_list_right_title">
                    {{ $t('column.Explore_planets') }}: {{ r.title }}
                  </van-col>
                  <van-col span="18" offset="1" class="explore_planet_records_list_right_text" v-if="r.text">
                    {{ r.text }}
                  </van-col>
                  <van-col span="18" offset="1" class="explore_planet_records_list_right_time">
                    {{ r.create_time }}
                  </van-col>
                  <div class="explore_planet_records_list_right_img" v-if="r.win_status == 1">
                    <img :src="success_img"/>
                  </div>
                  <div class="explore_planet_records_list_right_img" v-else-if="r.win_status == 2">
                    <img :src="error_img"/>
                  </div>
                  <div class="explore_planet_records_list_right_no" v-else>
                    {{ $t('column.Not_participating') }}
                  </div>
                </van-col>
              </van-col>
          </van-col>
          <van-col span="22" offset="1" class="explore_planet_records_list" v-show="RecordsShowChecked == 1">
            <van-col span="20" offset="2" class="explore_planet_records_list_top" v-for="r in RecordsListLog" :key="r.id">
              <van-col span="4" class="explore_planet_records_list_left">
                <img src="../assets/img/media/Saturn_icon2.png" v-if="r.type == 1"/>
                <img src="../assets/img/media/Venus_result2.png" v-else-if="r.type == 2"/>
                <img src="../assets/img/media/Mars_icon2.png" v-else-if="r.type == 3"/>
                <img src="../assets/img/media/Uranus_result2.png" v-else-if="r.type == 4"/>
                <img src="../assets/img/media/Jupiter_result2.png" v-else-if="r.type == 5"/>
                <img src="../assets/img/media/Neptune_icon2.png" v-else-if="r.type == 6"/>
                <img src="../assets/img/media/Mercury_icon2.png" v-else-if="r.type == 7"/>
                <img src="../assets/img/media/Kepler_icon2.png" v-else/>
              </van-col>
              <van-col span="19" offset="1" class="explore_planet_records_list_right">
                <van-col span="18" offset="1" class="explore_planet_records_list_right_title">
                  {{ $t('column.Explore_planets') }}: {{ r.title }}
                </van-col>
                <van-col span="18" offset="1" class="explore_planet_records_list_right_text" v-if="r.text">
                  {{ r.text }}
                </van-col>
                <van-col span="18" offset="1" class="explore_planet_records_list_right_time">
                  {{ r.create_time }}
                </van-col>
                <div class="explore_planet_records_list_right_img" v-if="r.win_status == 1">
                  <img :src="success_img"/>
                </div>
                <div class="explore_planet_records_list_right_img" v-else-if="r.win_status == 2">
                  <img :src="error_img"/>
                </div>
                <div class="explore_planet_records_list_right_no" v-else>
                  {{ $t('column.Not_participating') }}
                </div>
              </van-col>
            </van-col>
          </van-col>
        </van-popup>

        <!--  排行    -->
        <van-popup v-model="RankingShow" position="bottom"  class="explore_ranking">
          <van-col span="24">
            <van-col span="4" class="explore_ranking_icon" @click="explore_ranking_close"><van-icon name="arrow-left" /></van-col>
            <van-col span="18">
              <van-tabs :active="RankingActive" type="card"  @click="onClickRankingActive" class="ranking_action_radius">
                <van-tab :title="$t('column.Anchor_List')" name="0" v-if="anchor_chart_switch"></van-tab>
                <van-tab :title="$t('column.User_List')" name="1" v-if="user_list_switch"></van-tab>
              </van-tabs>
            </van-col>
          </van-col>
          <van-col span="24" class="explore_ranking_list">
            <van-col v-for="(t,i) in RankingList" :key="i" span="23" offset="1" class="explore_ranking_pad">
              <van-col span="2" class="explore_ranking_number">{{ i + 1 }}</van-col>
              <van-col span="5" class="explore_ranking_avatar">
                <img :src="t.avatar"/>
              </van-col>
              <van-col span="17" class="explore_ranking_bor">
                <van-col span="10" class="explore_ranking_name">{{ t.user_nickname }}</van-col>
                <van-col span="14" class="explore_ranking_sum" v-if="RankingActive !=1">{{ t.income }}</van-col>
                <van-col span="14" class="explore_ranking_gift" v-else>
                  <div class="explore_ranking_gift_img" v-for="g in t.gift_list" :key="g.id">
                    <img :src="g.gift_img"/>
                    <div class="explore_ranking_gift_sum">{{ g.gift_sum }}</div>
                  </div>
                </van-col>
              </van-col>
            </van-col>

          </van-col>
        </van-popup>
      </van-row>

    </div>
  </div>
</template>
<script>
import axios from 'axios'
import { Toast, Popover, Dialog } from 'vant'

export default {
  components: { Popover },

  data() {
    return {
      explore_planet_bg:  require('../assets/img/media/bg.png'),
      explore_planet_warn:  'data:image/png;base64,',
      rank: require('../assets/img/media/rank.png'),
      record: require('../assets/img/media/record.png'),
      howplay: require('../assets/img/media/howplay.png'),

      Countdown_time:0,
      explore_planet_number_1:['explore_planet_number','magic-number-0'],
      explore_planet_number_2:['explore_planet_number_colon','magic-number-0'],
      explore_planet_number_3:['explore_planet_number','magic-number-0'],
      explore_planet_number_4:['explore_planet_number','magic-number-0'],
      explore_planet_number_5:['explore_planet_expedition_time','magic-number-0'],
      explore_planet_number_6:['explore_planet_expedition_time','magic-number-0'],
      planet_tip_1:require('../assets/img/media/Saturn_result.png'),
      planet_tip_2:require('../assets/img/media/Venus_result.png'),
      planet_tip_3:require('../assets/img/media/Mars_result.png'),
      planet_tip_4:require('../assets/img/media/Uranus_result.png'),
      planet_tip_5:require('../assets/img/media/Jupiter_result.png'),
      planet_tip_6:require('../assets/img/media/Neptune_result.png'),
      planet_tip_7:require('../assets/img/media/Mercury_result.png'),
      planet_tip_8:require('../assets/img/media/Kepler_result.png'),
      success_img: require('../assets/img/media/planet_success_img.png'),
      error_img:require('../assets/img/media/planet_error_img.png'),
      that: this,
      expedition_time: 10,
      is_expedition: false,
      planetTipShow: false,
      ExploreSupplyTankShow: false,
      participationTime: 5,
      coin: 0,
      explore_supply_tank: 0,
      game_explore_coin: [],
      game_explore_list: [],
      game_explore_log: [],
      game_explore_coin_checked: 0,
      game_explore_coin_text: 0,
      currency_name: '',
      NumberDeparturesShow: false,
      NumberDeparturesUrl: '',
      NumberDeparturesMultiple: '',
      tankShow_multiple_list: [
        {id: 1,'title':'1 ' + this.$t('column.rack'),sum: 1},
        {id: 2,'title':'10 '+ this.$t('column.rack'),sum: 10},
        {id: 3,'title':'50 '+ this.$t('column.rack'),sum: 50},
      ],
      tankShow_multiple_list_checked: 0,
      ships_number: 0,
      explore_planet_bay_type_i: 1,
      count_number: 0,
      explore_planet_order: '',
      planet_tip_one: {},
      planet_tip_type: '',
      portal:{},
      portalShow: false,
      RecordsShow: false,
      RankingShow: false,
      RankingList: [],
      RecordsList: [],
      RecordsListLog: [],
      RecordsShowChecked: 1,
      RankingActive:0,
      game_explore_restrict: 0,
      is_ranking: 0,
      anchor_chart_switch: 0,
      number_anchor_charts: 0,
      user_list_switch: 0,
      number_user_list_displays: 0,
      loading: false,
      loading_send_airship: false,
      finished: false,
      page: 1,
    }
  },
  created: function() {
    // document.title = '游戏'
    this.getList()
  },
  filters: {
    get_time(time,type,that) {

      if (type == 1) {
        const i = Math.floor(parseInt(time / 60 % 60)/10)
        that.explore_planet_number_1[1] = 'magic-number-' + i
      } else if (type == 2) {
        const i = parseInt(time / 60 % 60) % 10
        that.explore_planet_number_2[1] = 'magic-number-' + i
      } else if (type == 3) {
        const i = Math.floor(parseInt(time % 60)/10)
        that.explore_planet_number_3[1] = 'magic-number-' + i
      } else if (type == 4) {
        const i = Math.floor(parseInt(time % 60)%10)
        that.explore_planet_number_4[1] = 'magic-number-' + i
      } else if (type == 5) {
        const i = Math.floor(parseInt(time % 60)/10)
        that.explore_planet_number_5[1] = 'magic-number-' + i
      } else {
        const i = Math.floor(parseInt(time % 60)%10)
        that.explore_planet_number_6[1] = 'magic-number-' + i
      }
    }
  },
  mounted() {
    // app调js
    window.game_planet_result = this.game_planet_result

  },
  methods:{

    get_ranking_list(){
      // 获取排行数据
      // 获取探索记录
      const uid = this.$route.query.uid
      const token = this.$route.query.token
      if (this.loading) {
        return false
      }
      this.loading = true
      axios.get(this.baseUrlApi+"/vue/explore_planet_api/rank_list",{
        params:{
          uid: uid,
          token: token,
          type: this.RankingActive
        }
      }).then(res =>{
        this.loading = false
        if(res.data.code === 1){
          this.RankingList = res.data.data
        }
      })
    },
    game_planet_result(data) {
      // if (this.planetTipShow == false && this.expedition_time <= 1) {
      //   let _this = this
      //   setTimeout(() => {
      //     _this.planetTipInfo()
      //   }, 1500);
      // }
    },
    onClickRankingActive(i) {
      this.RankingActive = i
      this.get_ranking_list()
    },
    rankClick() {
      this.RankingActive = this.anchor_chart_switch ? 0 : 1
      this.get_ranking_list()
      this.RankingShow = true
    },
    explore_ranking_close() {
      this.RankingShow = false
    },
    portal_show_bg_btn() {
      this.portalShow = false
      this.RecordsShow = false
    },
    RecordsShowType(i) {
      this.RecordsShowChecked = i
      console.log(i)
    },
    recordsLogBtn() {
      this.RecordsShowChecked = 1
      this.getRecordsList()
      this.getRecordsList2()
      this.RecordsShow = true
    },
    getRecordsList2(){
      // 获取探索记录
      const uid = this.$route.query.uid
      const token = this.$route.query.token
      axios.get(this.baseUrlApi+"/vue/explore_planet_api/records_log",{
        params:{
          uid: uid,
          token: token,
          type: 0
        }
      }).then(res =>{
        if(res.data.code === 1){
          this.RecordsList = res.data.data
        }
      })
    },
    getRecordsList(){
      // 获取探索记录
      const uid = this.$route.query.uid
      const token = this.$route.query.token
      axios.get(this.baseUrlApi+"/vue/explore_planet_api/records_log",{
        params:{
          uid: uid,
          token: token,
          page:this.page,
          type: 1
        }
      }).then(res =>{
        if(res.data.code === 1){
          this.RecordsListLog = res.data.data
          this.finished = true
        }
      })
    },
    portalIsShow() {
      this.portalShow = true
    },
    send_airship() {
      // 发送起飞数量
      const uid = this.$route.query.uid
      const token = this.$route.query.token
      const room_id = this.$route.query.room_id
      if (this.count_number > 0) {
        if (this.loading_send_airship) {
          return false
        }
        this.loading_send_airship = true
        axios.get(this.baseUrlApi+"/vue/explore_planet_api/send_airship",{
          params:{
            uid: uid,
            token: token,
            game_explore_list: JSON.stringify(this.game_explore_list),
            room_id: room_id
          }
        }).then(res =>{
          this.loading_send_airship = false

          Toast(res.data.msg)
          if(res.data.code === 1){
            this.getList()
          }
        })
      }
    },
    NumberDeparturesShowBtn() {

      if (this.ships_number > this.game_explore_restrict) {
        Toast(this.$t('column.Maximum_dispatch_per_planet') + this.game_explore_restrict + this.$t('column.rack'))
        return false
      }
      // 星球发送架数 每个星球最多派遣50架哦
      this.game_explore_list[this.explore_planet_bay_type_i].waiting_shelves = this.ships_number
      let sum = 0
      for (let i = 1; i <= 8; i++) {
        sum += this.game_explore_list[i].waiting_shelves
      }
      this.count_number = sum
      this.NumberDeparturesShow = false
    },
    ships_number_save(type) {
      const numbers = this.tankShow_multiple_list[this.tankShow_multiple_list_checked].sum
      if (type == 1) {
        // 减少
        this.ships_number = this.ships_number - numbers > 0 ? this.ships_number - numbers : 0
      } else {

        if (this.game_explore_restrict >= this.ships_number + numbers + this.game_explore_list[this.explore_planet_bay_type_i]['departures_sum']){
          // 增加
          this.ships_number = this.ships_number + numbers
        }
      }
    },
    explore_planet_bay_type(i,url) {
      // 选择星球
      this.ships_number = this.game_explore_list[i].waiting_shelves
      this.tankShow_multiple_list_checked = 0
      this.NumberDeparturesMultiple = this.game_explore_list[i].multiple
      this.explore_planet_bay_type_i = i
      this.NumberDeparturesUrl = url
      this.NumberDeparturesShow = true
    },
    tankShow_multiple_list_btn(j){
   //   this.ships_number = 0
      this.tankShow_multiple_list_checked = j
    },
    explore_supply_tank_btn() {
      this.game_explore_coin_text = this.game_explore_coin[0].coin
      this.ExploreSupplyTankShow = true
    },
    game_explore_coin_checked_btn(i){
      this.game_explore_coin_checked = i
      this.game_explore_coin_text = this.game_explore_coin[i].coin
    },
    // 购买补给箱
    pay_supply_coin(){
      const uid = this.$route.query.uid
      const token = this.$route.query.token
      const id = this.game_explore_coin[this.game_explore_coin_checked].id
      const room_id = this.$route.query.room_id
      if (this.loading) {
        return false
      }
      this.loading = true
      axios.get(this.baseUrlApi+"/vue/explore_planet_api/pay_supply_coin",{
        params:{
          uid: uid,
          token: token,
          id: id,
          room_id: room_id
        }
      }).then(res =>{
        this.loading = false
        Toast(res.data.msg)
        if(res.data.code === 1){
          this.coin = res.data.data.coin
          this.explore_supply_tank = res.data.data.explore_supply_tank
          this.ExploreSupplyTankShow = false
        }else{
          if(res.data.code === 10002){
            // 余额不足处理
          }
        }
      })
    },
    //开始倒计时
    timeFn() {
      this.is_pause = true
      this.timer = setInterval(() => {
        if (this.Countdown_time > 1) {
          this.Countdown_time--;
        } else {
          clearInterval(this.timer);
          // 倒计时结束后需要执行的操作
          this.is_pause = false
          this.expeditionFn()
        }
      }, 1000);
    },
    //开始倒计时
    expeditionFn() {
      this.is_expedition = true
      this.is_pause = true
      this.expedition_timer = setInterval(() => {
        if (this.expedition_time > 6) {
          this.expedition_time--;
        } else {
          clearInterval(this.expedition_timer);
          // 倒计时结束后需要执行的操作
          this.is_pause = false
          this.planetTipInfo()
          this.is_expedition = false
        }
      }, 1000);
    },
    planetTipInfo(){
      // 查询胜利结果
      const uid = this.$route.query.uid
      const token = this.$route.query.token
      if (this.loading) {
        return false
      }
      this.loading = true
      axios.get(this.baseUrlApi+"/vue/explore_planet_api/planet_tip_info",{
        params:{
          uid: uid,
          token: token
        }
      }).then(res =>{
        this.loading = false

        if (res.data.data.time < 10) {
          this.participationTime =  res.data.data.time
          this.participationFn()
          if(res.data.code === 1){
            this.planet_tip_one = res.data.data

            switch(this.planet_tip_one.type) {
              case 1:
                this.planet_tip_type = this.planet_tip_1
                break;
              case 2:
                this.planet_tip_type = this.planet_tip_2
                break;
              case 3:
                this.planet_tip_type = this.planet_tip_3
                break;
              case 4:
                this.planet_tip_type = this.planet_tip_4
                break;
              case 5:
                this.planet_tip_type = this.planet_tip_5
                break;
              case 6:
                this.planet_tip_type = this.planet_tip_6
                break;
              case 7:
                this.planet_tip_type = this.planet_tip_7
                break;
              default:
                this.planet_tip_type = this.planet_tip_8
            }
            this.planetTipShow = true
          }else{
            this.participationTime =  res.data.data.time
            this.planetTipShow = true
          }
        }else{
          this.planetTipShow = false
          this.getList()
        }

      })
    },
    //下一轮即将开始--倒计时
    participationFn() {

        this.is_pause2 = true
        this.participation_timer = setInterval(() => {
          if (this.participationTime > 0) {
            this.participationTime--;
          } else {
            clearInterval(this.participation_timer);
            // 倒计时结束后需要执行的操作
            this.is_pause2 = false
          }
        }, 1000);
    },
    immediate_participation_btn() {
      this.getList()
      this.planetTipShow = false
    },
    close() {
      // 拦截关闭游戏
      window.location.href="live://live?action=close"
     // window.location.href="live://close_game"
    },
    getList() {
      const uid = this.$route.query.uid
      const token = this.$route.query.token
      if (this.loading) {
        return false
      }
      this.loading = true
      axios.get(this.baseUrlApi+"/vue/explore_planet_api/get_list",{
        params:{
          uid: uid,
          token: token
        }
      }).then(res =>{
        this.loading = false
        if(res.data.code === 1){
          this.coin = res.data.data.coin
          this.explore_supply_tank = res.data.data.explore_supply_tank
          this.game_explore_coin = res.data.data.game_explore_coin
          this.game_explore_list = res.data.data.game_explore_list
          this.Countdown_time = res.data.data.countdown_time
          this.expedition_time = res.data.data.expedition_time
          this.game_explore_log = res.data.data.game_explore_log
          this.currency_name = res.data.data.currency_name
          this.explore_planet_order = res.data.data.explore_planet_order
          this.portal = res.data.data.portal
          this.game_explore_restrict =res.data.data.game_explore_restrict
          this.count_number = 0
          this.is_ranking = res.data.data.is_ranking
          this.anchor_chart_switch = res.data.data.anchor_chart_switch
          this.number_anchor_charts = res.data.data.number_anchor_charts
          this.user_list_switch = res.data.data.user_list_switch
          this.number_user_list_displays = res.data.data.number_user_list_displays

          if (this.Countdown_time > 0) {
            clearInterval(this.timer)
            this.is_pause = false
            this.timeFn()
          }else{
            clearInterval(this.expedition_timer)
            this.is_expedition = true
            this.expeditionFn()
          }

        }else{
          Toast(res.data.msg)
        }
      }).catch(error =>{
        console.log("///////////////")
        console.log(error);
        console.log("aaaaaaaaaaaaaaaaa")
        console.log(error.response);
        if (error.response) {
          console.log("===============")
          // 请求成功发出且服务器也响应了状态码，但状态代码超出了 2xx 的范围
          console.log(error.response.data);
          console.log(error.response.status);
          console.log(error.response.headers);
        } else if (error.request) {
          console.log("qqqqqqqqqqqqqqq")
          // 请求已经成功发起，但没有收到响应
          // `error.request` 在浏览器中是 XMLHttpRequest 的实例，
          // 而在node.js中是 http.ClientRequest 的实例
          console.log(error.request);
        } else {
          console.log("wwwwwwwwwwwwwwwwww")
          // 发送请求时出了点问题

          console.log('Error', error.message);
        }

        // ErrorCode：error.response.status ErrorMsg：error.message
        Toast('ErrorCode:' + error.status + ' ErrorMsg：' + error.message)
      })
    },

    onLoad() {
      // 异步更新数据
      if (this.page != 1) {
       // this.getRecordsList()
      }
    }
  }
}
</script>

<style lang="css">

</style>



