<template>
  <div class="dress">
    <!-- <div class="healp-top dress_banner">

    </div> -->
    <div class="dress-top-banner" v-if="navigation_map">
      <img :src="navigation_map" alt="">
    </div>
    <div class="dress-top">
      <div class="dress-top-box float_l" v-for="val in title" :key="val.id" @click="href(val.id)">
        <div class="dress-tabls-img">
          <img v-if="val.id==1" src="../assets/img/xunzhang@2x.png" alt="">
          <img v-if="val.id==2" src="../assets/img/zhuyetexiao@2x.png" alt="">
          <img v-if="val.id==3" src="../assets/img/touxiangkuang@2x.png" alt="">
          <img v-if="val.id==4" src="../assets/img/liaotianqipao@2x.png" alt="">
          <img v-if="val.id==5" src="../assets/img/liaotianbeijing@2x.png" alt="">
          <img v-if="val.id==7" src="../assets/img/jinchangdonghua@2x.png" alt="">
        </div>
        <div class="dress-tabls-title">{{val.name}}</div>
      </div>
    </div>

    <div class="dress-box">
      <div class="title">
        <p class="title-txt">{{ $t('column.New_clothes_recommendation') }}</p>
      </div>
      <div class="dress-box-bottom">
        <div class="float_l dress-box-bottom-a" v-for="val in list" :key="val.id" @click="href(3)">
          <div class="dress-avatar-k">
            <img :src="val.icon" alt="">
          </div>
          <div class="dress-box-bottom-b">
            <div class="dress-box-bottom-b-t">
              <img class="dress-avatar" src="../assets/img/tutu@2x.png" alt="">
            </div>
            <div class="dress-box-bottom-b-name">{{val.name}}</div>
            <div class="dress-box-bottom-b-text">{{ $t('column.Head_frame') }}</div>
          </div>
        </div>
      </div>

      <div class="title">
        <p class="title-txt">{{ $t('column.Top_recommendations') }}</p>
      </div>
      <div class="dress-box-bottom">
        <div class="float_l dress-box-bottom-a" v-for="val in list" :key="val.id" @click="href(3)">
          <div class="dress-avatar-k">
            <img :src="val.icon" alt="">
          </div>
          <div class="dress-box-bottom-b">
            <div class="dress-box-bottom-b-t">
              <img class="dress-avatar" src="../assets/img/tutu@2x.png" alt="">
            </div>
            <div class="dress-box-bottom-b-name">{{val.name}}</div>
            <div class="dress-box-bottom-b-text">{{ $t('column.Head_frame') }}</div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        list:[],
        type_list:[],
        //privilege:[],
        id:0,
        coin:0,
        currency_name:'',
        friend_name:'',
        navigation_map: '',
        active:0,
        title:[
          {id:1,name:'勋章',img:'/xunzhang@2x.png'},
          {id:2,name:'主页特效',img:'/zhuyetexiao@2x.png'},
          {id:3,name:'头像框',img:'/touxiangkuang@2x.png'},
          {id:4,name:'聊天气泡',img:'/liaotianbeijing@2x.png'},
          {id:5,name:'聊天背景',img:'/liaotianbeijing@2x.png'},
          {id:7,name:'进场特效',img:'/liaotianbeijing@2x.png'},
        ]
      }
    },
    created: function() {
      for(let i= 0; i < this.title.length; i++) {
        if (this.title[i].id === 1 ) {
          this.title[i].name = this.$t('btn.medal')
        }else if(this.title[i].id === 2) {
          this.title[i].name = this.$t('btn.Home_effects')
        }else if(this.title[i].id === 3) {
          this.title[i].name = this.$t('btn.Head_frame')
        }else if(this.title[i].id === 4) {
          this.title[i].name = this.$t('btn.Chat_bubble')
        }else if(this.title[i].id === 5) {
          this.title[i].name = this.$t('btn.Chat_background')
        }else {
          this.title[i].name = this.$t('btn.Mobilization_effect')
        }
      }
      this.get_list();
      document.title = this.$t('title.Dress_up_center')
    },
    methods: {
      get_list(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/vue/dress_api/index",{ params:{ uid:uid,token:token }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             _this.list = res.data.data.list
             _this.navigation_map = res.data.data.navigation_map ? res.data.data.navigation_map : require('../assets/img/banner1@2x.png')
             //_this.type_list = res.data.data.type_list

             //_this.privilege = res.data.data.privilege
             //_this.id =  _this.info.id
             //_this.currency_name =  res.data.data.currency_name
             //_this.friend_name = res.data.data.friend_name
             //console.log(_this.list)
             //this.bank_name = list[0]['name']
           }else{
             Toast.fail(res.data.msg);
           }

        })
      },
      href_log(id){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //window.location.href = data;
        this.$router.push({path:'/help_info',query:{uid:uid, token: token,id:id }});
      },
      href(id){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var path = '';
        if(id==3){
          path = 'avatar_frame';
        }else if(id==4){
          path = 'chat_bubble'
        }else if(id==5){
          path = 'chat_bg'
        }else if(id==2){
          path = 'home_page'
        }else if(id==1){
          path = 'medal'
        }else if(id==7){
          path = 'car'
        }
        //window.location.href = data;
        this.$router.push({path:'/'+path+'',query:{uid:uid, token: token,id:id }});
      },
      get_list_id(id){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/vue/user_vue_api/get_help",{ params:{ uid:uid,token:token,id:id }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             _this.list = res.data.data.list
             //_this.type_list = res.data.data.type_list

             //_this.privilege = res.data.data.privilege
             //_this.id =  _this.info.id
             //_this.currency_name =  res.data.data.currency_name
             //_this.friend_name = res.data.data.friend_name
             //console.log(_this.list)
             //this.bank_name = list[0]['name']
           }else{
             Toast(res.data.msg);
           }

        })
      },
    }
  }

</script>

<style>
  .dress{
    background-color: #F5F7FA;
  }




  .dress-tabls-img{
    margin-top: 1rem;
  }
  .dress-tabls-img img{
    height: 1.5rem;
  }
  .dress-tabls-title{
    line-height: 2rem;
    color: #333333;
    font-size: 0.8rem;
  }

</style>



