<template>
  <div id="voice_details" :style="{backgroundImage: 'url(' + voice.image + ')', backgroundSize:'100% 100%', backgroundRepeat: 'no-repeat'}">
    <van-nav-bar title="房间详情" safe-area-inset-top @click-left="onClickLeft" left-arrow>
    </van-nav-bar>
    <van-row>
      <van-col span="22" offset="1">
        <van-col span="24" class="voice_details_room_bg">
          <van-col span="5" class="voice_details_img">
            <van-image
              width="6vh"
              height="6vh"
              round
              fit="cover"
              :src="voice.voice_avatar"
            />
          </van-col>
          <van-col span="15" class="voice_details_center">
            <van-col span="24" class="voice_details_center_title">{{ voice.title }}</van-col>
            <van-col span="24" class="voice_details_center_id">房间ID：{{ voice.id }}</van-col>

          </van-col>
          <van-col span="4" class="voice_details_center_share">
            <van-icon name="share" @click="JumpShare"/>
          </van-col>
        </van-col>
        <!--  家族      -->
        <van-col span="24" class="voice_details_guild" v-if="guild">所属家族</van-col>
        <van-col span="24" v-if="guild" @click="clickGuild">
          <van-col span="4" class="voice_details_guild_img">
            <van-image
              width="6vh"
              height="6vh"
              fit="cover"
              :src="guild.logo"
            />
          </van-col>
          <van-col span="17" class="voice_details_guild_center">
            <van-col span="24" class="voice_details_guild_title">{{ guild.name }}</van-col>
            <van-col span="24" class="voice_details_guild_id">家族ID：{{ guild.id }}</van-col>
          </van-col>
          <van-col span="3" class="voice_details_guild_icon" ><van-icon name="arrow" /></van-col>
        </van-col>
        <van-col span="24" v-if="guild"><van-divider /></van-col>
        <van-col span="24">
          <van-col span="4" class="voice_details_id_bg">房主：</van-col>
          <van-col span="20"> <span class="voice_details_bg" @click="JumpUserinfo(voice.user_id)">{{ voice.user_nickname }}</span></van-col>
        </van-col>
        <van-col span="24" class="voice_details_id_top" v-if="administrator.length">
          <van-col span="4" class="voice_details_id_bg">管理：</van-col>
          <van-col span="20">
            <span v-for="(item,i) in administrator" :key="i" :class="item.type === 2 ? 'voice_details_bg_host' : 'voice_details_bg_host voice_details_bg_type'" @click="JumpUserinfo(item.user_id)">{{ item.user_nickname }}</span>
          </van-col>
        </van-col>
        <van-col span="24"><van-divider /></van-col>
        <van-col span="24">
          <van-col span="24" class="voice_details_gift">累计送出的礼物</van-col>
          <van-col span="24" class="voice_details_gift_type">
            <van-grid :gutter="10" :border="false">
              <van-grid-item v-for="(item,i) in gift_list" :key="i">
                <van-col span="24" class="voice_details_gift_img">
                  <img :src="item.img"/>
                </van-col>
                <van-col span="24" class="voice_details_gift_name">{{ item.name }}</van-col>
                <van-col span="24" class="voice_details_gift_id">x{{ item.gift_count }}</van-col>
              </van-grid-item>
            </van-grid>
          </van-col>
        </van-col>
      </van-col>
    </van-row>
  </div>
</template>
<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import {Dialog, Toast} from 'vant'

  export default {
    data:function() {
      return {
        voice: {},
        administrator: [],
        guild: {},
        gift_list: [],
        share_voice: '',
      }
    },
    created: function() {
      this.get_voice()
    },
    methods: {
      onClickLeft() {
        // 拦截关闭h5
        window.location.href="live://live?action=back"
      },
      JumpShare() {
        // 拦截跳转分享直播间页面
        window.location.href="live://live?action=voiceShare&JumpUrl=" + encodeURIComponent(this.share_voice)
      },
      clickGuild() {
        // 拦截跳转公会页面
        window.location.href="live://live?action=guild&guild_id=" + this.guild.id
      },
      JumpUserinfo(id) {
        // 拦截跳转用户主页面
        window.location.href="live://live?action=JumpUser&id=" + id
      },
      get_voice() {
        const uid = this.$route.query.uid
        const token = this.$route.query.token
        const voice_id = this.$route.query.voice_id
        axios.get(this.baseUrlApi+"/vue/user_vue_api/get_voice",{ params:{ uid: uid, token: token, voice_id: voice_id }}).then(res =>{
          if(res.data.code==1){
            this.voice = res.data.data.voice
            this.administrator = res.data.data.administrator
            this.guild = res.data.data.guild
            this.gift_list = res.data.data.gift_list
            this.share_voice = res.data.data.share_voice
          }else{
            Toast.fail(res.data.msg);
          }
        })
      }
    }
  }

</script>

<style>
</style>



