<template>
  <div id="">
    <van-list
      v-model="loading"
      :finished="finished"
      :finished-text="$t('Tips.no_more')"
      :loading-text="$t('Tips.loading')"
      @load="onLoad"
    >
      <div class="attention-box" v-for="(val,key) in list">
        <div class="attention-box-a">
          <img :src="val.avatar" alt="">
        </div>
        <div class="attention-box-b">
          <div class="attention-box-b-name">
            <div class="attention-box-b-name-a">{{val.user_nickname}}</div>
            <div v-if="val.sex==1" class="attention-box-b-name-b">♂{{val.age}}</div>
            <div v-if="val.sex==2" class="attention-box-b-name-c">♀{{val.age}}</div>
          </div>
          <div class="attention-box-b-id">ID：{{val.id}}</div>
        </div>
        <div class="attention-box-c">
          <div class="attention-box-c-default" @click="delAtt(key,val.id)">{{ $t('btn.Cancel_follow') }}</div>
        </div>
      </div>
      <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
    </van-list>
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        list:[],
        info:[],
        privilege:[],
        loading:false,
        finished:false,
        page:1,
      }
    },
    created: function() {
      //this.convert();
      //this.get_list();
      document.title = this.$t('title.attention')
    },
    methods: {
      onLoad(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var page = this.page;

        axios.get(this.baseUrlApi+"/vue/user_vue_api/get_attention_vue",{ params:{ uid:uid,token:token,page:page }}).then(res =>{
           //console.log(res.data)
           this.loading = false;
           if(res.data.code==1){
             if(res.data.data==''){
               this.finished = true
             }
             var _this = this;
             var list = res.data.data
             //_this.list = list
             _this.page++;
             for(var i =0;i<list.length;i++){
              _this.list.push(list[i])
             }
           }else{
             this.finished = true
           }
           //console.log(page)

        })
      },
      delAtt(keys,id){
        var list = this.list;
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var page = this.page;

        axios.get(this.baseUrlApi+"/vue/user_vue_api/click_attention_vue",{ params:{ uid:uid,token:token,id:id }}).then(res =>{
           //console.log(res.data)
           //this.loading = false;
           if(res.data.code==1){
             Toast.success(res.data.msg);
             list.splice(keys,1);
           }else{
             Toast.fail(res.data.msg);
           }
           //console.log(page)

        })
      }
    }
  }

</script>

<style>
</style>



