<template>
  <div id="">
    <div class="msg-box">
      <div class="msg-box-a">{{title}}</div>
      <div class="msg-box-content-b">{{centent}}</div>

    </div>
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        title:'',
        centent:'',
        privilege:[],
        loading:false,
        finished:false,
        refreshing: false,
        page:1,
      }
    },
    created: function() {
      //this.convert();
      this.get_list();
      document.title = '系统消息'
    },
    methods: {
      get_list(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var id = this.$route.query.id;
        axios.get(this.baseUrlApi+"/vue/system_message_api/get_system_info",{ params:{ uid:uid,token:token,id:id }}).then(res =>{
           console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             var list = res.data.data
             _this.centent = list.centent
             _this.title = list.title;
             /* for(var i =0;i<list.length;i++){

             } */
             /* var _this = this;
             _this.list = res.data.data.noble_list
             _this.info = res.data.data.noble_info
             _this.privilege = res.data.data.privilege
             _this.id =  _this.info.id
             _this.currency_name =  res.data.data.currency_name
             _this.coin = res.data.data.coin */
             //console.log(_this.list)
             //this.bank_name = list[0]['name']
           }

        })
      },
      href_log(id){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //window.location.href = data;
        this.$router.push({path:'/system_content',query:{uid:uid, token: token,id:id }});
      },

    }
  }

</script>

<style>
  #app,body{
    background-color: #F5F7FA;
  }
  

</style>



