<template>
  <div id="app">
    <div class="tree_close" @click="close"></div>
    <div class="tree_game" :style="{backgroundImage: 'url(' + tree_game_bg + ')', backgroundSize:'100% 100%', backgroundRepeat: 'no-repeat'}">
      <van-row>
        <!--   顶部余额     -->
        <van-col span="22" offset="1">
          <van-col span="15" class="tree_game_balance">
            <span>{{ $t('column.balance') }}：</span>
            {{ coin }}
          </van-col>
          <van-col span="3" class="tree_game_bg" @click="treePlayExplain">
            <van-icon name="question-o" />
      <!--    玩法    -->
            <div>{{ $t('column.Play_description') }}</div>
          </van-col>
          <van-col span="3" class="tree_game_bg" @click="treeKnapsack">
            <van-icon name="bag-o" />
      <!--   背包    -->
            <div>{{ $t('column.knapsack') }}</div>
          </van-col>
          <van-col span="3" class="tree_game_bg" @click="treePlaylogBtn">
            <van-icon name="label-o" />
      <!--    记录  -->
            <div>{{ $t('column.record') }}</div>
          </van-col>
        </van-col>
          <!--    广播滚动    -->
        <van-col span="24">
          <van-notice-bar left-icon="volume-o" color="#ffffff" :scrollable="false" background="#514592">
            <van-swipe
              vertical
              class="tree_game_swipe"
              :autoplay="3000"
              :show-indicators="false"
            >
              <van-swipe-item v-for="(v,i) in notice" :key="i">
                <span v-html="v.text"></span>
              </van-swipe-item>
            </van-swipe>
          </van-notice-bar>
        </van-col>
        <!--  树      -->
        <van-col span="24" class="tree_img">
          <img :src="tree" class="tree_img_width"/>
          <div class="tree_ab">
            <van-col span="4" class="tree_ab_gift">
              <van-col class="tree_ab_gift1">
                <van-col span="20" offset="2" class="tree_ab_gift_img" v-for="(item,i) in gift" :key="i" v-if="i < gift.length / 2" @click="giftShowBtn(item)">
                  <img :src="pao" class="tree_ab_gift_img1"/>
                  <img :src="item.img" style="width:3.5vh;height: 3.5vh;" class="tree_ab_gift_image"/>
                  <div class="tree_ab_gift_name">{{ item.coin }} <img :src="diamond"/></div>
                </van-col>
              </van-col>
            </van-col>
            <van-col span="16" class="tree_ab_center">
              <p class="tree_ab_center_shine position_shine" v-for="t in shine_sum" :key="t">
                <img :src="shine" style="width:5vh;height: 12vh;"/>
              </p>
              <img :src="kettle" class="tree_ab_center_hu" v-if="kettleShow"/>
            </van-col>
            <van-col span="4" class="tree_ab_gift">
              <van-col class="tree_ab_gift1">
                <van-col span="20" offset="2" class="tree_ab_gift_img" v-for="(item,i) in gift" :key="i" v-if="i >= gift.length / 2" @click="giftShowBtn(item)">
                  <img :src="pao" class="tree_ab_gift_img1"/>
                  <img :src="item.img" style="width:3.5vh;height: 3.5vh;" class="tree_ab_gift_image"/>
                  <div class="tree_ab_gift_name">{{ item.coin }} <img :src="diamond"/></div>
                </van-col>
              </van-col>
            </van-col>
          </div>
        </van-col>
        <!--    按钮    -->
        <van-col span="24" class="tree_btn">
          <van-button v-for="(v,i) in consumption" :disabled="isBtn" :key="i"  :color="i === 0 ? 'linear-gradient(to right, #d083e7, #e675f8)': i === 1 ? 'linear-gradient(to right, #9670f7, #b872e2)': 'linear-gradient(to right, #eb82ac, #ae5ce2)'" @click="treeBtn(i)">
            <van-col span="24" class="tree_btn_frequency">{{ v.sum_name }}</van-col>
            <van-col span="24" class="tree_btn_coin">{{ v.coin_name }}</van-col>
          </van-button>
        </van-col>
      </van-row>
      <van-popup v-model="giftShow" class="tree_gift_show">
        <img :src="giftOne.img" style="width: 60%;"/>
        <van-col span="24" class="tree_gift_show_name">{{ giftOne.name }}</van-col>
        <van-col span="24" class="tree_gift_show_coin"> {{ giftOne.coin }}{{ currency_name }}</van-col>
      </van-popup>
<!--  玩法    -->
      <van-popup v-model="playExplainShow" round closeable class="tree_play_explain_show" position="bottom" style="background: #22222e;">
        <van-col span="24" class="tree_play_explain_show_title"> ● {{ $t('column.Play_description') }} ● </van-col>
        <van-col span="22" offset="1" style="font-size: 0.8rem;" class="reward_show_gift"><p v-html="play_explain"/></van-col>
      </van-popup>
      <!--  背包    -->
      <van-popup v-model="treeKnapsackShow" round closeable class="tree_play_explain_show" position="bottom" style="background: #22222e;">
        <van-col span="24" class="tree_play_explain_show_title"> ● {{ $t('column.knapsack') }} ● </van-col>
        <van-col span="24" class="reward_show_gift" v-if="bag.length">
          <van-grid :column-num="4" :gutter="5">
            <van-grid-item v-for="(t,i) in bag" :key="i" >
              <van-col span="24" class="reward_show_gift_img">
                <img :src="pao" style="width:8vh;height: 8vh;"/>
                <img :src="t.img" style="width:4vh;height: 4vh;" class="tree_ab_gift_image"/>
                <van-col span="24" class="tree_ab_gift_coin">{{ t.coin }} <img :src="diamond"/></van-col>
              </van-col>
              <van-col span="24" class="tree_ab_gift_sum"> x {{ t.giftnum }}</van-col>
            </van-grid-item>
          </van-grid>
        </van-col>
        <van-col span="22" offset="1" class="tree_zanwu" v-else>
          <img :src="zanwu" style="height: 12vh;"/>
          <van-col span="24" class="tree_zanwu_name">{{ $t('column.No_data') }}</van-col>
        </van-col>
      </van-popup>
<!--  记录    -->
      <van-popup v-model="treePlaylogShow" round closeable class="tree_play_explain_show" position="bottom" style="background: #22222e;">
        <van-col span="24" class="tree_play_explain_show_title"> ● {{ $t('column.record') }} ● </van-col>
        <van-col span="24"  class="reward_show_gift" v-if="treeLog.length">
          <van-list
            v-model="loading"
            :finished="finished"
            :finished-text="$t('Tips.no_more')"
            :loading-text="$t('Tips.loading')"
            @load="onLoad"
          >
            <van-cell v-for="(item,i) in treeLog" :key="i">
              <van-col span="4" class="reward_show_gift_img">
                <img :src="pao" style="width:5vh;height: 5vh;"/>
                <img :src="item.gift_img" style="width:2.5vh;height: 2.5vh;" class="tree_log_gift_image"/>
              </van-col>
              <van-col span="14">
                <van-col span="24" class="tree_play_log_gift_name">{{ item.gift_name }}</van-col>
                <van-col span="24" class="tree_play_log_gift_time">{{ item.create_time }}</van-col>
              </van-col>
              <van-col span="5" class="tree_play_log_gift_coin">{{ item.gift_coin }} <img :src="diamond"/></van-col>
            </van-cell>
          </van-list>
        </van-col>
        <van-col span="22" offset="1" class="tree_zanwu" v-else>
          <img :src="zanwu" style="height: 12vh;"/>
          <van-col span="24" class="tree_zanwu_name">{{ $t('column.No_data') }}</van-col>
        </van-col>
      </van-popup>
      <!--  中奖提示    -->
      <van-popup v-model="rewardShow" round closeable close-icon="close" style="min-height: 43vh;width: 80%;background: linear-gradient(to top, #4377c0, #665aac);">
        <van-col span="24" class="tree_play_explain_show_title"> {{ $t('column.Congratulations_winning_prize')}} </van-col>
        <van-col span="24" class="reward_show_gift_bg">
          <van-grid :column-num="4" :gutter="5">
            <van-grid-item v-for="(t,i) in reward_gift" :key="i" >
              <van-col span="24" class="reward_show_gift_img">
                <img :src="pao" style="width:8vh;height: 8vh;"/>
                <img :src="t.img" style="width:4vh;height: 4vh;" class="tree_ab_gift_image"/>
                <van-col span="24" class="tree_ab_gift_coin">{{ t.coin }} <img :src="diamond"/></van-col>
              </van-col>
              <van-col span="24" class="tree_ab_gift_sum"> x {{ t.count }}</van-col>
            </van-grid-item>
          </van-grid>
        </van-col>
        <van-col span="24" class="reward_show_bag"> {{ reward_gift_coin }} </van-col>
        <van-col span="20" offset="2" class="reward_show_btn">
          <van-button round size="large" color="linear-gradient(to right, #7e5fe7, #6085d5)" @click="rewardShow = false">{{ $t('column.got_it') }}</van-button>
        </van-col>
      </van-popup>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'
  import { Toast, Popover, Dialog } from 'vant'
  export default {
    name: 'app',
    components: { Popover },
    data() {
      return {
        coin: 0,
        currency_name: '',
        notice: [],
        gift: [],
        reward_gift: [],
        bag: [],
        reward_gift_coin: '', // 中奖总金额文本说明
        consumption: [],
        shine_sum: 10, // 光芒数量
        loading: false,
        finished: false,
        giftShow: false,
        kettleShow: false,
        playExplainShow: false,
        treeKnapsackShow: false,
        treePlaylogShow: false,
        rewardShow: false,
        giftOne: {},
        tree_game_bg: '', // tree_game_bg: require('../assets/img/tree_bg.png'),
        tree: '', // tree: require('../assets/img/tree.png'),
        pao: require('../assets/img/pao.png'),
        shine: require('../assets/img/shine.png'),
        kettle: require('../assets/img/kettle.gif'),
        diamond: '', // diamond: require('../assets/img/diamond_1.png'),
        zanwu: require('../assets/img/zanwu.png'),
        play_explain: '',
        treeLog: [], // 记录
        isBtn: false,
        page: 1,
      }
    },
    created: function() {
      // document.title = '游戏'
      this.get_tree()
      this.get_notice()
    },
    methods: {
      treeBtn(i) {
        if (this.isBtn) {
          const name = this.$t('column.Operation_is_too_fast')
          Toast({
            message: name,
            position: 'bottom',
          });
          return false
        }
        this.isBtn = true
        // 抽奖
        const uid = this.$route.query.uid
        const token = this.$route.query.token
        const voice_id = this.$route.query.voice_id
        const sum = this.consumption[i].id
        this.loading = true
        axios.get(this.baseUrlApi+"/vue/tree_game_api/playing_tree",{
          params:{
            uid: uid,
            token: token,
            sum: sum,
            voice_id: voice_id
          }
        }).then(res =>{
          this.loading = false
          if(res.data.code === 1){
            this.kettleShow = true
            this.coin = res.data.data.user.coin
            this.reward_gift = res.data.data.winning
            this.reward_gift_coin = res.data.data.winning_coin_text
            let timer = setInterval(() => {
              //需要定时执行的代码
              this.rewardShow = true
              this.kettleShow = false
              clearTimeout(timer)
              this.isBtn = false
            },1500)
          }else{
            Toast.fail(res.data.msg)
            this.isBtn = false
          }
        })
      },
      treePlayExplain(){
        // 玩法
        this.playExplainShow = true
      },
      get_notice() {
        // 公告
        const uid = this.$route.query.uid
        const token = this.$route.query.token
        this.loading = true
        axios.get(this.baseUrlApi+"/vue/tree_game_api/winning_announcement",{
          params:{
            uid: uid,
            token: token
          }
        }).then(res =>{
          this.loading = false
          if(res.data.code === 1){
            this.notice = res.data.data
          }
        })
      },
      treeKnapsack(){
        // 背包
        const uid = this.$route.query.uid
        const token = this.$route.query.token
        this.loading = true
        axios.get(this.baseUrlApi+"/vue/tree_game_api/user_bag",{ params:{ uid:uid,token:token }}).then(res =>{
          this.loading = false
          if(res.data.code === 1){
            this.bag = res.data.data
            this.treeKnapsackShow = true
          }else{
            Toast.fail(res.data.msg)
          }
        })
      },
      treePlaylogBtn() {
        this.page = 1
        this.finished = false
        this.treePlaylog()
      },
      treePlaylog() {
        // 记录
        const uid = this.$route.query.uid
        const token = this.$route.query.token
        this.loading = true
        axios.get(this.baseUrlApi+"/vue/tree_game_api/get_log",{
          params:{
            uid: uid,
            token: token,
            page: this.page
          }
        }).then(res =>{
          if(res.data.code === 1){
            if(this.page === 1){
              this.treeLog = []
            }
            const ret_data = res.data.data
            for(let i =0; i<ret_data.length; i++){
              this.treeLog.push(ret_data[i])
            }
            this.loading = false
            if(ret_data.length <= 0){
              this.finished = true
            }
            this.page++
            this.treePlaylogShow = true
          }else{
            Toast.fail(res.data.msg)
          }
        })
      },
      giftShowBtn(v) {
        this.giftOne = v
        this.giftShow = true
      },
      close() {
        // 拦截关闭游戏
        window.location.href="live://live?action=close"
      },
      onLoad() {
        // 异步更新数据
        if (this.page != 1) {
          this.treePlaylog()
        }
      },
      get_tree() {
        const uid = this.$route.query.uid;
        const token = this.$route.query.token;
        axios.get(this.baseUrlApi+"/vue/tree_game_api/get_tree_list",{ params:{ uid:uid,token:token }}).then(res =>{
          if(res.data.code==1){
            this.gift = res.data.data.gift
            this.consumption = res.data.data.consumption
            this.coin = res.data.data.coin
            this.currency_name = res.data.data.currency_name
            this.play_explain = res.data.data.game_list.rule
            this.tree_game_bg = res.data.data.game_list.game_bg
            this.tree = res.data.data.game_list.game_title
            this.diamond = res.data.data.game_list.game_coin_picture
            console.log(res.data.data)
          }else{
            Toast.fail(res.data.msg)
          }
        })
      }
    }
  }
</script>

<style lang="css">

</style>



