<template>
  <div class="level-box">
    <div class="level-top">
      <img :src="user_info.avatar" alt="">
    </div>
    <div class="level-jd">
      <div class="level-jd-box" v-if="user_type==1">
        <p>{{ $t('column.Distance_anchor') }}{{grade_level.top_name}}{{ $t('column.Grade_difference') }}{{grade_level.top_up}}</p>
        <div>
          <div class="level-jd-box-l">Lv{{grade_level.level_name}}</div>
          <div class="level-progress">
            <van-progress  :percentage="grade_level.top_progress" :show-pivot="show_pivot" color="#5A91F6"/>
          </div>
        </div>
      </div>
      <div class="level-jd-box" v-if="user_type==2">
        <p>{{ $t('column.Distance_anchor') }}{{income_level.top_name}}{{ $t('column.Grade_difference') }}{{income_level.top_up}}</p>
        <div>
          <div class="level-jd-box-l">Lv{{income_level.level_name}}</div>
          <div class="level-progress">
            <van-progress  :percentage="income_level.top_progress" :show-pivot="show_pivot" color="#5A91F6"/>
          </div>
        </div>
      </div>
      <div class="level-jd-box" v-if="user_type==3">
        <p>{{ $t('column.Distance_anchor') }}{{talker.top_name}}{{ $t('column.Grade_difference') }}{{talker.top_up}}</p>
        <div>
          <div class="level-jd-box-l">Lv{{talker.talker_level_name}}</div>
          <div class="level-progress">
            <van-progress  :percentage="talker.top_progress" :show-pivot="show_pivot" color="#5A91F6"/>
          </div>
        </div>
      </div>
      <div class="level-jd-box" v-if="user_type==4">
        <p>{{ $t('column.Distance_play') }}{{player.top_name}}{{ $t('column.Grade_difference') }}{{player.top_up}}</p>
        <div>
          <div class="level-jd-box-l">Lv{{player.player_level_name}}</div>
          <div class="level-progress">
            <van-progress :percentage="player.top_progress" :show-pivot="show_pivot" color="#5A91F6"/>
          </div>
        </div>
      </div>
    </div>

    <div class="level-button">
      <div class="level-button-box">
        <div :class=" user_type == 1?'level-button-box-l':'level-button-box-l-a'" @click="authType(1)" v-if="level_type.grade_type">{{ $t('btn.wealth_level') }}</div>
        <div :class="user_type == 2?'level-button-box-r-a':'level-button-box-l-a'" @click="authType(2)" v-if="level_type.grade_income_type">{{ $t('btn.profit_level') }}</div>
        <div :class=" user_type == 3?'level-button-box-l':'level-button-box-l-a'" @click="authType(3)" v-if="level_type.talker_type">{{ $t('btn.Anchor_level') }}</div>
        <div :class="user_type == 4?'level-button-box-r-a':'level-button-box-l-a'" @click="authType(4)" v-if="level_type.player_type">{{ $t('btn.Level_accompanying_player') }}</div>
      </div>
    </div>

    <div class="anchor_level-bottom">
      <van-row>
        <van-col span="7">
          <van-col span="24" v-if="user_type==1 || user_type==2 || user_type==3">
            <div class="anchor_level_img_name1" style="text-align: right"> 聊天<img src="../assets/img/tu1.png" style="margin-left: 0.5rem;"></div>
          </van-col>
          <van-col span="24" style="margin-top: 6rem;text-align: right" v-if="user_type==1 || user_type==2 || user_type==3">
            <div class="anchor_level_img_name1"> 语音<img src="../assets/img/tu3.png" style="margin-left: 0.5rem;"></div>
          </van-col>
          <van-col span="24" v-if="user_type==4">
            <div class="anchor_level_img_name1" style="text-align: right"> 下单<img src="../assets/img/tu5.png" style="margin-left: 0.5rem;"></div>
          </van-col>
        </van-col>

        <van-col span="10" style="text-align: center;margin-top: 1rem;height: 9rem;">
          <img :src="level_bg_img" class="anchor_level_img1">
        </van-col>
        <van-col span="7">
          <van-col span="24" v-if="user_type==1 || user_type==2 || user_type==3">
            <div class="anchor_level_img_name1"> <img src="../assets/img/tu2.png" style="margin-right: 0.5rem;">送礼</div>
          </van-col>
          <van-col span="24" style="margin-top: 6rem;" v-if="user_type==1 || user_type==2 || user_type==3">
              <div class="anchor_level_img_name1"> <img src="../assets/img/tu4.png" style="margin-right: 0.5rem;">视频</div>
          </van-col>
        </van-col>
      </van-row>



<!--      <img src="../assets/img/dengji_tu@2x.png" alt="">-->
<!--      <img v-if="type==2 || user_type==2" src="../assets/img/dengji_tu1@2x.png" alt="">-->
    </div>
  </div>
</template>
<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'
  import QRCode from 'qrcodejs2'
  //Vue.use(Loading);

  export default {
    name: 'Index',
    data:function() {
      return {
        loading:false,
        user_account: false,
        withdrawal: '',
        user_info:[],
        player:[],
        talker:[],
        type:1,
        show_pivot:false,
        user_type:1,
        level_type: {
          'grade_type': 0,
          'grade_income_type': 0,
          'talker_type': 0,
          'player_type': 0
        },
        level_bg_img: '',
        grade_level: [],
        income_level: [],
      }
    },
    created: function() {
      //this.invite_info.income = 0;
      this.get_list();
      //this.creatQrCode()
      //this.loading = true
      document.title = this.$t('title.anchor_level')
    },
    methods: {
      get_list(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        const loadings = Toast.loading({
          message: this.$t('Tips.loading'),
          forbidClick: true,
          duration: 0, // 持续展示 toast
        });
        axios.get(this.baseUrlApi+"/vue/user_vue_api/get_user_level",{ params:{ uid:uid,token:token }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             var _this = this;

             _this.talker = res.data.data.talker_level
             _this.player = res.data.data.player_level
             _this.user_info = res.data.data.user_info
             _this.type = res.data.data.type
             if(_this.type==1 || _this.type==2){
               _this.user_type = res.data.data.type
             }
             _this.level_type =  res.data.data.level_type
             _this.grade_level= res.data.data.grade_level
            _this.income_level= res.data.data.income_level
             _this.level_bg_img = res.data.data.level_bg_img ? res.data.data.level_bg_img : require('../assets/img/tu6.png')
             console.log(';;;;;;;;;;;')
             console.log(this.user_type)
             loadings.clear()
           }else{
             loadings.clear()
             Toast.fail(res.data.msg);
           }
           loadings.clear()
        })

      },
      href(href_d){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        this.$router.push({path:'/'+href_d+'',query:{uid:uid, token: token }});
      },
      authType(type){
        this.user_type = type
      }
    }
  }

</script>

<style>

</style>



