<template>
  <div id="disguise">
    <van-row>
      <van-col span="24">
        <van-tabs @click="onClickType">
          <van-tab v-for="index in title" :key="index" :title="index"/>
        </van-tabs>
      </van-col>
      <van-col span="22" offset="1" class="disguise_bg">

        <van-col span="24" class="disguise_bg_img">
          <van-col span="24" class="disguise_bg_img_avatar" v-if="keys === 2">
            <img :src="avatar_img"/>
          </van-col>
          <van-col span="24" class="disguise_bg_img_icon">
            <img :src="head_img"/>
          </van-col>
        </van-col>
      </van-col>

      <van-col span="22" offset="1" class="disguise_list">
        <van-row gutter="20">
          <van-col span="12" v-for="(item,i) in list" :key="i">
            <van-col span="24" :class="list_i === i ? 'disguise_list_border disguise_list_border_checked' : 'disguise_list_border'" @click="disguise_border_btn(i)">
              <div class="disguise_list_border_Wearing" v-if="item.status">
                {{ $t('column.Wearing') }}
              </div>
              <van-col span="24" class="disguise_list_border_img">
                <img :src="avatar_img" class="disguise_list_border_img_avatar" v-if="keys === 2"/>
                <van-col span="24" class="disguise_list_border_img_icon">
                  <img :src="item.dress_up_icon"/>
                </van-col>
              </van-col>
              <van-col span="24" class="disguise_list_border_name">{{ item.dress_up_name }}</van-col>
              <van-col span="24" class="disguise_list_border_endtime">{{ $t('column.Expiration_time') }}: {{ item.endtime }}</van-col>
              <div class="disguise_list_border_checked_i">
                <van-icon name="success" />
              </div>
            </van-col>
          </van-col>
        </van-row>
      </van-col>
      <van-col span="24"  class="disguise_bottom">
        <van-col span="20" offset="2" class="disguise_bottom_btn" v-if="list.length">
          <van-button round color="#cccccc" size="large" @click="Wear_btn" v-if="wear_btn_status === 1">
            {{ $t('btn.close') }}
          </van-button>
          <van-button round color="linear-gradient(to right, #F86C2F, #CA26ED)" size="large" @click="Wear_btn" v-else>
            {{ $t('btn.Wear') }}
          </van-button>
        </van-col>
      </van-col>
    </van-row>
  </div>
</template>
<script>
import '@/assets/css/style_a.css'
import axios from 'axios'
import {Dialog, Toast} from 'vant'

export default {
  data:function() {
    return {
      head_img: '',
      keys:0,
      list:[],
      list_i: 0,
      title:[],
      avatar_img: '',
      wear_btn_status: 0
    }
  },
  created: function() {
    for(let i= 1; i < 8; i++) {
      switch (i){
        case 1:
          this.title.push(this.$t('btn.medal'))  // 勋章
          break;
        case 2:
          this.title.push(this.$t('btn.Home_effects')) // 主页特效
          break;
        case 3:
          this.title.push(this.$t('btn.Head_frame'))// 头像框
          break;
        case 4:
          this.title.push(this.$t('btn.Chat_bubble')) // 聊天气泡
          break;
        case 5:
          this.title.push(this.$t('btn.Chat_background')) // 聊天背景
          break;
        case 6:
          this.title.push(this.$t('btn.entry_vehicles')) // 进场动画
          break;
        case 7:
          this.title.push(this.$t('btn.Mobilization_effect')) // 进场特效
          break;
        default:
      }
    }
    console.log(this.title)
    this.get_list();
    document.title = this.$t('title.my_dress')
  },
  methods: {
    onClickType(name) {
      this.list_i = 0
      this.keys = name
      this. get_list()
    },
    disguise_border_btn(i) {
      this.list_i = i
      this.head_img = this.list[this.list_i].dress_up_icon
      this.wear_btn_status = this.list[this.list_i].status
      console.log(this.list_i)
    },
    get_list(){
      const uid = this.$route.query.uid;
      const token = this.$route.query.token;
      const type = this.keys + 1 === 6 ? 11 : this.keys + 1
      axios.get(this.baseUrlApi+"/vue/dress_api/my_display",{ params:{ uid:uid,token:token, type: type }}).then(res =>{
        this.loading = false;
        if(res.data.code===1){
          this.list = res.data.data.list
          this.avatar_img = res.data.data.avatar_img
          console.log(this.list)
          this.head_img = this.list.length ? this.list[this.list_i].dress_up_icon : ''
          this.wear_btn_status = this.list.length ? this.list[this.list_i].status : ''
        }
      })
    },
    Wear_btn(){
      const uid = this.$route.query.uid
      const token = this.$route.query.token
      const id = this.list[this.list_i].id
      let message = ''
      if (this.wear_btn_status === 1){
        message = this.$t('Tips.is_close')
      } else {
        message = this.$t('Tips.is_open')
      }
      Dialog.confirm({
        message:  message,
        confirmButtonColor:'#CA26ED',
        confirmButtonText: this.$t('btn.confirm'),
        cancelButtonText: this.$t('btn.cancel')
      }).then(() => {
        // on confirm
        axios.get(this.baseUrlApi+"/vue/dress_api/save_display_status",{ params:{ uid:uid,token:token, id: id }}).then(res =>{
          Toast(res.data.msg)
          if(res.data.code===1){
            this. get_list()
          }
        })
      }).catch(() => {
        // on cancel
      });
    }
  }
}
</script>
<style>
</style>



