</template>
<script>
import '@/assets/css/style_a.css'
import axios from 'axios'
import { Dialog, Toast } from 'vant'
export default {
  data:function() {
    return {
      active: 0,
      vip_privilege1: require('../assets/img/vip_privilege1.png'),
      vip_privilege2: require('../assets/img/vip_privilege2.png'),
      vip_privilege3: require('../assets/img/vip_privilege3.png'),
      vip_privilege4: require('../assets/img/vip_privilege4.png'),
      vip_privilege5: require('../assets/img/vip_privilege5.png'),
      vip_privilege6: require('../assets/img/vip_privilege6.png'),
      vip_privilege7: require('../assets/img/vip_privilege7.png'),
      vip_privilege8: require('../assets/img/vip_privilege8.png'),
      vip_privilege9: require('../assets/img/vip_privilege9.png'),
      vip_privilege10: require('../assets/img/vip_privilege10.png'),
      vip_privilege11: require('../assets/img/vip_privilege11.png'),
      vip_privilege12: require('../assets/img/vip_privilege12.png'),
      list: [],
      user_vip: {},
      show_pivot:false,
      content: '',
    }
  },
  created: function() {
    this.get_list()
  },
  methods: {
    onClickLeft() {
      // 拦截返回app
      window.location.href="live://live?action=close"
    },
    onClickRight() {
      // 说明
      this.show_pivot = true
    },
    PurchaseBtn(item){

      const uid = this.$route.query.uid;
      const token = this.$route.query.token;
      Dialog.confirm({
        message: this.$t('vip.is_pay_vip'),
        cancelButtonText: this.$t('btn.cancel'),
        confirmButtonText: this.$t('btn.confirm'),
        closeOnClickOverlay: true
      }).then(() => {
          // on confirm
        const loadings = Toast.loading({
          message: this.$t('Tips.loading'),
          forbidClick: true,
          duration: 0, // 持续展示 toast
        });
        axios.get(this.baseUrlApi+"/vue/vip_api/pay_vip",{ params:{ uid:uid,token:token, id:item.id }}).then(res =>{
          loadings.clear()
          if(res.data.code==1){
            Toast.success({ message: res.data.msg, duration: 1000 })
            setTimeout(() => {
              //需要定时执行的代码
              this.get_list()
            }, 2000)
          }else{
            Toast.fail(res.data.msg)
          }
        })
      }).catch(() => {
        // on cancel
      })
    },
    get_list(){
      const uid = this.$route.query.uid
      const token = this.$route.query.token
      const loadings = Toast.loading({
        message: this.$t('Tips.loading'),
        forbidClick: true,
        duration: 0, // 持续展示 toast
      });
      axios.get(this.baseUrlApi+"/vue/vip_api/get_vip_list",{ params:{ uid:uid,token:token }}).then(res =>{
        loadings.clear()
        if(res.data.code==1){
          this.list = res.data.data.list
          this.user_vip = res.data.data.user_vip
          this.content = res.data.data.content
        }else{
          Toast.fail(res.data.msg);
        }
      })
    }
  }
}

</script>

