<template>
  <div>
    <div class="feedback-top">
      <textarea class="feedback-top-text" v-model="content" maxlength="200" :placeholder="$t('placeholder.feedback_content')" />
    </div>
    <div class="feedback-bottom">
      <van-uploader v-model="fileList" :before-read="fileUpload"  multiple :max-count="4"/>
    </div>
    <div class="feedback-tel">
      <span>{{ $t('column.feedback_tel') }}</span>
      <input :placeholder="$t('placeholder.feedback_tel')" v-model="tel" oninput="if(value.length>11)value=value.slice(0,11)" type="number">
    </div>
    <div class="feedback-button" @click="requestAdd">{{ $t('btn.feedback_btn') }}</div>
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        content: '',
        tel:'',
        fileList: [],
        fileurl:'',
        uploadToken:'',
        domain:'',
        bucket:'',
      }
    },
    created: function() {
      //this.convert();v-model="fileList"
      this.getQiniuToken();
      document.title = this.$t('title.feedback')
    },
    methods: {
      getQiniuToken() {
        //console.log(this.fileList)

      	axios.get(this.baseUrlApi + "/api/app_api/get_qiniu_upload_token").then(res => {
      		//console.log(res)
          this.uploadToken = res.data.token;
      		this.domain = res.data.domain;
          this.bucket = res.data.bucket;
      	})
      },
      fileUpload(file){
        //console.log(file.name)
        var timestamp = new Date().getTime();
        let data = new FormData();
        data.append('token', this.uploadToken);
        //七牛需要的token，叫后台给，是七牛账号密码等组成的hash
        data.append('file', file);
        var custom_name = timestamp+'_'+file.name
        data.append('key',custom_name)
        //data.append('fileName',file.name)

        axios({
          method: 'post',
          url: 'http://up-z0.qiniup.com',
          data:data,
          headers: {
              'Content-Type': 'multipart/form-data'
          }
        })
        .then(res => {
            let url = new Array()
            url['url'] = this.domain+'/'+res.data.key;
            this.fileList.push(url)
            if(this.fileurl==''){
              this.fileurl +=this.domain+'/'+res.data.key
            }else{
              this.fileurl += ','+this.domain+'/'+res.data.key
            }
        })
        .catch(err => {
            console.log(err)
        })

      },
      requestAdd(data){
        //console.log(data)

        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var fileList = this.fileList;
        var fileurl = this.fileurl;
        var tel = this.tel;
        var content = this.content;
        //console.log(content.length)
        if(content==''){
          Toast.fail(this.$t('Tips.feedback_content'));
          return
        }
        if(content.length < 10){
          Toast.fail(this.$t('Tips.feedback_content_length'));
          return
        }
        if(tel==''){
          Toast.fail(this.$t('Tips.feedback_tel'));
          return
        }
        if(fileList.length<1){
          Toast.fail(this.$t('Tips.feedback_fileList'));
          return
        }
        const message = this.$t('Tips.loading')
        const loadings = Toast.loading({
          message: message,
          forbidClick: true,
          duration: 0, // 持续展示 toast
        });
        axios.get(this.baseUrlApi+"/vue/user_vue_api/buy_feedback",{ params:{ uid:uid,token:token,fileurl:fileurl,content:content,tel:tel }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             Toast.success(res.data.msg);
             _this.fileList = []
             _this.content = ''
             _this.tel = ''
             _this.fileurl = ''
           }else{
             Toast.fail(res.data.msg);
           }
           loadings.clear()

        }).catch(err => {
            console.log(err)
            loadings.clear()
        })
      }
    }
  }

</script>


<style>
  body,#app{
    background-color: #F5F7FA;
  }
  .feedback-top{
    width: 100%;
    min-height: 10rem;
    background-color: #FFFFFF;
    overflow: hidden;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  .feedback-top-text{
    width: 95%;
    height: 10rem;
    margin: 0 auto;
    margin-left: 2.5%;
    padding: 0.5rem 0;
    border: none;
    resize:none;
  }
  .feedback-bottom{
    /* width: 100%; */
    background-color: #FFFFFF;
    padding: 1rem;
  }
  .feedback-tel{
    background-color: #FFFFFF;
    margin-top: 1rem;
    line-height: 3rem;
  }
  .feedback-tel span{
    color: #333333;
    margin-left: 2.5%;
    font-size: 1.1rem;
  }
  .feedback-tel input{
    width: 60%;
    float: right;
    margin-right: 2.5%;
    border:none;
    text-align: right;
  }
  .feedback-button{
    width: 80%;
    margin: 0 auto;
    background:linear-gradient(-90deg,rgba(90,145,246,1) 0%,rgba(121,218,250,1) 100%);
    border-radius: 3rem;
    text-align: center;
    margin-top: 3rem;
    line-height: 3rem;
    color: #FFFFFF;
  }
</style>



