<template>
  <div id="wealth_level">
    <van-row>
      <van-col span="24" class="wealth_level_bg">
        <van-nav-bar
          class="nav-pad-top"
          left-arrow
          safe-area-inset-top
          @click-right="onClickRight"
          @click-left="onClickLeft"
        >
          <template #left>
            <van-icon name="arrow-left" size="18" color="#ffffff"/>
          </template>
          <template #title>
            <span class="wealth_level_title">{{ $t('level.bar_title') }}</span>
          </template>
          <template #right>
            <van-icon name="info-o" size="18" color="#ffffff"/>
          </template>
        </van-nav-bar>
        <van-col span="12" offset="6">
          <van-col span="12" @click="activeBtn(1)">
            <van-col span="24" class="wealth_level_center">{{ $t('level.Wealth') }}</van-col>
            <van-col span="4" offset="10" class="wealth_level_border" v-if="active === 1"> </van-col>
          </van-col>
          <van-col span="12" @click="activeBtn(2)">
            <van-col span="24" class="wealth_level_center">{{ $t('level.Charm') }}</van-col>
            <van-col span="4" offset="10" class="wealth_level_border" v-if="active === 2"> </van-col>
          </van-col>
        </van-col>
        <van-col span="22" offset="1" class="wealth_level_top">
          <van-col span="5" class="wealth_level_image">
            <van-image
              round
              width="7vh"
              height="7vh"
              :src="user.avatar"
            />
          </van-col>
          <van-col span="19">
            <van-col span="24" class="wealth_level_name">{{ user.user_nickname }} <img :src="level.level_icon" class="wealth_level_name_img"/> </van-col>
            <van-col span="20">
              <van-progress :percentage="progress" :show-pivot="false" stroke-width="6" color="#F182FF" track-color	="#FFFFFF"/>
            </van-col>
            <van-col span="24" class="wealth_level_distance">{{ $t('level.Distance_to_upgrade')}} {{ level.spread }}</van-col>
          </van-col>
        </van-col>
      </van-col>
      <van-col span="24" class="wealth_level_bg_img">
        <img src="../assets/img/wealth_level_bg_img.png" alt="">
      </van-col>
      <van-col span="22" offset="1" class="wealth_level_type_top">
        <van-col span="6" class="wealth_level_type_title">{{ $t('level.level') }}</van-col>
        <van-col span="6" class="wealth_level_type_title wealth_level_type_title_bor">{{ $t('level.icon') }}</van-col>
        <van-col span="12" class="wealth_level_type_title">{{ $t('level.effects') }}</van-col>
      </van-col>
      <van-col span="22" offset="1">
        <van-col span="24" v-for="(item,i) in list" :key="i" class="wealth_level_type_list">
          <van-col span="6" class="wealth_level_type_name">{{ item.min_level }} - {{ item.max_level }}</van-col>
          <van-col span="6" class="wealth_level_type_name wealth_level_type_title_bor">
            <img :src="item.level_icon" alt="" class="wealth_level_level_icon">
          </van-col>
          <van-col span="12" class="wealth_level_type_name">
            <img :src="item.icon" alt="" class="wealth_level_icon">
          </van-col>
        </van-col>
      </van-col>
    </van-row>
    <van-popup v-model="show_pivot"
               closeable
               round
               position="bottom"
               :style="{ height: '90%' }">
      <div style="padding: 40px 10px 20px" v-html="content"></div>
    </van-popup>
  </div>

</template>
<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'
  export default {
    data:function() {
      return {
        active: 1,
        list:[],
        user: {},
        level: {},
        level_bg_img: '',
        show_pivot:false,
        content: '',
        progress: 0,
      }
    },
    created: function() {
     this.get_list();
      document.title = this.$t('title.anchor_level')
    },
    methods: {
      activeBtn(i) {
        this.active = i
        this.get_list()
      },
      onClickRight() {
        // 说明
        this.show_pivot = true
      },
      onClickLeft() {
        // 拦截关闭h5
        window.location.href="live://live?action=close"
      },
      get_list(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        const loadings = Toast.loading({
          message: this.$t('Tips.loading'),
          forbidClick: true,
          duration: 0, // 持续展示 toast
        });
        axios.get(this.baseUrlApi+"/vue/level_api/level_list",{ params:{ uid:uid,token:token,type:this.active }}).then(res =>{
          if(res.data.code==1){
            console.log(res.data)
            this.user = res.data.data.user
            this.list = res.data.data.list
            this.level = res.data.data.grade_level
            this.progress = this.level.progress
            this.content = res.data.data.content
            loadings.clear()
          }else{
            loadings.clear()
            Toast.fail(res.data.msg);
          }
          loadings.clear()
        })
      }
    }
  }

</script>

<style>

</style>



