<template>
  <div id="app">
    <div class="up-top">
      <div class="up-top-box">{{currency_name}}{{ $t('column.balance') }} <span>{{info.coin}}</span></div>
    </div>
    <div class="up-list">
      <div class="up-list-top">{{ $t('column.Please_select_recharge') }}{{currency_name}}</div>
      <div class="rule-list">
         <div class="rule-list-box" v-for="val in list" :key="val.id">
           <div :class="val.id==id?'rule-list-box-ta':'rule-list-box-t'" @click="add_rule(val.id)">
             <div :class="val.id==id?'rule-list-box-t-aa':'rule-list-box-t-a'">
               {{val.coin}}
               <img src="../assets/img/coin_xin_big@2x.png" alt="">
             </div>
             <div :class="val.id==id?'rule-list-box-t-ba':'rule-list-box-t-b'">¥{{val.money}}</div>
           </div>
         </div>
      </div>
    </div>
    <div class="up_button" >{{ $t('btn.payment') }}</div>
    <div class="up_button_xy">{{ $t('column.Payment_means_consent') }} <span @click="agreement()">《{{ $t('column.Recharge_agreement') }}》</span></div>
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        list:[],
        info:[],
        privilege:[],
        id:0,
        coin:0,
        currency_name:'',
        friend_name:'',
      }
    },
    created: function() {
      //this.convert();
      this.get_list();
      document.title = this.$t('title.top_up')
    },
    methods: {
      get_list(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/api/wallet_vue_api/get_recharge_page_data",{ params:{ uid:uid,token:token }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             _this.list = res.data.data.list
             _this.info = res.data.data.user_info
             //_this.privilege = res.data.data.privilege
             //_this.id =  _this.info.id
             _this.currency_name =  res.data.data.currency_name
             _this.friend_name = res.data.data.friend_name
             //console.log(_this.list)
             //this.bank_name = list[0]['name']
           }else{
             Toast.fail(res.data.msg);
           }

        })
      },
      get_list_info(pid){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/api/noble_vue_api/get_info",{ params:{ uid:uid,token:token,id:pid }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             _this.list = res.data.data.noble_list
             _this.info = res.data.data.noble_info
             _this.privilege = res.data.data.privilege

           }
        })
      },
      add_rule(id){
        var _this = this;
        _this.id =  id
      },
      // 充值 协议
      agreement:function(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        window.location.href='#/agreement?uid='+uid+'&token='+token;
      }
    }
  }

</script>

<style>
</style>



