<template>
  <div>
    <div class="about-top">
      <img src="../assets/img/banner@2x.png" alt="">
    </div>
    <div class="about-box">
      <h3>关于我们</h3>
      <p>
        {{web_name}}<br/>
        联系方式：{{tel}}<br/>
        企业官方网站：{{web_url}}<br/>
        {{copyright}}
      </p>
    </div>

  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        tel: '',
        web_name:'',
        web_url:'',
        copyright:''
      }
    },
    created: function() {
      //this.convert();
      this.get_info();
      document.title = '关于我们'
    },
    methods: {
      get_info(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/mapi/public/index.php/vue/about_api/index",{ params:{ uid:uid,token:token }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             //_this.list = res.data.data.noble_list
             _this.web_name = res.data.data.system_name
             _this.copyright =  res.data.data.copyright
             _this.web_url =  res.data.data.web_url
             _this.tel = res.data.data.tel
             //console.log(_this.list)
             //this.bank_name = list[0]['name']
           }else{
             Toast.fail(res.data.msg);
           }

        })
      },
      href_log(data){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //window.location.href = data;
        this.$router.push({path:'/'+data+'',query:{uid:uid, token: token }});
      }
    }
  }

</script>

<style>
  .about-top{
    width: 100%;
    overflow: hidden;
  }
  .about-top img{
    width: 100%;
  }
  .about-box{
    width: 95%;
    margin: 0 auto;
  }
</style>



