<template>
  <div id="app">
    <div class="gongge_close" @click="gongge_close"></div>
    <div class="gongge_div" :style="{backgroundImage: 'url(' + gongge_bg + ')', backgroundSize:'100% 100%', backgroundRepeat: 'no-repeat'}">
      <van-row>
        <van-col span="10">
<!--          <van-col span="10" offset="2" class="gongge_top_img" @click="rongyaobangdanShowClick">-->
<!--            <van-image-->
<!--              width="50"-->
<!--              :src="rongyaobangdan"-->
<!--            />-->
<!--          </van-col>-->
<!--          <van-col span="10" class="gongge_top_img">-->
<!--            <van-image-->
<!--              width="50"-->
<!--              :src="xingyunjingli"-->
<!--            />-->
<!--          </van-col>-->
        </van-col>
        <van-col span="13">
          <div class="gongge_top_rule_div">
            <span class="gongge_top_rule"><span class="gongge_top_rule_left" @click="gongge_log">我抽中的</span>|<span class="gongge_top_rule_right" @click="portalShow = true">规则</span></span >
          </div>
        </van-col>
        <van-col span="20" offset="2" class="gongge_title_img">
          <van-image
            height="120"
            :src="gongge_title_img"
          />
        </van-col>
        <van-col span="22" offset="1" class="gongge_title_type">
          <van-row justify="center">
            <van-col span="6" v-for="(v,i) in gongge_type" :key="i" :class="gongge_type_i === i ? 'gongge_title_type_name gongge_title_type_checked' : 'gongge_title_type_name'" @click="gongge_type_btn(i)">
              {{ v.title }}
              <div class="gongge_title_type_name_level" v-if="v.level && v.level_limit">{{ v.level }}级解锁</div>
            </van-col>
          </van-row>
        </van-col>
        <van-col span="18" offset="3" class="gongge_notice">
          <van-notice-bar color="#ffffff" scrollable background="linear-gradient(90deg,#613998 0%,#7166D1 50%,#8240A3 100%)" left-icon="bell" :scrollable="false">
            <van-swipe
              vertical
              style="height: 3vh;font-size: 0.6rem;"
              :autoplay="3000"
              :show-indicators="false"
            >
              <van-swipe-item v-for="(v,i) in System_notification" :key="i"><span v-html="v.text"></span></van-swipe-item>
            </van-swipe>
          </van-notice-bar>
        </van-col>
        <van-col span="22" offset="1" class="gongge_lottery" :style="'background:' + gongge_type[gongge_type_i].bg_color">
          <div class="lottery" >
            <div class="lottery-item">
              <div class="lottery-start">
                <div class="box">
                  <p class="gongge_Lucky_b">
                    <b v-if="lucky_time">
                      <van-count-down :time="lucky_time" format="ss"style="color: #ffffff;" ref="countDown" @finish="finish"/>
                    </b>
                    <b v-else>{{ lucky_val }}</b>
                  </p>
                  <p class="gongge_Lucky_val">{{ lucky_time ? '倒计时' : '幸运值'}}</p>
                </div>
              </div>
              <div class="lottery_start_question" v-show="IsPopoverCenter && showPopoverCenter">
                <van-popover v-model="showPopover" trigger="click" placement="top">
                  <div class="lottery_start_question_html" v-html="showPopoverCenter"></div>
                  <template #reference>
                    <van-icon name="question-o" size="1.2rem" color="#ffffff"/>
                  </template>
                </van-popover>
              </div>
              <ul>
                <li v-for="(item,i) in list" :class="i==index? 'on box_sum' + count :'box_sum' + count">
                  <div class="box" :style="'background:' + gongge_type[gongge_type_i].gift_bg_color">
                    <p class="gongge_img"><img :src="item.img" alt=""></p>
                    <p class="gongge_title">{{ item.name }}</p>
                    <p class="gongge_sum" v-if="item.coin"> <img :src="diamond_img" alt="" v-if="diamond_img">{{ item.coin }}</p>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </van-col>
        <van-col  span="22" offset="1" class="gongge_btn">

          <van-col  span="8" class="gongge_btn_img" v-for="(v,i) in gongge_type[gongge_type_i].luck_draw" :key="i"  @click="startLottery(v)" v-if="i< 3">
            <img src="../assets/img/gongge_button1.png" alt="" v-if="i === 0">
            <img src="../assets/img/gongge_button2.png" alt="" v-else-if="i === 1">
            <img src="../assets/img/gongge_button3.png" alt="" v-else>
            <div class="gongge_btn_sum">
              <div class="gongge_btn_sum_top">抽{{ v }}次</div>
              <div class="gongge_btn_sum_bottom">{{ gongge_type[gongge_type_i].coin * v }}{{ currency_name }}</div>
            </div>

          </van-col>

        </van-col>
      </van-row>
      <!--  活动规则    -->
      <van-popup v-model="portalShow" position="bottom" :style="{ height: '90%' }" closeable>
        <div class="portal_show_title">活动规则</div>
        <div v-html="portal" class="portal_show_center" ></div>
      </van-popup>
      <!-- 中奖弹窗 -->
      <van-popup v-model="showToast" round closeable style="width: 90%;background: #824FC9;min-height: 10%;">
        <van-row>
          <van-col span="22" offset="1">
            <van-col  span="24" class="Win_show_title"> {{ is_winning ? '恭喜获得' : '很遗憾' }} </van-col>
            <van-col  span="24" class="Win_show_type">
              <van-grid :column-num="3" :gutter="10">
                <van-grid-item v-for="(w,i) in winning" :key="i" v-if="w.gift_id > 0">
                  <div class="Win_show_type_img">
                    <img :src="w.img"/>
                  </div>
                  <div class="Win_show_type_name">{{ w.name }} x {{ w.count }}</div>
                  <div class="Win_show_type_money">{{ w.coin }} {{ currency_name }}</div>
                </van-grid-item>
              </van-grid>
            </van-col>
            <van-col span="24" class="Win_show_coin" v-if="is_winning">礼物总价值：{{ winning_coin }}{{ currency_name }}</van-col>
            <van-col span="24" class="Win_show_url">{{ is_winning ? '奖品已放入您的背包' : '您未中的任何奖励'}}</van-col>
          </van-col>
        </van-row>
      </van-popup>
      <!--  我抽中的记录    -->
      <van-popup v-model="gonggeLogShow" position="bottom" :style="{ height: '90%' }" closeable>
        <div class="portal_show_title">中奖记录</div>
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
        >
          <van-cell v-for="(v,i) in gongge_log_list" :key="i">
            <van-row>
              <van-col span="22" offset="1">
                <van-col span="20">
                  <van-col span="24" class="gongge_log_name">{{ v.name }} x {{ v.sum }}</van-col>
                  <van-col span="24" class="gongge_log_time">{{ v.create_time }}</van-col>
                </van-col>
                <van-col span="4">
                  <img :src="v.img" class="gongge_log_img"/>
                </van-col>
              </van-col>
            </van-row>
          </van-cell>
        </van-list>
      </van-popup>
      <!--  排行榜 ---- 未完成  -->
      <van-popup v-model="rongyaobangdanShow" position="bottom" :style="{ height: '90%',background: 'linear-gradient(90deg,#613998,#7166d1 50%,#8240a3)' }" closeable>
        <div class="rongyaobangdan_title">荣耀本周榜单</div>
        <van-col span="22" offset="1" class="gongge_title_type">
          <van-row justify="center">
            <van-col span="6" v-for="(v,i) in gongge_type" :key="i" :class="rongyaobangdan_i === i ? 'gongge_title_type_name gongge_title_type_checked' : 'gongge_title_type_name'" @click="gongge_rongyaobangdan_btn(i)">{{ v.title }}</van-col>
          </van-row>
        </van-col>
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
        >
          <van-cell v-for="(v,i) in rongyaobangdan_list" :key="i">

            <van-row>
              <van-col span="22" offset="1">
                <van-col span="20">
                  <van-col span="24" class="gongge_log_name">{{ v.user_nickname }}</van-col>
                  <van-col span="24" class="gongge_log_time">{{ v.uid }}</van-col>
                </van-col>
                <van-col span="4">
                  <img :src="v.avatar" class="gongge_log_img"/>
                </van-col>
              </van-col>
            </van-row>


          </van-cell>
        </van-list>
      </van-popup>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'
  import { Toast, Popover, Dialog } from 'vant'
  export default {
    name: 'app',
    components: { Popover },
    data() {
      return {
        gonggeLogShow: false,
        rongyaobangdanShow: false,
        loading: false,
        finished: false,
        portalShow: false,
        showPopover: false,
        showPopoverCenter: '',
        IsPopoverCenter: false,
        gongge_bg: '',
        rongyaobangdan: require('../assets/img/rongyaobangdan.png'),
        xingyunjingli: require('../assets/img/xingyunjingli.png'),
        gongge_title_img: '',
        diamond_img: '',
        gongge_type_i: 0,
        portal: undefined, // 游戏规则
        gongge_type: [], // 类型列表
        gongge_log_list: [], // 我抽中的
        System_notification: [],
        lucky_val: 0, // 幸运值
        lucky_time: 0, // 幸运值 --倒计时时间
        score: 10, //消耗积分
        list: [], //奖品1-14
        index: -1, // 当前转动到哪个位置，起点位置
        count: 10, // 总共有多少个位置
        timer: 0, // 每次转动定时器
        speed: 200, // 初始转动速度
        times: 0, // 转动次数
        cycle: 50, // 转动基本次数：即至少需要转动多少次再进入抽奖环节
        prize: -1, // 中奖位置
        click: true,//标识是否处于抽奖状态
        showToast: false, //显示中奖弹窗
        winning: [], // 奖励列表
        currency_name: '', // 系统币
        winning_coin: 0, // 总奖励数
        page: 0, // 分页
        is_winning: false, // 是否中奖
        rongyaobangdan_i: 0, // 宫格id
        rongyaobangdan_list: [], // 排行榜
        level_limit: 0,
        level_explain: '',
        gongge_version: '', //游戏版本-后台编辑不一致时更新数据
      }
    },
    created: function() {
      document.title = '游戏'

      this.get_gongge()
    },
    methods: {
      gongge_close() {
        // 拦截关闭游戏
        window.location.href="live://live?action=close"
      },
      rongyaobangdanShowClick() {
        this.rongyaobangdan_i = 0
        this.rongyaobangdan_list = []
        this.page = 0
        this.get_rank()
        this.rongyaobangdanShow = true
      },
      gongge_rongyaobangdan_btn(i) {
        this.rongyaobangdan_i = i
        this.rongyaobangdan_list = []
        this.page = 0
        this.get_rank()
      },
      get_rank() {
        // 加载状态结束
        this.loading = true;
        const uid = this.$route.query.uid;
        const token = this.$route.query.token;
        axios.get(this.baseUrlApi+"/vue/gongge_api/get_ranking",{
          params:{
            uid: uid,
            token: token,
            gongge_id: this.gongge_type[this.rongyaobangdan_i].id,
            page: this.page
          }
        }).then(res =>{
          console.log(res.data)
          if(res.data.code==1){
            for (let i = 0; i < res.data.data.length; i++) {
              console.log(res.data.data[i])
              this.rongyaobangdan_list.push(res.data.data[i]);
            }
            this.page ++
            this.loading = false
            console.log(this.rongyaobangdan_list)
            // 数据全部加载完成
            if (res.data.data.length < 20) {
              this.finished = true
            }
          }
        })
      },
      onLoad() {
        // 异步更新数据
        if (this.page != 0) {
          this.get_gongge_log_list()
        }
      },
      finish() {
        console.log('倒计时结束')
        this.get_lucky_time()
      },
      gongge_log() {
        this.page = 0
        this.finished = false
        this.gongge_log_list = []
        this.get_gongge_log_list()
        this.gonggeLogShow = true
      },
      get_gongge_log_list() {
        // 加载状态结束
        this.loading = true;
        const uid = this.$route.query.uid;
        const token = this.$route.query.token;
        axios.get(this.baseUrlApi+"/vue/gongge_api/gongge_gift_log",{
          params:{
            uid: uid,
            token: token,
            page: this.page
          }
        }).then(res =>{
          console.log(res.data)
          if(res.data.code==1){
            for (let i = 0; i < res.data.data.length; i++) {
              console.log(res.data.data[i])
              this.gongge_log_list.push(res.data.data[i]);
            }
            this.page ++
            this.loading = false
            console.log(this.gongge_log_list)
            // 数据全部加载完成
            if (res.data.data.length < 20) {
              this.finished = true
            }
          }
        })
      },
      get_lucky_time() {
        const uid = this.$route.query.uid;
        const token = this.$route.query.token;
        axios.get(this.baseUrlApi+"/vue/gongge_api/get_lucky_val",{
          params:{
            uid: uid,
            token: token,
            gongge_id: this.gongge_type[this.gongge_type_i].id
          }
        }).then(res =>{
          console.log(res.data)
          if(res.data.code==1){
            this.lucky_val = res.data.data.number
            this.lucky_time = res.data.data.lucky_time
            this.level_limit = res.data.data.level_limit
            this.level_explain = res.data.data.level_explain
            this.$refs.countDown.reset()
          }
        })
      },
      get_gongge(){
        const uid = this.$route.query.uid;
        const token = this.$route.query.token;
        axios.get(this.baseUrlApi+"/vue/gongge_api/index",{
          params:{
            uid: uid,
            token: token
          }
        }).then(res =>{
          if(res.data.code==1){
            this.gongge_type = res.data.data.list
            this.portal = res.data.data.portal
            this.currency_name = res.data.data.currency_name
            this.diamond_img = res.data.data.diamond_img
            this.gongge_bg = res.data.data.gongge_bg
            this.gongge_title_img = res.data.data.gongge_title_img
            this.gongge_version = res.data.data.gongge_version
          //  this.lucky_time = 10 *1000;
            this.showPopoverCenter = this.gongge_type[this.gongge_type_i].explain
            this.IsPopoverCenter = this.gongge_type[this.gongge_type_i].double
            this.count = this.gongge_type[this.gongge_type_i].gift_count

            console.log(this.count)
            this.get_gift_list()
            this.get_roll_log()
            this.get_lucky_time()
          }else{
            Toast(res.data.msg);
          }
        })
      },
      get_gift_list() {
        axios.get(this.baseUrlApi+"/vue/gongge_api/gift_list",{
          params:{
            id: this.gongge_type[this.gongge_type_i].id
          }
        }).then(res =>{
          if(res.data.code==1){
            this.list = res.data.data
          }else{
            Toast(res.data.msg);
          }
        })
      },
      get_roll_log() {
        axios.get(this.baseUrlApi+"/vue/gongge_api/get_roll_log",{
          params:{
            id: this.gongge_type[this.gongge_type_i].id
          }
        }).then(res =>{
          if(res.data.code==1){
            this.System_notification = res.data.data
          }else{
            Toast(res.data.msg);
          }
        })
      },
      onSelect(action) {
        console.log('ddddddd')
        Toast(action.text)
      },
      // 切换宫格类型
      gongge_type_btn(i) {
        // 判断是否正在旋转中
        if (!this.click) {
          Toast('抽奖中不能切换')
          return
        }
        this.gongge_type_i = i
        this.showPopoverCenter = this.gongge_type[this.gongge_type_i].explain
        this.IsPopoverCenter = this.gongge_type[this.gongge_type_i].double
        this.count = this.gongge_type[this.gongge_type_i].gift_count

        this.get_gift_list()
        this.get_roll_log()
        this.get_lucky_time()
      },
      //抽奖方法
      startLottery(sum) {
        console.log('ddddd')
        // 是否有等级限制
        if(this.level_limit) {
          Dialog.alert({
            title: '用户等级不足',
            message: this.level_explain,
          }).then(() => {
            return false
          })
          return false
        }
        if (!this.click) {
          return false
        }
        this.click = false
        const uid = this.$route.query.uid;
        const token = this.$route.query.token;
        axios.get(this.baseUrlApi+"/vue/gongge_api/luck_draw",{
          params:{
            uid: uid,
            token: token,
            sum: sum,
            gongge_id: this.gongge_type[this.gongge_type_i].id
          }
        }).then(res =>{
          console.log(res.data)
          if(res.data.code==1){
            this.prize = res.data.data.winning_index; //中奖位置,可由后台返回

            this.index = -1;
            this.winning = res.data.data.winning
            this.is_winning = res.data.data.is_winning
            this.winning_coin = res.data.data.winning_coin

            this.startRoll()
            if (this.gongge_version != res.data.data.gongge_version){
              this.gongge_version = res.data.data.gongge_version
              // 更新游戏数据
              this.get_gongge()
            }
          }else{
            this.click = true
            Toast(res.data.msg);
          }
        })
      },
      // 开始转动
      startRoll() {
        this.times += 1 // 转动次数
        this.oneRoll() // 转动过程调用的每一次转动方法，这里是第一次调用初始化
        // 如果当前转动次数达到要求 && 目前转到的位置是中奖位置
        if (this.times > this.cycle + 10 && this.prize === this.index) {
          clearTimeout(this.timer) // 清除转动定时器，停止转动
          this.prize = -1
          this.times = 0
          this.speed = 200
          this.click = true
          this.click = true
          const that = this
          setTimeout(res => {
             that.showToast = true
             that.index = -1
            that.get_lucky_time()
            that.get_roll_log()
          }, 500)
        } else {
          if (this.times < this.cycle) {
            this.speed -= 10 // 加快转动速度
          } else if (this.times === this.cycle) {
            if (this.prize > this.count) {
              this.prize = this.count
            }
          } else if (this.times > this.cycle + 10 && ((this.prize === 0 && this.index === 7) || this.prize === this.index +
            1)) {
            this.speed += 110
          } else {
            this.speed += 20
          }
          if (this.speed < 40) {
            this.speed = 40
          }
          this.timer = setTimeout(this.startRoll, this.speed)
        }
      },

      // 每一次转动
      oneRoll() {
        let index = this.index // 当前转动到哪个位置
        const count = this.count // 总共有多少个位置
        index += 1
        if (index > count - 1) {
          index = 0
        }
        this.index = index
        console.log(this.index)
      },
    }
  }
</script>

<style lang="css">
  #app,body{
    background-color: rgba(165, 42, 42, 0)!important;
  }
  img {
    border: 0px;
  }

  ul,
  li {
    list-style-type: none;
  }

  .lottery-box {
    overflow: hidden;
  }

  .lottery-box .title {
    text-align: center;
    padding: 50px 0;
    font-size: 18px;
    color: #fff;
  }

  .lottery {
    animation: changeBg 1s ease infinite;
    overflow: hidden;
    padding: 4vw;
    margin: 0 auto;
    background-repeat: no-repeat;
    background-size: contain;
    height: 92%;
  }

  @keyframes changeBg {
    0% {
      background-image: url(../assets/img/gongge_biue.png);
    }
    100% {
      background-image: url(../assets/img/gongge_yellow.png);
    }
  }

  .lottery .lottery-item {
    height: 100%;
    position: relative;
  }


  /* 10 个宫格处理 */
  .lottery .lottery-item ul .box_sum10 {
    width: 20vw;
    position: absolute;
    padding-right: 10px;
  }

  .lottery .lottery-item ul .box_sum10:nth-child(2) {
    left: 25%;
  }

  .lottery .lottery-item ul .box_sum10:nth-child(3) {
    left: 50%;
  }

  .lottery .lottery-item ul .box_sum10:nth-child(4) {
    left: 75%;
  }

  .lottery .lottery-item ul .box_sum10:nth-child(5) {
    left: 75%;
  }
  .lottery .lottery-item ul .box_sum10:nth-child(5),.lottery .lottery-item ul .box_sum10:nth-child(10) {
    top: 21vw;
  }
  .lottery .lottery-item ul .box_sum10:nth-child(5) .box,.lottery .lottery-item ul .box_sum10:nth-child(10) .box{
    height: 38vw;
  }
  .lottery .lottery-item ul .box_sum10:nth-child(5) .box .gongge_img img,.lottery .lottery-item ul .box_sum10:nth-child(10) .box .gongge_img img {
    margin: 10vw auto 1vw;
  }

  .lottery .lottery-item ul .box_sum10:nth-child(6),.lottery .lottery-item ul .box_sum10:nth-child(7),.lottery .lottery-item ul .box_sum10:nth-child(8),.lottery .lottery-item ul .box_sum10:nth-child(9){
    top: 61vw;
  }
  .lottery .lottery-item ul .box_sum10:nth-child(6) {
    left: 75%;
  }
  .lottery .lottery-item ul .box_sum10:nth-child(7) {
    left: 50%;
  }
  .lottery .lottery-item ul .box_sum10:nth-child(8) {
    left: 25%;
  }
  .lottery .lottery-item ul .box_sum10:nth-child(9) {
    left: 0;
  }
  .lottery .lottery-item ul .box_sum10:nth-child(10) {
    left: 0;
  }
  .lottery .lottery-item ul .box_sum10 .box {
    height: 19vw;
    position: relative;
    text-align: center;
    overflow: hidden;
    background: #6757bc;
    margin: 0.5vw;
    border:1px solid #7e67fc;
    border-radius: 5px;
  }

  .lottery .lottery-item ul .box_sum10 .box .gongge_img img {
    display: block;
    height: 6vw;
    margin: 2vw auto 1vw;
  }

  .lottery .lottery-item ul .box_sum10 .box p {
    color: #FFFFFF;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 0.6rem;
    margin: 0;
  }


  /* 14 个宫格处理 */


  .lottery .lottery-item ul .box_sum14 {
    width: 17vw;
    position: absolute;
    padding-right: 10px;
  }

  .lottery .lottery-item ul .box_sum14:nth-child(2),.lottery .lottery-item ul .box_sum14:nth-child(11) {
    left: 20%;
  }

  .lottery .lottery-item ul .box_sum14:nth-child(3),.lottery .lottery-item ul .box_sum14:nth-child(10) {
    left: 40%;
  }
  .lottery .lottery-item ul .box_sum14:nth-child(4),.lottery .lottery-item ul .box_sum14:nth-child(9) {
    left: 60%;
  }
  .lottery .lottery-item ul .box_sum14:nth-child(5),.lottery .lottery-item ul .box_sum14:nth-child(6),.lottery .lottery-item ul .box_sum14:nth-child(7),.lottery .lottery-item ul .box_sum14:nth-child(8) {
    left: 80%;
  }

  .lottery .lottery-item ul .box_sum14:nth-child(6),.lottery .lottery-item ul .box_sum14:nth-child(14) {
    top: 20vw;
  }
  .lottery .lottery-item ul .box_sum14:nth-child(7),.lottery .lottery-item ul .box_sum14:nth-child(13) {
    top: 40vw;
  }

  .lottery .lottery-item ul .box_sum14:nth-child(8),.lottery .lottery-item ul .box_sum14:nth-child(9),.lottery .lottery-item ul .box_sum14:nth-child(10),.lottery .lottery-item ul .box_sum14:nth-child(11),.lottery .lottery-item ul .box_sum14:nth-child(12){
    top: 60vw;
  }

  .lottery .lottery-item ul .box_sum14 .box {
    height: 19vw;
    position: relative;
    text-align: center;
    overflow: hidden;
    background: #6757bc;
    margin: 0.5vw;
    border:1px solid #7e67fc;
    border-radius: 5px;
  }

  .lottery .lottery-item ul .box_sum14 .box .gongge_img img {
    display: block;
    height: 6vw;
    margin: 2vw auto 1vw;
  }

  .lottery .lottery-item ul .box_sum14 .box p {
    color: #FFFFFF;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 0.6rem;
    margin: 0;
  }

  .lottery .lottery-item ul li.on .box {
    /*background: #7e67fc;*/
    border: 1px solid #51E8F1!important;
  }

  .lottery .lottery-item ul li.on .box p {
    color: #fff;
  }

  .lottery .lottery-item .lottery-start {
    position: absolute;
    left: 21vw;
    width: 40vw;
    height: 38vw;
    top: 22vw;
  }

  .lottery .lottery-item .lottery-start .box {
    width: 36vw;
    font-size: 0.6rem;
    color: #fff;
    cursor: pointer;
    text-align: center;
    overflow: hidden;
    background: url(../assets/img/xingpan.png) no-repeat;
    background-size:auto 100%;
    height: 34vw;
    margin: 2vw auto;
  }

  .lottery .lottery-item .lottery-start .box p b {
    font-size:1rem;
    margin-top: 12vw;
    display: block;
  }

  .lottery .lottery-item .lottery-start .box:active {
    opacity: 0.7;
  }

  .lottery .lottery-item .lottery-start .box.gray {
    background: url(../assets/img/3.png) no-repeat center;
    background-size: 100% 100%;
  }

  .lottery .lottery-item .lottery-start .box.gray p {
    color: #708ABF;
    font-weight: bold;
  }

  .mask {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    position: fixed;
    overflow: hidden;
    z-index: 222;
    top: 0;
    left: 0;
  }

  .lottery-alert {
    max-width: 400px;
    text-align: center;
    z-index: 10000;
    border-radius: 10px;
    background: #fff;
    padding: 20px;
    position: fixed;
    left: 0;
    right: 0;
    margin: auto;
    top: 50%;
    transform: translateY(-50%);
  }

  .lottery-alert h1 {
    font-size: 18px;
    font-weight: bold;
    color: #D92B2F;
  }

  .lottery-alert img {
    display: block;
    height: 120px;
    margin: 0 auto;
  }

  .lottery-alert h2 {
    font-weight: normal;
    color: #D92B2F;
    font-size: 15px;
    padding-top: 15px;
  }

  .lottery-alert p {
    color: #666;
    font-size: 16px;
    padding-top: 5px;
  }

  .lottery-alert .btnsave {
    border-radius: 3px;
    box-shadow: none;
    height: 40px;
    cursor: pointer;
    line-height: 40px;
    color: #fff;
    margin-top: 12px;
    background: linear-gradient(180deg, rgba(213, 60, 63, 1) 0%, rgba(201, 20, 24, 1) 100%);
    font-size: 16px;
  }
</style>



