<template>
  <div id="Index" >
    <!-- 提现 -->
    <van-row>
      <van-col span="20" offset="2" class="withdrawal_div">
        <van-cell title="结算到" is-link :value="pay" @click="click_add_pay"/>
        <van-col span="22" offset="1" class="withdrawal_div_title">结算金额</van-col>
        <van-col span="22" offset="1" class="withdrawal_div_money_top">
          <van-col span="2" class="withdrawal_div_money_title">￥</van-col>
          <van-col span="15"><van-field v-model="money" size="150px"/></van-col>
        </van-col>
        <van-col span="22" offset="1" class="withdrawal_div_income">可提现金额 ￥{{ income }}</van-col>
        <van-col span="14" offset="5" class="withdrawal_div_btn">
          <van-button color="linear-gradient(to right, #ff34ea, #a10aee)" round block  @click="user_withdrawal_btn">
            立即结算
          </van-button>
        </van-col>
      </van-col>
      <van-col span="18" offset="3">
        <div class="withdrawal_text"> {{ notice_text }}</div>
      </van-col>
    </van-row>
    <van-popup v-model:show="add_account" position="bottom" :style="{ height: '70%' }">
      <van-row>
        <van-col span="20" offset="2">
          <van-col span="24" class="add_account_title">绑定提现账号</van-col>
          <van-col span="24" class="add_account_top">
            <van-field v-model="apply_pay" label="支付宝账号" />
            <van-field v-model="apply_name" label="姓名" />
          </van-col>
          <van-col span="24" class="add_account_btn">
            <van-button color="linear-gradient(to right, #ff34ea, #a10aee)" round block  @click="add_account_btn">确定</van-button>
          </van-col>
        </van-col>
      </van-row>

    </van-popup>
  </div>
</template>
<script>
  //请求接口组件
  import '@/assets/css/style.css'
  import axios from 'axios'
  import { Toast } from 'vant'
  export default {
    name: 'withdrawal',
    data:function() {
      return {
        add_account: false,
        money: undefined,
        uid: undefined,
        token: undefined,
        income: undefined,
        notice_text: '',
        pay: undefined,
        name: undefined,
        isBtn: false,
        apply_pay: undefined,
        apply_name: undefined,
      }
    },
    created: function() {
      this.uid = this.$route.query.uid
      this.token = this.$route.query.token
      document.title = '提现'
      this.convert();
    },
    methods: {
      // 获取提现信息
      convert: function() {
        axios.get(this.baseUrlApi+"/vue/wallet_api/withdrawal",{
          params:{
            uid: this.uid,
            token: this.token
          }
        }).then(res =>{
          console.log( this.list)
          if (res.data.code === 1) {
            this.money = undefined
            this.income = res.data.data.income
            this.notice_text = res.data.data.notice_text
            this.pay = res.data.data.pay
            this.name = res.data.data.name
          } else {
            Toast.fail({ message: res.data.msg, duration: 1000 })
            this.click_login()
          }
        })
      },
      user_withdrawal_btn() {
        if (!this.pay) {
          Toast.fail({ message: '请输入绑定提现账号', duration: 1000 })
          return false
        }
        if (!this.money) {
          Toast.fail({ message: '请输入提现金额', duration: 1000 })
          return false
        }
        if (this.isBtn) {
          return false
        }
        this.isBtn = true
          axios.get(this.baseUrlApi+"/vue/wallet_api/cash_income",{
            params:{
              uid: this.uid,
              token: this.token,
              account: this.pay,
              account_name: this.name,
              type: 2,
              money: this.money
            }
          }).then(res =>{
            console.log(res.data.data)
            this.isBtn = false
            if (res.data.code === 0) {
              Toast.fail({ message: res.data.msg, duration: 1000 })
            } else {
              Toast.fail({ message: res.data.msg, duration: 1000 })
              this.convert()
            }
          })
      },
      add_account_btn(){
        if (!this.apply_pay) {
          Toast.fail({ message: '请输入提现账号', duration: 1000 })
          return false
        }
        if (!this.apply_name) {
          Toast.fail({ message: '请输入姓名', duration: 1000 })
          return false
        }
        if (this.isBtn) {
          return false
        }
        this.isBtn = true
        axios.get(this.baseUrlApi+"/vue/wallet_api/apply_withdrawal",{
          params:{
            uid: this.uid,
            token: this.token,
            pay: this.apply_pay,
            name: this.apply_name
          }
        }).then(res =>{
          console.log(res.data.data)
          this.add_account = false
          this.isBtn = false
          if (res.data.code === 0) {
            Toast.fail({ message: res.data.msg, duration: 1000 })
          } else {
            Toast.fail({ message: res.data.msg, duration: 1000 })
            this.convert()
          }
        })
      },
      click_add_pay(){
        this.apply_pay = this.pay
        this.apply_name = this.name
        this.add_account = true
     //   this.$router.push({path:'/add_account',query:{uid: this.uid, token: this.token }});
      },
      click_login(){
        this.$router.push({path:'/login'});
      },
    }

  }
</script>



