<template>
  <div id="guild_ranking">
    <van-nav-bar title="公会排行榜" safe-area-inset-top @click-left="onClickLeft" left-arrow>
            <template #right>
              <van-icon name="search" size="18" @click="searchShow"/>
            </template>
    </van-nav-bar>
    <van-row>
    <van-col span="24" class="guild_ranking_title"><em>集结吧!</em>创造更强大的家族</van-col>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <van-cell v-for="(item,i) in list" :key="i">
        <van-col span="24" :class="i < 3 ? 'guild_ranking_cell guild_ranking_' + i : 'guild_ranking_cell'">
          <van-col span="4" class="guild_ranking_cell_num">
            <img src="../assets/img/guild_ranking_num1.png" v-if="i === 0">
            <img src="../assets/img/guild_ranking_num2.png" v-else-if="i === 1">
            <img src="../assets/img/guild_ranking_num13.png" v-else-if="i === 2">
            <span v-else>{{ i + 1 }}</span>
          </van-col>
          <van-col span="5" class="guild_ranking_cell_img" @click="Jump_guild(item.id)">
            <van-image
              width="8vh"
              height="8vh"
              fit="cover"
              :src="item.logo"
            />
          </van-col>
          <van-col span="8" class="guild_ranking_cell_name" @click="Jump_guild(item.id)">
            <van-col span="24" class="guild_ranking_cell_name_title">{{ item.name }}</van-col>
            <van-col span="24" class="guild_ranking_cell_name_id">家族ID:{{ item.id }}</van-col>
            <van-col span="24" class="guild_ranking_cell_name_ren">人数:{{ item.number }}</van-col>
          </van-col>
          <van-col span="7" class="guild_ranking_cell_add" v-if="is_join === 0 || is_join === item.id">
            <van-button color="linear-gradient(to top, #d69eff, #cb54bb)" round size="small" style="box-shadow: 0px 5px 5px #dbc1f5 ;" @click="add_join_guild(item.id,item.join_status)" v-if="is_join === 0">
              <van-icon name="plus"/>
              <span class="guild_ranking_cell_add_name">加入家族</span>
            </van-button>
            <van-button color="linear-gradient(to top, #d69eff, #cb54bb)" round size="small" style="box-shadow: 0px 5px 5px #dbc1f5 ;" @click="Jump_guild(item.id)" v-else>
              <span class="guild_ranking_cell_add_name">我的家族</span>
            </van-button>
          </van-col>
        </van-col>
      </van-cell>
    </van-list>
    </van-row>
    <van-popup v-model="show" position="top" :style="{ height: '22%' }">
       <van-col span="24" class="show_centent_title">公会搜索</van-col>
      <van-field
        v-model="search_val"
        center
        clearable
        label="公会名"
        placeholder="请输入公会名"
      >
        <template #button>
          <van-button color="linear-gradient(to top, #6aaff3 , #94e4d2)" round size="small" @click="search_btn">搜索</van-button>
          <van-button color="#a9a9a9" round size="small" @click="clear_search">清空</van-button>
        </template>
      </van-field>

    </van-popup>
  </div>
</template>
<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Dialog, Toast } from 'vant'

  export default {
    name: 'GuildRanking',
    data:function() {
      return {
        show: false,
        list: [],
        loading: false,
        finished: false,
        page: 1,
        is_join: 0,
        search: '',
        search_val: '',
      }
    },
    created: function() {

      document.title = '公会排行榜'
    },
    methods: {
      onLoad() {
         this.get_list()
      },
      onClickLeft() {
        // 拦截关闭h5
        window.location.href="live://live?action=back"
      },
      searchShow() {
        this.search_val = this.search
        this.show = true
      },
      search_btn() {
        this.search = this.search_val
        this.page = 1
        this.finished = false
        this.get_list()
        this.show = false
      },
      clear_search() {
        this.search = ''
        this.page = 1
        this.finished = false
        this.get_list()
        this.show = false
      },
      Jump_guild(id) {
        window.location.href="live://live?action=guild&guild_id=" + id
        // const uid = this.$route.query.uid
        // const token = this.$route.query.token
        // this.$router.push({path:'/guild',query:{uid: uid, token: token, guild_id: id }});
      },
      get_list(){
        this.loading = true
        const uid = this.$route.query.uid
        const token = this.$route.query.token
        axios.get(this.baseUrlApi+"/vue/guild_api/guild_ranking",{ params:{ uid: uid, token: token, page: this.page, search: this.search }}).then(res =>{
          if(res.data.code==1){
            if (this.page === 1) {
              this.list = []
            }
            this.is_join = res.data.data.is_join
            const data = res.data.data.list
            if(data.length < 10){
              this.finished = true
            }
            for(let i=0; i< data.length; i++) {
              this.list.push(data[i])
            }

            this.page ++
            this.loading = false
            console.log(this.list)
          }else{
            // 加载状态结束
            this.loading = false
            // 数据全部加载完成
            this.finished = true
            Toast.fail(res.data.msg);
          }
        })
      },
      add_join_guild(id,status) {
        const uid = this.$route.query.uid
        const token = this.$route.query.token

        if (this.is_join === id) {
          const text = this.$t('Tips.Compulsory_withdrawal_center')
          const Whether_quit_guild = this.$t('Tips.Whether_quit_guild')
          Dialog.confirm({
            title: Whether_quit_guild,
            cancelButtonText: this.$t('btn.cancel'),
            confirmButtonText: this.$t('btn.confirm'),
            closeOnClickOverlay: true
          }).then(() => {
            if (status === 1) {
              const Exit_type = this.$t('Tips.Exit_type')
              Dialog.confirm({
                title: Exit_type,
                message: text,
                cancelButtonText: this.$t('btn.tuì_huì'),
                confirmButtonText: this.$t('btn.Compulsory_withdrawal')
              }).then(() => {
                axios.get(this.baseUrlApi+"/vue/user_vue_api/quit_guild_join",{ params:{ uid:uid,token:token, is_force: 1 }}).then(res =>{
                  Toast.fail(res.data.msg);
                  if(res.data.code==1){
                    this.page = 1
                    this.finished = false
                    this.get_list()
                  }
                })
              }).catch(() => {
                axios.get(this.baseUrlApi+"/vue/user_vue_api/quit_guild_join",{ params:{ uid:uid,token:token, is_force: 0 }}).then(res =>{
                  Toast.fail(res.data.msg);
                  if(res.data.code==1){
                    this.page = 1
                    this.finished = false
                    this.get_list()
                  }
                })
              });
            } else {
              axios.get(this.baseUrlApi+"/vue/user_vue_api/quit_guild_join",{ params:{ uid:uid,token:token, is_force: 0 }}).then(res =>{
                Toast.fail(res.data.msg);
                if(res.data.code==1){
                  this.page = 1
                  this.finished = false
                  this.get_list()
                }
              })
            }
          }).catch(() => {
            // on cancel
          });
        }else{
          Dialog.confirm({
            title: "申请加入家族",
            message: "是否加入本家族？",
            cancelButtonText: this.$t('btn.cancel'),
            confirmButtonText: this.$t('btn.confirm')
          }).then(() => {

            axios.get(this.baseUrlApi+"/vue/guild_api/add_join_guild",{ params:{ uid:uid,token:token, guild_id: id }}).then(res =>{
              Toast.fail(res.data.msg);
              if(res.data.code==1){
                this.page = 1
                this.finished = false
                this.get_list()
              }
            })

          }).catch(() => {
            // on cancel
          });
        }
      }
    }
  }

</script>

<style>
</style>



