<template>
  <div id="">
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
    <van-list
      v-model="loading"
      :finished="finished"
      :finished-text="$t('Tips.no_more')"
      :loading-text="$t('Tips.loading')"
      @load="onLoad"
    >
      <div class="attention-box" v-for="(val,key) in list">
        <div class="attention-box-a">
          <img :src="val.avatar" alt="">
        </div>
        <div class="attention-box-b">
          <div class="attention-box-b-name">
            <div class="attention-box-b-name-a">{{val.user_nickname}}</div>
            <div v-if="val.sex==1" class="attention-box-b-name-b">♂{{val.age}}</div>
            <div v-if="val.sex==2" class="attention-box-b-name-c">♀{{val.age}}</div>
          </div>
          <div class="attention-box-b-id">ID：{{val.id}}</div>
        </div>
        <div class="attention-box-c">
          <div v-if="val.focus==0" class="fans-box-c-default" @click="delAtt(key,val.id)">{{ $t('btn.follow') }}</div>
        </div>
      </div>
      <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
    </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        list:[],
        info:[],
        privilege:[],
        loading:false,
        finished:false,
        refreshing: false,
        page:1,
      }
    },
    created: function() {
      //this.convert();
      //this.get_list();
      document.title = this.$t('title.fans')
    },
    methods: {
      get_list(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/vue/user_vue_api/get_attention_vue",{ params:{ uid:uid,token:token }}).then(res =>{
           console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             var list = res.data.data
             _this.list = list
             _this.page = 2;
             /* for(var i =0;i<list.length;i++){

             } */
             /* var _this = this;
             _this.list = res.data.data.noble_list
             _this.info = res.data.data.noble_info
             _this.privilege = res.data.data.privilege
             _this.id =  _this.info.id
             _this.currency_name =  res.data.data.currency_name
             _this.coin = res.data.data.coin */
             //console.log(_this.list)
             //this.bank_name = list[0]['name']
           }

        })
      },
      onLoad(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var page = this.page;

        axios.get(this.baseUrlApi+"/vue/user_vue_api/get_attention_vue",{ params:{ uid:uid,token:token,page:page,type:2 }}).then(res =>{
           //console.log(res.data)
           this.loading = false;
           if(res.data.code==1){
             if(res.data.data==''){
               this.finished = true
             }
             var _this = this;
             var list = res.data.data
             //_this.list = list
             _this.page++;
             for(var i =0;i<list.length;i++){
              _this.list.push(list[i])
             }
           }else{
             this.finished = true
           }
           this.loading = false;

        })
      },
      delAtt(keys,id){
        var list = this.list;
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var page = this.page;

        axios.get(this.baseUrlApi+"/vue/user_vue_api/click_attention_vue",{ params:{ uid:uid,token:token,id:id }}).then(res =>{
           //console.log(res.data)
           //this.loading = false;
           if(res.data.code==1){
             Toast.success(res.data.msg);
             //list.splice(keys,1);
             list[keys]['focus'] = 1;
           }else{
             Toast.fail(res.data.msg);
           }
           //console.log(page)

        })
      },
      onRefresh(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var page = 1;
        this.finished = false;
        // 重新加载数据
        // 将 loading 设置为 true，表示处于加载状态
        //this.loading = true;

        axios.get(this.baseUrlApi+"/vue/user_vue_api/get_attention_vue",{ params:{ uid:uid,token:token,page:page,type:2 }}).then(res =>{
           //console.log(res.data)
           this.refreshing = false;
           if(res.data.code==1){
             if(res.data.data==''){
               this.refreshing = true
             }
             var _this = this;
             var list = res.data.data
             _this.list = list
           }else{
             this.refreshing = true
           }
           //console.log(page)

        })
      }
    }
  }

</script>

<style>
</style>



