<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        post_content: "",
        list: [],

      }
    },
    created: function() {
      //this.convert();
      this.get_info();
      document.title = this.$t('title.friend_info')
    },
    methods: {
      get_info(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/vue/about_api/friend_level",{ params:{ uid:uid,token:token }}).then(res =>{
           console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             //_this.list = res.data.data.noble_list
             _this.post_content = res.data.data

           }else{
             Toast.fail(res.data.msg);
           }

        })
      },
      href_log(data){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //window.location.href = data;
        this.$router.push({path:'/'+data+'',query:{uid:uid, token: token }});
      }
    }
  }

</script>

