<template>
  <div id="">
    <div class="gift-log-top">
      <div class="gift-log-top-l gift-log-top-active">商城礼物</div>
      <div class="gift-log-top-r" @click="href_log()">背包礼物</div>
    </div>
    <div class="gift-log-top-bottom"> </div>
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
    <van-list
      v-model="loading"
      :finished="finished"
      :finished-text="$t('Tips.no_more')"
:loading-text="$t('Tips.loading')"
      @load="onLoad"
    >
      <div class="" v-for="(val,key) in list" :key="val.id">
          <div class="gift-log-box">
              <div class="gift-log-box-l">
                <img :src="val.img" alt="">
              </div>
              <div class="gift-log-box-c">
                <div class="gift-log-box-c-t">{{val.gift_name}}</div>
                <div class="gift-log-box-c-b">送礼人 {{val.user_nickname}}</div>
              </div>
              <div class="gift-log-box-r">
                <div class="gift-log-box-r-t">{{val.profit}}</div>
                <div class="gift-log-box-r-b">{{val.addtime}}</div>
              </div>
          </div>
      </div>
      <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
    </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        list:[],
        info:[],
        privilege:[],
        loading:false,
        finished:false,
        refreshing: false,
        page:1,
      }
    },
    created: function() {
      //this.convert();
      //this.get_list();
      document.title = '收礼物记录'
    },
    methods: {
      onLoad(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var page = this.page;

        axios.get(this.baseUrlApi+"/vue/user_vue_api/get_gift_log",{ params:{ uid:uid,token:token,page:page,type:1 }}).then(res =>{
           //console.log(res.data)
           //this.loading = false;
           if(res.data.code==1){
             if(res.data.data==''){
               this.loading = false
               this.finished = true
               this.refreshing = false
               return
             }
             var _this = this;
             var list = res.data.data
             //_this.list = list
             _this.page++;
             for(var i =0;i<list.length;i++){
              _this.list.push(list[i])
             }
           }
           //this.finished = true
           this.refreshing = false
           this.loading = false;
        })
      },
      onRefresh(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var page = 1;
        this.finished = false;
        // 重新加载数据
        // 将 loading 设置为 true，表示处于加载状态
        //this.loading = true;

        axios.get(this.baseUrlApi+"/vue/user_vue_api/get_gift_log",{ params:{ uid:uid,token:token,page:page,type:1 }}).then(res =>{
           //console.log(res.data)
           this.refreshing = false;
           if(res.data.code==1){
             if(res.data.data==''){
               this.refreshing = true
             }
             var _this = this;
             var list = res.data.data
             _this.list = list
           }else{
             this.refreshing = true
           }
           //console.log(page)

        })
      },
      href_log(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //window.location.href = data;
        this.$router.push({path:'/receive_gift_log',query:{uid:uid, token: token }});

      },

    }
  }

</script>

<style>
  #app,body{
    background-color: #F5F7FA;
  }

</style>



