<template>
  <div id="">
    <img class="system-activity-info" :src="img" alt="">
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        img: '',
        title: '活动详情',

      }
    },
    created: function() {
      //this.convert();
      this.getInfo();

    },
    methods: {
      getInfo(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        var id = this.$route.query.id;
        axios.get(this.baseUrlApi+"/vue/system_message_api/get_system_activity_info",{ params:{ uid:uid,token:token,id:id }}).then(res =>{
           // console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             //var list = res.data
             _this.img = res.data.data.url
             _this.title = res.data.data.title
             document.title = this.title
           }else{
             //this.finished = true
           }

        })
      },
      href(data){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;

        window.location.href=data+'?uid='+uid+'&token='+token
        //window.location.href = data;
        //this.$router.push({path:'/system_content',query:{uid:uid, token: token,id:id }});
      },

    }
  }

</script>

<style>
  #app,body{
    background-color: #F5F7FA;
  }
  .system-activity-info{
    width: 100%;
  }
</style>



