// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Element from 'element-ui'
import Vant from 'vant'
import 'vant/lib/index.css';
import App from './App'
import router from './router'
import 'element-ui/lib/theme-chalk/index.css'
import i18n from './assets/i18n'
import { GetUrlParam } from "./utils/GetUrlParam.js"

Vue.use(Vant)
Vue.use(Element)
Vue.config.productionTip = false
Vue.prototype.baseUrl = '';
// Vue.prototype.baseUrl = "https://app.topchatapp.com"

Vue.prototype.baseUrlApi = Vue.prototype.baseUrl + ""
let language = GetUrlParam("lang")

if (language === undefined) {
  language =GetUrlParam("language")
}

import { Locale } from 'vant';

// 引入语言包
import mongolian from './assets/lang/mongolian'
import enUS from 'vant/es/locale/lang/en-US'
import zh_CN from 'vant/es/locale/lang/zh-CN'
import ar from './assets/lang/ar'


if (language !== undefined) {
  localStorage.setItem('api_lang', language)
  localStorage.setItem('lang', language)
  let langs = language
  if(language === 'mongolian') {
    Locale.use('mongolian', mongolian)
    localStorage.setItem('lang', mongolian)

  } else if (language === 'zh-cn' || language === 'zh') {
    // 引入中文语言包
    Locale.use('zh-CN', zh_CN)
    langs = 'zh-cn'
    localStorage.setItem('lang', 'zh-cn')
    localStorage.setItem('api_lang', 'zh-cn')
  } else if (language === 'ar') {
    // 引入越南语言包
    Locale.use('ar', ar)
  }else{
 // } else if (language === 'en-us' || language === 'en') {

    // 引入英文语言包
    Locale.use('en-US', enUS)
    langs = 'en-us'
    localStorage.setItem('lang', 'en')
    localStorage.setItem('api_lang', 'en')
  }

  i18n.locale = langs
  i18n.fallbackLocale = langs
}
import axios from 'axios'
const api_language = localStorage.getItem('api_lang')
axios.interceptors.request.use(config => {
  // 为请求头添加x-access-token字段为服务端返回的token
 // config.headers['lang'] = api_language
  config.params['lang'] = api_language
  // return config是固定用法 必须有返回值
  console.log(config)
  return config
})
/* eslint-disable no-new */
router.beforeEach((to, from, next) => {
  // 这里可以编写拦截逻辑

  // 获取目标路由的query参数
  to.query['lang']= language
  console.log('router拦截器')
  console.log(to.query)
  console.log('router拦截器结束')
  // 已经登录或符合白名单则正常跳转
  return next();
})
new Vue({
  el: '#app',
  router,
  i18n,
  components: { App },
  template: '<App/>',
})

// 页面修改时修改浏览器标签栏
router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})



