<template>
  <div id="exchange_income_log">
    <van-nav-bar :title="$t('column.Revenue_records')" safe-area-inset-top @click-left="onClickLeft" left-arrow/>
    <van-row>
      <van-list
          v-model="loading"
          :finished="finished"
          :finished-text="$t('Tips.no_more')"
          :loading-text="$t('Tips.loading')"
          @load="onLoad"
      >
        <van-cell v-for="(item,i) in list" :key="i">
          <van-col span="24" class="exchange_income_log_cell">
            <van-col span="16">
              <van-col span="24" class="exchange_income_log_cell_title"> {{ item.text }} </van-col>
              <van-col span="24" class="exchange_income_log_cell_time"> {{ item.create_time }} </van-col>
            </van-col>
            <van-col span="8" class="exchange_income_log_cell_coin">{{ item.coin_text }}</van-col>
          </van-col>
        </van-cell>
      </van-list>
    </van-row>
  </div>
</template>
<script>
import '@/assets/css/style_a.css'
import axios from 'axios'
import { Dialog, Toast } from 'vant'

export default {
  data:function() {
    return {
      list: [],
      loading: false,
      finished: false,
      page: 1,
    }
  },
  created: function() {
  },
  methods: {
    onLoad() {
      this.get_list()
    },
    onClickLeft() {
      // 拦截关闭h5
      this.$router.back()
    },
    get_list(){
      this.loading = true
      const uid = this.$route.query.uid
      const token = this.$route.query.token
      axios.get(this.baseUrlApi+"/vue/exchange_api/earnings_record",{ params:{ uid: uid, token: token, page: this.page }}).then(res =>{
        if(res.data.code==1){
          if (this.page === 1) {
            this.list = []
          }
          const data = res.data.data.list
          if(data.length < 10){
            this.finished = true
          }
          for(let i=0; i< data.length; i++) {
            this.list.push(data[i])
          }

          this.page ++
          this.loading = false
          console.log(this.list)
        }else{
          // 加载状态结束
          this.loading = false
          // 数据全部加载完成
          this.finished = true
          Toast(res.data.msg);
        }
      })
    }
  }
}

</script>

<style>
</style>



