<template>
  <div id="guild_description_choose">
    <van-nav-bar
      class="nav-pad-top"
      left-arrow
      fixed
      safe-area-inset-top
      @click-left="onClickLeft"
    >
      <template #left>
        <van-icon name="arrow-left" size="18" color="#1A1A1A"/>
      </template>
      <template #title>
        <span class="guild_description_choose_color">{{ $t('guild.Agency_application') }}</span>
      </template>
    </van-nav-bar>
    <van-row>
      <van-col span="22" offset="1" class="guild_description_apply_title guild_description_apply_top">
        * {{ $t('guild.phone_number') }}
      </van-col>
      <van-col span="22" offset="1" class="guild_description_apply_input">
        <van-field v-model="tel"  :placeholder="$t('guild.Please_enter_phone_number')"/>
      </van-col>

      <van-col span="22" offset="1" class="guild_description_apply_title">
        {{ $t('guild.know_agency') }}
      </van-col>
      <van-col span="22" offset="1" class="guild_description_apply_input">
        <van-field v-model="company" type="textarea" rows="2" show-word-limit
                   autosize maxlength="100" :placeholder="$t('guild.i_saw_agency')"/>
      </van-col>

      <van-col span="22" offset="1" class="guild_description_apply_id">
       {{ $t('guild.anyone_recommend_agency') }}
      </van-col>
      <van-col span="22" offset="1" class="guild_description_apply_input">
        <van-field v-model="agency"  :placeholder="$t('guild.fill_her_id')"/>
      </van-col>
      <van-col span="22" offset="1" class="guild_description_apply_reminder">
        * {{ $t('guild.Friendly_Reminder') }}:
      </van-col>
      <van-col span="22" offset="1" class="guild_description_apply_reminder_center">
        {{ $t('guild.Fill_in_above_answer') }}
      </van-col>
      <van-col span="22" offset="1" class="guild_description_apply_checkbox">
        <van-button color="linear-gradient(to right, #F86C2F, #CA26ED)" round size="large" @click="ApplyJoin">{{ $t('guild.Apply_join') }}</van-button>
      </van-col>
    </van-row>
  </div>

</template>
<script>
import '@/assets/css/style_a.css'
import axios from 'axios'
import {Dialog, Toast} from 'vant'
export default {
  data:function() {
    return {
      tel: '',
      company: '',
      agency: ''
    }
  },
  created: function() {
    document.title = this.$t('guild.Agency_application')
  },
  methods: {
    onClickLeft() {
      // 返回上一页
      const token = this.$route.query.token
      const uid = this.$route.query.uid
      this.$router.push({path:'/GuildDescriptionChoose',query:{ uid: uid, token: token }})
    },
    ApplyJoin(){
      const uid = this.$route.query.uid
      const token = this.$route.query.token
      const guild_id = this.$route.query.guild_id
      if (this.tel.length <= 0) {
        Toast(this.$t('guild.Please_enter_phone_number'))
      }
      Dialog.confirm({
        message: this.$t('guild.Apply_admission_guild'),
        cancelButtonText: this.$t('btn.cancel'),
        confirmButtonText: this.$t('btn.confirm'),
        closeOnClickOverlay: true
      }).then(() => {
        axios.get(this.baseUrlApi+"/vue/guild_api/add_guild_join",{
          params:{
            uid: uid,
            token: token,
            guild_id: guild_id,
            tel: this.tel,
            introduction: this.company,
            agency_id:  this.agency_id
          }}).then(res =>{
          Toast(res.data.msg)
          if(res.data.code==1){
            setTimeout(() =>{
              window.location.href="live://live?action=close"
            },3000)
          }
        }).catch(() => {})
        return
      })
    }
  }
}

</script>

<style>

</style>



