<template>
  <div id="app">
    <div class="my-guild-top">
      <img class="my-guild-top-b" :src="info.logo" alt="">
    </div>
    <div class="my-guild-content">
      <div class="my-guild-top-a">{{ $t('column.Guild_ID')}}：{{info.id}}</div>
      <div class="my-guild-top-a">{{ $t('column.Guild_name') }}：{{info.name}}</div>
      <div class="my-guild-top-a">{{ $t('column.Guild_members') }}：{{info.user_count}}</div>
      <div class="my-guild-top-a">{{ $t('column.Guild_time')}}：{{info.addtime}}</div>
    </div>
    <div v-if="parseInt(info.user_id) !== parseInt(uid)">
        <div class="guild_button" v-if="info.quit_guild">{{ $t('column.Withdrawal_application') }}</div>
        <div class="guild_button" @click="guild_join_quit" v-else> {{ $t('btn.tuì_huì') }} </div>
    </div>

  </div>
</template>
<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import {Dialog, Toast} from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        list:[],
        info:[],
        uid: undefined,
      }
    },
    created: function() {
      //this.convert();
        this.uid = this.$route.query.uid
      this.get_list();
      document.title = this.$t('title.my_guild')
    },
    methods: {
      get_list(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/vue/user_vue_api/request_guild_info",{ params:{ uid:uid,token:token }}).then(res =>{
           //console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             //_this.list = res.data.data.list
             _this.info = res.data.data
             //_this.privilege = res.data.data.privilege
             //_this.id =  _this.info.id
             //_this.currency_name =  res.data.data.currency_name
             //_this.friend_name = res.data.data.friend_name
             //console.log(_this.list)
             //this.bank_name = list[0]['name']
           }else{
             Toast.fail(res.data.msg);
           }

        })
      },
      guild_join_quit() {
        const uid = this.$route.query.uid;
        const token = this.$route.query.token;
        const text = this.$t('Tips.Compulsory_withdrawal_center')
        const Whether_quit_guild = this.$t('Tips.Whether_quit_guild')
        const Exit_type = this.$t('Tips.Exit_type')
        Dialog.confirm({
          title: Whether_quit_guild,
          cancelButtonText: this.$t('btn.cancel'),
          confirmButtonText: this.$t('btn.confirm'),
          closeOnClickOverlay: true
        }).then(() => {
          Dialog.confirm({
            title: Exit_type,
            message: text,
            cancelButtonText: this.$t('btn.tuì_huì'),
            confirmButtonText: this.$t('btn.Compulsory_withdrawal')
          }).then(() => {
            axios.get(this.baseUrlApi+"/vue/user_vue_api/quit_guild_join",{ params:{ uid:uid,token:token, is_force: 1 }}).then(res =>{
              Toast.fail(res.data.msg);
              if(res.data.code==1){
                const _this = this;
                _this.info = res.data.data
              }
            })
          }).catch(() => {
            axios.get(this.baseUrlApi+"/vue/user_vue_api/quit_guild_join",{ params:{ uid:uid,token:token, is_force: 0 }}).then(res =>{
              Toast.fail(res.data.msg);
              if(res.data.code==1){
                const _this = this;
                _this.info = res.data.data
              }
            })
          });
        }).catch(() => {
        });
      }
    }
  }

</script>

<style>
</style>



