<template>
  <div id="room_ranking">
    <van-nav-bar :title="$t('title.ranking')" fixed safe-area-inset-top @click-left="onClickLeft" left-arrow />
    <van-row>
      <van-col span="24" class="room_ranking_top" safe-area-inset-top>
        <van-col span="18" offset="3">
          <van-tabs v-model="active" type="card" color="#ffffff" background="#33344D" title-active-color="#5D17EB"	title-inactive-color="#ffffff" line-width="100px" @change="ChangeCard">
            <van-tab :title="$t('column.Daily')" name="0"></van-tab>
            <van-tab :title="$t('column.Weekly')" name="1"></van-tab>
          </van-tabs>
        </van-col>
      </van-col>
      <van-list
        v-model="loading"
        :finished="finished"
        :finished-text="$t('Tips.no_more')"
        :loading-text="$t('Tips.loading')"
        class="room_ranking_list"
        @load="onLoad"
      >
        <van-cell :key="-1" v-if="list.length">
          <van-col span="24" class="room_ranking_cell_1">
            <div class="room_ranking_van_cell1" v-if="list.length> 0" @click="Jump_userinfo(list[0].id)">
              <van-col span="24" class="room_ranking_cell_1_avatar">
                <van-image
                  width="60"
                  height="60"
                  fit="cover"
                  class="room_ranking_cell_1_avatar_src"
                  :src="list[0].avatar"
                />
                <img src="../assets/img/ranking_room1.png" class="room_ranking_cell_1_avatar_bg"/>
              </van-col>
              <van-col span="22" offset="1">
                <van-col span="12" class="room_ranking_cell_1_name">
                  {{ list[0].user_nickname }}
                </van-col>
                <van-col span="12" class="room_ranking_cell_1_sex">
                  <img src="../assets/img/sex1.png" v-if="list[0].sex === 1"/>
                  <img src="../assets/img/sex2.png" v-else/>
                  <div class="room_ranking_cell_1_sex_age">{{ list[0].age }}</div>
                </van-col>
              </van-col>
              <van-col span="22" offset="1">
                <van-col span="12" class="room_ranking_cell_1_sex_l">
                  <img :src="list[0].level_img"/>
                </van-col>
                <van-col span="12" class="room_ranking_cell_1_sex">
                  <img :src="list[0].income_level_img"/>
                </van-col>
              </van-col>
              <van-col span="22" offset="1" class="room_ranking_cell_1_red_heart">
                {{ list[0].total_diamonds | get_money }}  <img src="../assets/img/red_heart.png"/>
              </van-col>
            </div>

            <div class="room_ranking_van_cell2" v-if="list.length> 1" @click="Jump_userinfo(list[1].id)">
              <van-col span="24" class="room_ranking_cell_2_avatar">
              <van-image
                width="60"
                height="60"
                fit="cover"
                class="room_ranking_cell_1_avatar_src"
                :src="list[1].avatar"
              />
              <img src="../assets/img/ranking_room2.png" class="room_ranking_cell_1_avatar_bg"/>
            </van-col>
              <van-col span="22" offset="1">
                <van-col span="12" class="room_ranking_cell_1_name">
                  {{ list[1].user_nickname }}
                </van-col>
                <van-col span="12" class="room_ranking_cell_1_sex">
                  <img src="../assets/img/sex1.png" v-if="list[1].sex === 1"/>
                  <img src="../assets/img/sex2.png" v-else/>
                  <div class="room_ranking_cell_1_sex_age">{{ list[1].age }}</div>
                </van-col>
              </van-col>
              <van-col span="22" offset="1">
                <van-col span="12" class="room_ranking_cell_1_sex_l">
                  <img :src="list[1].level_img"/>
                </van-col>
                <van-col span="12" class="room_ranking_cell_1_sex">
                  <img :src="list[1].income_level_img"/>
                </van-col>
              </van-col>
              <van-col span="22" offset="1" class="room_ranking_cell_1_red_heart">
                {{ list[1].total_diamonds | get_money }}  <img src="../assets/img/red_heart.png"/>
              </van-col>
            </div>
            <div class="room_ranking_van_cell3" v-if="list.length> 2" @click="Jump_userinfo(list[2].id)">
              <van-col span="24" class="room_ranking_cell_2_avatar">
                <van-image
                  width="60"
                  height="60"
                  fit="cover"
                  class="room_ranking_cell_1_avatar_src"
                  :src="list[2].avatar"
                />
                <img src="../assets/img/ranking_room3.png" class="room_ranking_cell_1_avatar_bg"/>
              </van-col>
              <van-col span="22" offset="1">
                <van-col span="12" class="room_ranking_cell_1_name">
                  {{ list[2].user_nickname }}
                </van-col>
                <van-col span="12" class="room_ranking_cell_1_sex">
                  <img src="../assets/img/sex1.png" v-if="list[2].sex === 1"/>
                  <img src="../assets/img/sex2.png" v-else/>
                  <div class="room_ranking_cell_1_sex_age">{{ list[2].age }}</div>
                </van-col>
              </van-col>
              <van-col span="22" offset="1">
                <van-col span="12" class="room_ranking_cell_1_sex_l">
                  <img :src="list[2].level_img"/>
                </van-col>
                <van-col span="12" class="room_ranking_cell_1_sex">
                  <img :src="list[2].income_level_img"/>
                </van-col>
              </van-col>
              <van-col span="22" offset="1" class="room_ranking_cell_1_red_heart">
                {{ list[2].total_diamonds | get_money}}  <img src="../assets/img/red_heart.png"/>
              </van-col>
            </div>
          </van-col>
        </van-cell>
        <van-cell v-for="(item,i) in list" :key="i" v-if="i>2"  @click="Jump_userinfo(item.id)">
          <van-col span="24">
            <van-col span="4" class="room_ranking_cell_num"> {{ item.order_num }}  </van-col>
            <van-col span="5" class="room_ranking_cell_img">
              <van-image
                width="8vh"
                height="8vh"
                fit="cover"
                :src="item.avatar"
              />
            </van-col>
            <van-col span="10" class="room_ranking_cell_name">
              <van-col span="24" class="room_ranking_cell_name_val">
               <div class="room_ranking_cell_name_val_nickname"> {{ item.user_nickname }}</div>
                <div class="room_ranking_cell_name_val_sex">
                  <img src="../assets/img/sex1.png" v-if="item.sex === 1"/>
                  <img src="../assets/img/sex2.png" v-else/>
                  <div class="room_ranking_cell_name_sex_age">{{ item.age }}</div>
                </div>
              </van-col>
              <van-col span="22" offset="1" class="room_ranking_cell_name_level">
                  <img :src="item.level_img"/>
                  <img :src="item.income_level_img"/>
              </van-col>
            </van-col>
            <van-col span="5" class="room_ranking_cell_coin" >
              {{ item.total_diamonds | get_money }}  <img src="../assets/img/red_heart.png"/>
            </van-col>
          </van-col>
        </van-cell>
      </van-list>
      <div class="room_ranking_bottom">
        <van-col span="22" offset="1">
          <van-col span="4" class="room_ranking_bottom_img">
            <van-image
              width="6vh"
              height="6vh"
              fit="cover"
              :src="user.avatar"
            />
          </van-col>
          <van-col span="15" class="room_ranking_bottom_name">
            <van-col span="24" class="room_ranking_bottom_name_val"> {{ user.user_nickname }} </van-col>
            <van-col span="24" class="room_ranking_bottom_order_num"> {{ user.order_num }} </van-col>
          </van-col>
          <van-col span="5" class="room_ranking_cell_join_list" >
            <van-button color="linear-gradient(to right, #5D17EB, #8C52FE)" round size="small" block @click="Jump_join"> {{ $t('column.Challenge_list') }} </van-button>
          </van-col>
        </van-col>
      </div>
    </van-row>
  </div>
</template>
<script>
import '@/assets/css/style_a.css'
import axios from 'axios'
import { Toast } from 'vant'

export default {
  filters: {
    get_money(active_type) {
      return active_type > 1000 ?  (active_type / 1000).toFixed(1) + 'K' : active_type
    }
  },
  data:function() {
    return {
      active: 0,
      show: false,
      list: [],
      loading: false,
      finished: false,
      user: {}
    }
  },
  created() {
    this.get_list()
  },
  methods: {
    ChangeCard(){
      // 切换
      console.log(this.active)
      this.get_list()
    },
    onLoad() {},
    onClickLeft() {
      // 拦截关闭h5
      window.location.href="live://live?action=close"
    },
    Jump_join() {
      window.location.href="live://live?action=ranking"
    },
    Jump_userinfo(id) {
      window.location.href="live://live?action=Userinfo&id=" + id
    },
    get_list(){
      this.loading = true
      const uid = this.$route.query.uid
      const token = this.$route.query.token
      const room_id = this.$route.query.room_id
      axios.get(this.baseUrlApi+"/vue/ranking_api/room_ranking",{ params:{ uid: uid, token: token, room_id: room_id, type: this.active }}).then(res =>{
        if(res.data.code===1){
          if (this.page === 1) {
            this.list = []
          }
          this.user = res.data.data.user
          this.list = res.data.data.list
          this.finished = true
          this.loading = false
        }else{
          // 加载状态结束
          this.loading = false
          // 数据全部加载完成
          this.finished = true
          Toast.fail(res.data.msg);
        }
      })
    }
  }
}

</script>

<style>
</style>



