<template>
  <div id="exchange">
    <van-nav-bar :title="$t('title.Exchange')" safe-area-inset-top @click-left="onClickLeft" left-arrow>
      <template #right>
        <span class="exchange_click_log" @click="onClickRight">{{ $t('column.exchange_records') }}</span>
      </template>
    </van-nav-bar>
    <van-row>
      <van-col span="22" offset="1">
        <van-col span="12" class="exchange_income_name">
          {{ earnings_name }} <span>{{ user_info.income }}</span>
        </van-col>
        <van-col span="12" class="exchange_income_log" @click="exchange_income_log">
          {{ $t('column.Revenue_records') }} <van-icon name="arrow" />
        </van-col>
      </van-col>
      <van-col span="24" class="exchange_divider"></van-col>
      <van-col span="22" offset="1" class="exchange_select_title">{{ $t('column.Exchange') }} {{ coin_name }}</van-col>
      <van-col span="22" offset="1" class="exchange_select_grid">
        <van-grid :border="false" :column-num="3">
          <van-grid-item v-for="(item,i) in list" :key="i" @click="exchange_grid(i)">
            <van-col span="24" :class="exchange_key === i ? 'exchange_select_grid_checked exchange_select_grid_type' : 'exchange_select_grid_type'">
              <van-col span="24" class="exchange_select_grid_type_coin"> {{ item.coin }} <img src="../assets/img/diamond_1.png"/> </van-col>
              <van-col span="24" class="exchange_select_grid_type_consumption"> {{ item.earnings }} {{ earnings_name }} </van-col>
            </van-col>
          </van-grid-item>
        </van-grid>
      </van-col>
      <van-col span="22" offset="1" class="exchange_select_input" v-if="open_custom_exchange">
        <van-col span="8">
          <van-field v-model="exchange_income" type="digit" placeholder="100" @blur="exchange_income_input" class="exchange_select_input_left" />
        </van-col>
        <van-col span="6" class="exchange_select_input_center">
          {{ earnings_name }} =
        </van-col>
        <van-col span="8" class="exchange_select_input_center">
         {{ coin }}
        </van-col>
        <van-col span="2" class="exchange_select_input_img">
          <img src="../assets/img/diamond_1.png"/>
        </van-col>
      </van-col>
      <van-col span="22" offset="1" class="exchange_select_title" v-if="is_exchange">{{ $t('column.Help_friends_redeem') }}</van-col>
      <van-col span="22" offset="1" v-if="is_exchange">
        <van-field v-model="friend_uid" type="digit" :placeholder="$t('column.Redeem_yourself_without_filling')" class="exchange_select_friend"/>
      </van-col>
      <van-col span="22" offset="1" class="exchange_btn">
        <van-button color="linear-gradient(to left, #864BF8, #ED6491)" round  size="large" @click="exchange_btn">  {{ $t('column.Confirm_redemption') }}  </van-button>
      </van-col>
      <van-col span="22" offset="1" class="exchange_rule" v-html="post_content"> </van-col>
    </van-row>
  </div>
</template>

<script>

  //请求接口组件
  import '@/assets/css/style.css'
  import axios from 'axios'
  import {Dialog, Toast} from "vant";
  export default {
    name: 'Index',
    data:function() {
      return {
        exchange_key: 0,
        list:[],
        user_info: {},
        coin_name: '',
        earnings_name: '',
        open_custom_exchange: 0,
        integral_coin: 0,
        is_exchange: 0,
        post_content: '',
        minimum_exchange_quantity: 0,
        coin: '',
        exchange_income: '',
        friend_uid: ''
      }
    },
    created: function() {
      this.get_list();
    },
    methods: {
      onClickRight() {
        const uid = this.$route.query.uid
        const token = this.$route.query.token
        this.$router.push({path:'/exchange_log',query:{uid: uid, token: token }});
      },
      onClickLeft() {
        // 拦截关闭h5
        window.location.href="live://live?action=back"
      },
      exchange_income_log(){
        const uid = this.$route.query.uid
        const token = this.$route.query.token
        this.$router.push({path:'/exchange_gift_log',query:{uid: uid, token: token }});
      },
      // 兑换列表
      exchange_grid(i){
        this.exchange_key = i
        // this.exchange_income = this.list[i].earnings
        // this.coin = this.list[i].coin
      },
      exchange_income_input() {
       // 自定义
        this.coin = parseInt(this.exchange_income / this.integral_coin)
      },
      // 确认兑换
      exchange_btn(){
        const uid = this.$route.query.uid;
        const token = this.$route.query.token;
        Dialog.confirm({
          message: this.$t('column.is_Confirm_redemption'),
          confirmButtonText: this.$t('btn.confirm'),
          cancelButtonText: this.$t('btn.cancel')
        }).then(() => {
          // on confirm
          axios.get(this.baseUrlApi+"/vue/exchange_api/exchange_post",{
            params:{
              uid:uid,
              token:token,
              touid: this.friend_uid,
              exchange_income: this.exchange_income,
              id: this.list[this.exchange_key].id
            }
          }).then(res =>{
            Toast(res.data.msg)
            if (res.data.code === 1) {
              this.get_list()
            }
          })
        }).catch(() => {
          // on cancel
        });
      },
      get_list() {
        const uid = this.$route.query.uid;
        const token = this.$route.query.token;
        axios.get(this.baseUrlApi+"/vue/exchange_api/index",{
          params:{
            uid:uid,
            token:token
          }
        }).then(res =>{
          if (res.data.code === 1) {
            this.list = res.data.data.list
            this.user_info = res.data.data.user_info
            this.coin_name = res.data.data.coin_name
            this.earnings_name = res.data.data.earnings_name
            this.open_custom_exchange = res.data.data.open_custom_exchange
            this.integral_coin = res.data.data.integral_coin
            this.is_exchange = res.data.data.is_exchange
            this.post_content = res.data.data.post_content
            this.minimum_exchange_quantity = res.data.data.minimum_exchange_quantity
          } else {
            Toast(res.data.msg)
          }
        })
      }
    }
  }
</script>



