<template>
  <div id="disguise">
    <van-row>
      <van-col span="24">
        <van-tabs @click="onClickType">
          <van-tab v-for="index in title" :key="index" :title="index"/>
        </van-tabs>
      </van-col>
      <van-col span="22" offset="1" class="disguise_bg">

        <van-col span="24" class="disguise_bg_img">
          <van-col span="24" class="disguise_bg_img_avatar" v-if="keys === 2">
            <img :src="avatar_img" @error="errorImgSave"/>
          </van-col>
            <van-col span="24" class="disguise_bg_img_icon">
              <img :src="head_img" @error="errorImgSave"/>
            </van-col>
        </van-col>
      </van-col>

      <van-col span="22" offset="1" class="disguise_list">
        <van-row gutter="20">
          <van-col span="12" v-for="(item,i) in list" :key="i">
            <van-col span="24" :class="list_i === i ? 'disguise_list_border disguise_list_border_checked' : 'disguise_list_border'" @click="disguise_border_btn(i)">
              <van-col span="24" class="disguise_list_border_img">
                <img :src="avatar_img" class="disguise_list_border_img_avatar" v-if="keys === 2"/>
                <van-col span="24" class="disguise_list_border_img_icon">
                  <img :src="item.icon" @error="errorImgSave"/>
                </van-col>
              </van-col>
              <van-col span="24" class="disguise_list_border_name">{{ item.name }}</van-col>
              <van-col span="24" class="disguise_list_border_coin" v-if="item.is_pay == 1 || item.coin <= 0">
                <img src="../assets/img/diamond.png"/>
                {{ item.coin }} / {{ item.days }} {{ $t('other.day') }}
              </van-col>
              <van-col span="24" class="disguise_list_border_coin" v-else>
                {{ $t('Prohibit_purchases') }}
              </van-col>
              <div class="disguise_list_border_checked_i">
                <van-icon name="success" />
              </div>
            </van-col>
          </van-col>
        </van-row>
      </van-col>
      <van-col span="24"  class="disguise_bottom" v-if="list.length">
        <van-col span="15" offset="1"  class="disguise_bottom_text" v-if="list[list_i].is_pay == 1 || list[list_i].coin <= 0">
          <img src="../assets/img/diamond.png"/>
          <span>{{ list[list_i].coin }} / {{ list[list_i].days }} {{ $t('other.day') }}</span>
        </van-col>
        <van-col span="15" offset="1"  class="disguise_bottom_text" v-else>
          {{ $t('Prohibit_purchases') }}
        </van-col>
        <van-col span="7"  class="disguise_bottom_btn">
          <van-button round color="linear-gradient(to right, #F86C2F, #CA26ED)" size="large" @click="disguise_buy" v-if="list[list_i].is_pay == 1 || list[list_i].coin <= 0">
            {{ $t('btn.buy') }}
          </van-button>
          <van-button round color="#cecece" size="large" v-else>
            {{ $t('btn.buy') }}
          </van-button>
        </van-col>
      </van-col>
    </van-row>
  </div>
</template>
<script>
import '@/assets/css/style_a.css'
import axios from 'axios'
import {Dialog, Toast} from 'vant'

export default {
  data:function() {
    return {
      head_img: '',
      keys:0,
      list:[],
      list_i: 0,
      title:[],
      avatar_img: '',
      currency_name: '',
      errorImg: require('../assets/img/Load_Fail.png'),
    }
  },
  created: function() {
    for(let i= 1; i < 8; i++) {
      switch (i){
        case 1:
          this.title.push(this.$t('btn.medal'))  // 勋章
          break;
        case 2:
          this.title.push(this.$t('btn.Home_effects')) // 主页特效
          break;
        case 3:
          this.title.push(this.$t('btn.Head_frame'))// 头像框
          break;
        case 4:
          this.title.push(this.$t('btn.Chat_bubble')) // 聊天气泡
          break;
        case 5:
          this.title.push(this.$t('btn.Chat_background')) // 聊天背景
          break;
        case 6:
            this.title.push(this.$t('btn.entry_vehicles')) // 进场动画
            break;
        case 7:
            this.title.push(this.$t('btn.Mobilization_effect')) // 进场特效
            break;
        default:
      }
    }
    console.log(this.title)
    this.get_list();
    document.title = this.$t('title.Dress_up_center')
  },
  methods: {
    errorImgSave(event){
        event.target.src = this.errorImg
    },
    onClickType(name) {
      this.list_i = 0
      this.keys = name
      this. get_list()
    },
    disguise_border_btn(i) {
      this.list_i = i
      this.head_img = this.list[this.list_i].icon
      console.log(this.list_i)
    },
    get_list(){
      const uid = this.$route.query.uid;
      const token = this.$route.query.token;
      const type = this.keys + 1 === 6 ? 11 : this.keys + 1
      axios.get(this.baseUrlApi+"/vue/dress_api/get_display",{ params:{ uid:uid,token:token, type: type }}).then(res =>{
        this.loading = false;
        if(res.data.code===1){
          this.list = res.data.data.list
          this.avatar_img = res.data.data.avatar_img
          this.currency_name = res.data.data.currency_name
          console.log(this.list)
          this.head_img = this.list.length ? this.list[this.list_i].icon : ''
        }
      })
    },
    disguise_buy(){
      const uid = this.$route.query.uid
      const token = this.$route.query.token
      const id = this.list[this.list_i].id
      const message = this.$t('Tips.want_to_spend_to_buy',{coin: '<span style="color: #F9934A">' +this.list[this.list_i].coin +' ' + this.currency_name + '</span>'})
      Dialog.confirm({
        message:  message,
        confirmButtonColor:'#CA26ED',
        confirmButtonText: this.$t('btn.buy'),
        cancelButtonText: this.$t('btn.cancel')
      }).then(() => {
        // on confirm
        axios.get(this.baseUrlApi+"/vue/dress_api/buy_display",{ params:{ uid:uid,token:token, id: id }}).then(res =>{
          Toast(res.data.msg)
          if(res.data.code===1){
            this. get_list()
          }
        })
      }).catch(() => {
        // on cancel
      });
    }
  }
}
</script>
<style>
</style>



