import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)

// 注册i18n实例并引入语言文件
const i18n = new VueI18n({
  locale: 'zh-cn',
  fallbackLocale: 'zh-cn',
  messages: {
    'zh-cn': require('@/assets/languages/zh_cn.json'),
    'ar': require('@/assets/languages/ar.json'), // 阿拉伯
    'Mongolia': require('@/assets/languages/Mongolia.json'),
    'en-us': require('@/assets/languages/en.json')
  }
})

export default i18n



