<template>
  <div>
    <div class="healp-top">
      <div class="healp-top-l">
        {{ $t('column.Notice_notice') }}
      </div>
    </div>
    <div class="healp-list">
      <div class="title">
        <img class="title-img" src="../assets/img/remenwenti@2x.png" alt="">
        <p class="healp-list-txt">{{ $t('column.Hot_issues') }}</p>
      </div>
      <div class="healp-list-a" v-for="val in list" :key="val.id">
        <div class="healp-list-a-b" @click="href_log(val.id)">
          <div class="healp-list-a-b-l">{{val.post_title}}</div>
          <div class="healp-list-a-b-r">
            <img src="../assets/img/9.png" alt="">
          </div>
        </div>
      </div>
	  <div class="healp-list-bottom">&nbsp;</div>
    </div>
    <div class="help-buttom">
      <div class="help-buttom-a" v-for="v in type_list" :key="v.id" @click="get_list_id(v.id)">
        <img :src="v.img.thumbnail" alt=""> {{v.name}}
      </div>
    </div>
  </div>
</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        list:[],
        type_list:[],
        //privilege:[],
        id:0,
        coin:0,
        currency_name:'',
        friend_name:'',
      }
    },
    created: function() {
      //this.convert();
      this.get_list();
      document.title = this.$t('title.help')
    },
    methods: {
      get_list(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/vue/user_vue_api/get_help",{ params:{ uid:uid,token:token }}).then(res =>{
           console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             _this.list = res.data.data.list
             _this.type_list = res.data.data.type_list

             //_this.privilege = res.data.data.privilege
             //_this.id =  _this.info.id
             //_this.currency_name =  res.data.data.currency_name
             //_this.friend_name = res.data.data.friend_name
             //console.log(_this.list)
             //this.bank_name = list[0]['name']
           }else{
             Toast.fail(res.data.msg);
           }

        })
      },
      href_log(id){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //window.location.href = data;
        this.$router.push({path:'/help_info',query:{uid:uid, token: token,id:id }});
      },
      get_list_id(id){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/vue/user_vue_api/get_help",{ params:{ uid:uid,token:token,id:id }}).then(res =>{
           console.log(res.data)
           if(res.data.code==1){
             var _this = this;
             _this.list = res.data.data.list
             //_this.type_list = res.data.data.type_list

             //_this.privilege = res.data.data.privilege
             //_this.id =  _this.info.id
             //_this.currency_name =  res.data.data.currency_name
             //_this.friend_name = res.data.data.friend_name
             //console.log(_this.list)
             //this.bank_name = list[0]['name']
           }else{
             Toast.fail(res.data.msg);
           }

        })
      },
    }
  }

</script>

<style>
</style>



