<template>
  <div id="">
    <div class="title">{{ $t('column.In_progress') }}</div>
    <div class="order-box" v-for="ling in list" :key="ling.id">
      <div class="order-box-top">
        <div class="order-box-top-avatar">
          <img :src="ling.avatar" alt="">
          <div class="order-box-top-avatar-name">
            {{ling.user_nickname}}
          </div>
          <img id="order-box-top-avatar-a" src="../assets/img/9.png" alt="">
        </div>
        <div class="order-box-top-right">
          <div v-if="ling.status == 0">{{ $t('column.To_be_paid') }}</div>
          <div v-if="ling.status == 1" class="order-box-top-right-c">{{ $t('column.Order_to_be_received') }}</div>
          <div v-if="ling.status == 2" class="order-box-top-right-c">{{ $t('column.To_be_served')}}</div>
          <div v-if="ling.status == 3" class="order-box-top-right-c">{{ $t('column.To_be_confirmed') }}</div>
        </div>
      </div>
      <div class="order-box-bottm">
        <div class="order-box-bottm-t">
            <div class="order-box-bottm-t-l">
                {{ling.game_name}}
            </div>
            <div class="order-box-bottm-t-r">
                {{ling.total_coin}}
            </div>
        </div>
        <div class="order-box-bottm-b">
          <div class="order-box-bottm-t-l">
              {{ $t('column.service_time') }}：{{ling.addtime}}
          </div>
          <div class="order-box-bottm-t-r">
              {{ling.coin}}/{{ling.calc_method}}*{{ling.num}}
          </div>
        </div>
      </div>
      <div class="order-box-button">
        <van-button round size="small" color="linear-gradient(to left, #5A91F6, #79DAFA)"
         v-if="ling.status == 3" >{{ $t('btn.Confirm_order') }}</van-button>

         <van-button style="border: 1px solid #5C96F6;margin-right: 0.5rem;" round size="small"
          v-if="ling.status == 3" >{{ $t('btn.Cancel_order') }}</van-button>
      </div>
    </div>

    <div class="title">{{ $t('column.Historical_orders') }}</div>
      <div class="order-box" v-for="ling in list_end" :key="ling.id">
        <div class="order-box-top">
          <div class="order-box-top-avatar">
            <img :src="ling.avatar" alt="">
            <div class="order-box-top-avatar-name">
              {{ling.user_nickname}}
            </div>
            <img id="order-box-top-avatar-a" src="../assets/img/9.png" alt="">
          </div>
          <div class="order-box-top-right">

            <div v-if="ling.status == 5" class="order-box-top-right-d">{{ $t('column.Completed') }}</div>
            <div v-if="ling.status == 4" class="order-box-top-right-d">{{ $t('column.To_be_evaluated') }}</div>
          </div>
        </div>
        <div class="order-box-bottm">
          <div class="order-box-bottm-t">
              <div class="order-box-bottm-t-l">
                  {{ling.game_name}}
              </div>
              <div class="order-box-bottm-t-r">
                  {{ling.total_coin}}
              </div>
          </div>
          <div class="order-box-bottm-b">
            <div class="order-box-bottm-t-l">
                {{ $t('column.service_time') }}：{{ling.addtime}}
            </div>
            <div class="order-box-bottm-t-r">
                {{ling.coin}}/{{ling.calc_method}}*{{ling.num}}
            </div>
          </div>
        </div>
        <div class="order-box-button">
          <van-button round size="small" color="linear-gradient(to left, #5A91F6, #79DAFA)"
           v-if="ling.status == 5" >{{ $t('btn.View_comments') }}</van-button>
           <van-button round size="small" color="linear-gradient(to left, #5A91F6, #79DAFA)"
            v-if="ling.status == 4" >{{ $t('btn.To_evaluate') }}</van-button>

           <van-button style="border: 1px solid #5C96F6;margin-right: 0.5rem;" round size="small"
            v-if="ling.status == 6" >{{ $t('btn.To_evaluate') }}</van-button>
        </div>
      </div>

    <div class="order-box" v-if="list_end==''">
     {{ $t('column.No_orders_yet') }}
    </div>

  </div>

</template>

<script>
  import '@/assets/css/style_a.css'
  import axios from 'axios'
  import { Toast } from 'vant'

  export default {
    name: 'Index',
    data:function() {
      return {
        user_account: false,
        withdrawal: '',
        list:[],
        list_end:[],
        bank_name:''
      }
    },
    created: function() {
      //this.convert();
      this.get_list();
      document.title = this.$t('title.order_list')
    },
    methods: {
      get_list(){
        var uid = this.$route.query.uid;
        var token = this.$route.query.token;
        //var order_id = this.$route.query.order_id;
        axios.get(this.baseUrlApi+"/mapi/public/index.php/api/order_vue_api/get_order_list",{ params:{ uid:uid,token:token }}).then(res =>{
           console.log(res.data)

           if(res.data.code==1){
             var _this = this;

             _this.list = res.data.data.list
             _this.list_end = res.data.data.list_end
             //var list = res.data.data
             /* for(var i=0;i<list.length;i++){
               _this.list.push(list[i]['addtime']);
               _this.list.push(list[i]['avatar']);
               _this.list.push(list[i]['calc_method']);
               _this.list.push(list[i]['coin']);
               _this.list.push(list[i]['game_name']);
               _this.list.push(list[i]['game_id']);
               _this.list.push(list[i]['id']);
               _this.list.push(list[i]['status']);
               _this.list.push(list[i]['total_income']);
             } */
             console.log(_this.list)
             //this.bank_name = list[0]['name']
           }
              /* this.list = res.data.data.list;
              this.pay_list = res.data.data.pay_list;
              this.coin = res.data.data.coin;
              this.avatar = res.data.data.avatar; */
         //console.log(res.data);
        })
      }
    }
  }

</script>

<style>
</style>



