<template>
  <div>
    <div class="" style="width:95%;margin: 0 auto;overflow: hidden;">

      <div v-html="post_content" class="rich">{{post_content}}</div>
      <!-- {{post_content}} -->
    </div>
  </div>


</template>

<script>
  import '@/assets/css/style.css'
  import axios from 'axios'
  import { Toast } from 'vant';
  export default {
    data() {
      return {
        post_content:''
      }
    },
    created: function() {
      this.convert();
    },
    methods: {
      convert: function() {
        //  var id = this.$route.query.id;
        var id = this.$route.query.id;
        axios.get(this.baseUrlApi+"/vue/user_vue_api/get_help_info",{ params:{ id:id }}).then(res =>{
          //console.log(res);
          this.post_content = res.data.data.info.post_content;
          document.title = res.data.data.info.post_title

        })
      }
    }
  }
</script>

<style>

</style>



